import * as React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from "@material-ui/core/styles";
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';

import CloseIcon from '@material-ui/icons/Close';
import SendIcon from '@material-ui/icons/Send';
import Button from '@material-ui/core/Button';

import ReactToPdf from 'react-to-pdf';

const useStyles = makeStyles({
  // This group of buttons will be aligned to the right
  rightToolbar: {
    marginLeft: "auto",
    marginRight: -12
  },
  menuButton: {
    marginRight: 16,
    marginLeft: -12
  },
  root: {
    backgroundColor: '#8e24aa',
    '& h4': {
      color: 'white'
    }
  }
})

DialogScroll.propTypes = {
  showDialog : PropTypes.bool,
  titleBar: PropTypes.string,
  maxWidth: PropTypes.string,
  children: PropTypes.element.isRequired,
  closeFunction: PropTypes.any,
  showBotonPDF: PropTypes.bool,
  PDFfilename: PropTypes.any,
  PDFref: PropTypes.any  
};

export default function DialogScroll(props) {
  const { showDialog, titleBar, maxWidth, children, closeFunction, showBotonPDF } = props; 
  const classes = useStyles()   
  return (
    <React.Fragment>
          <Dialog  
            open={showDialog} 
            onClose={closeFunction} 
            fullWidth={true}
            // fullScreen={true}
             maxWidth={maxWidth} 
          >

            <AppBar position="static">    
                <Toolbar className={classes.root}>
                  <IconButton
                    className={classes.menuButton}
                    edge="start"
                    aria-label="close"
                    color="inherit"
                    onClick={closeFunction}
                  >                  
                      <CloseIcon />
                  </IconButton>
                  <Typography variant="title" color="inherit">
                    {titleBar}
                  </Typography>
                  <section className={classes.rightToolbar}>

                    {/* Boton Exportar a PDF un Componente Ref */}

                    
                    {showBotonPDF && <ReactToPdf 
                      targetRef={props.PDFref} 
                      filename={props.PDFfilename}
                    >
                      {({toPdf}) => (
                        <Button 
                            variant="outlined"
                            endIcon={<SendIcon />}
                            onClick={toPdf}
                            >
                            PDF
                        </Button>                           
                      )}                             
                    </ReactToPdf>}                     
                 
 
                  </section>
                </Toolbar>
            </AppBar>      

            {children} 

            
          </Dialog>   
    </React.Fragment>
  );
}
