import * as api from '../../api';
import * as React from 'react';


import { host, client } from '../../conf';

import {TextField} from "@material-ui/core"
import SupervisedUserCircleRounded from "@material-ui/icons/SupervisedUserCircleRounded";
import MaterialTable from 'material-table'
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'

import Notification from '../Notification'
import esLocale from 'date-fns/locale/es'
import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

var Fn = {
  validaRut: function(rutCompleto) {
      rutCompleto = rutCompleto.toUpperCase().trim()
      if (!/^[0-9]+-[0-9kK]{1}$/.test(rutCompleto))
          return false;
      var tmp = rutCompleto.split('-');
      var digv = tmp[1];
      var rut = tmp[0];
      return (Fn.dv(rut).toString() === digv);
  },
  dv: function(T) {
      var M = 0,
          S = 1;
      for (; T; T = Math.floor(T / 10))
          S = (S + T % 10 * (9 - M++ % 6)) % 11;
      return S ? S - 1 : 'K';
  }
}


export default function Operador(props: any) {

  const [OperadorList, updateOperadorList] = React.useState([]);
  const [selectedRow, setSelectedRow] = React.useState("");
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const classesCards = useStyleCards(); 
  const [loadData, setLoadData] = React.useState(false);
  //const box_style = {background: "white"}

  const downloadExcel=(filename)=>{
    const newData= OperadorList.filter(x => x.estado === "1").map((row)=>{
      //delete row.tableData
      return {
        Codigo: row.codigo,        
        Nombre: row.nombre,
        RUT: row.rut,
        Direccion: row.direccion,
        Resolucion: row.resolucion,
        Fecha_Resolucion: row.operador_fecha_resolucion,
        Vencimiento_Suspension: row.operador_fecha_vencim_suspension       
      }
    })

    const workSheet=XLSX.utils.json_to_sheet(newData)

    let header = ['Codigo','Nombre', 'RUT','Direccion','Resolucion', 'Fecha_Resolucion', 'Vencimiento_Suspension']
    const jsonKeys = header || Object.keys(newData[0])

    const objectMaxLength = []
    jsonKeys.forEach((key) => {
      objectMaxLength.push(
        pixelWidth(key, {
          size: 3,
        })
      )
    })

    newData.forEach((data, i) => {
      const value = newData[i]
      jsonKeys.forEach((key, j) => {
        const l = value[jsonKeys[j]]
          ? pixelWidth(value[jsonKeys[j]], {
              size: 3,
            })
          : 0
        objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

      })
    })



    const wscols = objectMaxLength.map((w: number) => ({width: w}))
    workSheet["!cols"] = wscols

    const workBook=XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workBook,workSheet,filename)
    //Buffer
    //let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
    //Binary string
    XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
    //Download
    XLSX.writeFile(workBook, filename+".xlsx")


  }    


  
  const updateAllOperadores = () => {
      setLoadData(true) 
      api.getData(host + '/api/fiscalizacion_controloperador/').then(x => {
        if(x.status === 200){
          updateOperadorList(x.data)
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        }).finally(() => {setLoadData(false)})
  }

  React.useEffect(() => {
      updateAllOperadores();
  }, [])

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <SupervisedUserCircleRounded fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Operadores</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>*/}          
          <MaterialTable
            data = {OperadorList}
            {...props}            
            isLoading ={loadData}
            editable={{
              isEditable: rowData => rowData.tiene_derivados === false,              
              isDeletable: rowData => rowData.tiene_derivados === false,               
              onRowAdd: newData =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    // newData.cliente = client
                    newData.isactive = 'true'
 
                    if (newData.fecha_resolucion && newData.fecha_resolucion !== null && newData.fecha_resolucion !=='')
                    {
                      let year = newData.fecha_resolucion.getFullYear();
                      let month = newData.fecha_resolucion.getMonth()+1;
                      let dt = newData.fecha_resolucion.getDate();
                      
                      if (dt < 10) {
                        dt = '0' + dt;
                      }
                      if (month < 10) {
                        month = '0' + month;
                      }
                      newData.fecha_resolucion = year+'-' + month + '-'+dt
                    }
                    if (newData.fecha_vencim_suspension && newData.fecha_vencim_suspension !== null && newData.fecha_vencim_suspension !== '')
                    {
                      let yearv = newData.fecha_vencim_suspension.getFullYear();
                      let monthv = newData.fecha_vencim_suspension.getMonth()+1;
                      let dtv = newData.fecha_vencim_suspension.getDate();
                      
                      if (dtv < 10) {
                        dtv = '0' + dtv;
                      }
                      if (monthv < 10) {
                        monthv = '0' + monthv;
                      }
                      newData.fecha_vencim_suspension = yearv+'-' + monthv + '-'+dtv
                    }   
        
                    let messages: any = [];                  
                    api.postData(host + '/api/fiscalizacion_controloperador/', newData)
                    .then(data => {
                      if(data.status !== 201){
                        Object.keys(data.data).map((key) => 
                        {
                          let mlabel = data.data[key]
                          if (key === "Datos Repetidos")
                          {
                            switch(data.data[key]) { 
                              case 'rut': { 
                                 mlabel = 'RUT'
                                 break; 
                              } 
                              case 'codigo': { 
                                mlabel = 'Código' 
                                 break; 
                              } 
                              case 'nombre': { 
                                mlabel = 'Nombre' 
                                 break; 
                              } 
                           } 
                          } 
                          messages.push((key === 'rut' ? 'RUT' : key) + ": " + mlabel)
                          return true
                        });
                        setNotify({isOpen: true, message: 'Error en la Adición de Operador:', errors: messages,type: 'error'});
                        reject();                          
                      } 
                        else {
                            setNotify({isOpen: true, message: 'Adición de Operador realizada con éxito', errors: [], type: 'success'});
                            newData = data.data                                                       
                            updateOperadorList([...OperadorList, newData]);                               
                        }
                    }).catch( e=>
                      { 
                        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                      });                  
                    resolve();
                  }, 1000)
                }),
              onRowUpdate: (newData, oldData) =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    const dataUpdate = [...OperadorList];
                    const index = oldData.tableData.id;
                    // newData.cliente = client
                    newData.isactive = 'true'
                    if (newData.fecha_resolucion && newData.fecha_resolucion.toString().length > 10 &&  newData.fecha_resolucion !== null)
                    {
                      let year = newData.fecha_resolucion.getFullYear();
                      let month = newData.fecha_resolucion.getMonth()+1;
                      let dt = newData.fecha_resolucion.getDate();
                      
                      if (dt < 10) {
                        dt = '0' + dt;
                      }
                      if (month < 10) {
                        month = '0' + month;
                      }
                      newData.fecha_resolucion = year+'-' + month + '-'+dt
                    }
                  

                    if (newData.fecha_vencim_suspension && newData.fecha_vencim_suspension.toString().length > 10 && newData.fecha_vencim_suspension  !== null)
                    {
                      let yearv = newData.fecha_vencim_suspension.getFullYear();
                      let monthv = newData.fecha_vencim_suspension.getMonth()+1;
                      let dtv = newData.fecha_vencim_suspension.getDate();
                      
                      if (dtv < 10) {
                        dtv = '0' + dtv;
                      }
                      if (monthv < 10) {
                        monthv = '0' + monthv;
                      }
                      newData.fecha_vencim_suspension = yearv+'-' + monthv + '-'+dtv
                    }
              
                    api.putData(host +'/api/fiscalizacion_controloperador/' + oldData.id + "/",  newData)
                    .then(data => {
                      let messages: any = [];
                      if(data.status !== 200){
                        Object.keys(data.data).map((key) => 
                        {
                          let mlabel = data.data[key]
                          if (key === "Datos Repetidos")
                          {
                            switch(data.data[key]) { 
                              case 'rut': { 
                                 mlabel = 'RUT'
                                 break; 
                              } 
                              case 'codigo': { 
                                mlabel = 'Código' 
                                 break; 
                              } 
                              case 'nombre': { 
                                mlabel = 'Nombre' 
                                 break; 
                              } 
                           } 
                          } 
                          messages.push((key === 'rut' ? 'RUT' : key) + ": " + mlabel)
                          return true
                        });
                        setNotify({isOpen: true, message: 'Error en la Edición de Operador:', errors: messages,type: 'error'});
                        reject();                          
                      } 
                        else {
                            setNotify({isOpen: true, message: 'Edición de Operador realizada con éxito', errors: [], type: 'success'}); 
                            newData = data.data             
                            dataUpdate[index] = newData;                    
                            updateOperadorList([...dataUpdate]); 
                            resolve();                             
                        }
                    }).catch( e=>
                      { 
                        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                      });
                  }, 1000)
                }),
              onRowDelete: oldData =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    const index = oldData.tableData.id;
                    let messages: any = [];
                    api.deleteData(host + '/api/fiscalizacion_controloperador/' + oldData.id + '/').then(data => {
                      if(data.status !== 204){
                          Object.keys(data.data).map(key => messages.push(key + ": " + data.data[key]));
                          setNotify({isOpen: true, message: 'Error en la Eliminación del Operador: ', errors: messages, type: 'error'}); 
                          reject();                           
                      } else {
                        setNotify({isOpen: true, message: 'Eliminación de Operador realizada con éxito', errors: [], type: 'success'}); 
                        const dataDelete = [...OperadorList];
                        dataDelete.splice(index, 1);
                        updateOperadorList([...dataDelete]);                         
                        resolve()                  
                      }        
                    }).catch( e=>
                      { 
                        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                      });

                  }, 1000)
                }),
            }}          
            localization={{

              pagination: {
                  labelDisplayedRows: '{from}-{to} de {count}',
                  labelRowsSelect: 'filas'
              },
              toolbar: {
                  nRowsSelected: '{0} filas(s) seleccionadas',
                  searchTooltip: 'Buscar',
                  searchPlaceholder: 'Buscar',
                  exportTitle: 'Exportar',
                  exportAriaLabel: 'Exportar',
                  exportCSVName: 'EXCEL',
                  exportPDFName: 'PDF'
              },
              header: {
                  actions: 'Acciones'
              },
              grouping: {
                groupedBy: 'Agrupar por',
                placeholder: 'Arraste la columna para agrupar'
              },
              body: {
                  emptyDataSourceMessage: 'No existen filas para visualizar',
                  dateTimePickerLocalization: esLocale,                  
                  deleteTooltip : 'Eliminar', 

                  editRow:{
                        saveTooltip: "Aceptar",
                        cancelTooltip: "Cancelar",                    
                        deleteText: "¿Está seguro que quiere borrar el Operador?"
                        
                  },
                  editTooltip : 'Editar', 
                  addTooltip: "Adicionar",
                  filterRow: {
                      filterTooltip: 'Filtrar'
                  }
              }
            }}              
            columns={[
                { field: 'id', title: 'Id' , hidden: true, export: false},
                { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                  
                { field: 'codigo', title: 'Código', defaultSort: 'asc', 
                editComponent: (editProps) =>  (
                  <TextField
                     autoFocus={true}
                      value={editProps.value}
                              onChange={(e) => editProps.onChange(e.target.value)}
                      helperText={editProps.helperText}
                      error={editProps.error}
                      variant="standard"
                  />              
                ),               
                validate: (rowData) =>
                  rowData.codigo === undefined
                  ? { isValid: false, helperText: 'Código Requerido' }
                  : rowData.codigo === ''
                  ? { isValid: false, helperText: 'Código Requerido' }
                  : rowData.codigo.length !== 3 ? 'Código debe tener 3 caracteres'               
                  : true

                },
                { field: 'nombre', title: 'Nombre',
                validate: (rowData) =>
                rowData.nombre === undefined
                  ? { isValid: false, helperText: 'Nombre Requerido' }
                  : rowData.nombre === ''
                  ? { isValid: false, helperText: 'Nombre Requerido' }
                  : true            
                }, 
                { field: 'rut', title: 'RUT',
                render: rowData => rowData.rut.toUpperCase().trim(),                     
                validate: (rowData) =>
                rowData.rut === undefined
                ? { isValid: false, helperText: 'Código Requerido' }
                : rowData.rut === ''
                ? { isValid: false, helperText: 'Código Requerido' }
                : rowData.rut.length !== 10 
                ? 'RUT debe tener 10 caracteres'  
                : rowData.rut.substr(8,1) !== '-' 
                ? 'Dígito 9 debe ser -' 
                : !(Fn.validaRut(rowData.rut)) 
                ? 'RUT no Válido'                                                   
                : true

              },
                { field: 'direccion', title: 'Dirección' },    
              { field: 'resolucion', title: 'Resolución'},   
              { field: 'fecha_resolucion', title: 'Fecha Resolución', type : 'date'},  
              { field: 'fecha_vencim_suspension', title: 'Vencimiento Rég.Susp.', type : 'date'},                                                     
              { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},   
                { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
                { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              

            ]} 
            onRowClick={((evt, selectedRow) => 
              {
                setSelectedRow(selectedRow.tableData.id)
              }
            )} 
            options={{
              filtering: true, 
              grouping: true, 
              actionsColumnIndex: -1,
              addRowPosition: "first",
              showTitle: false, 
              exportAllData: true,                     
              exportButton: true,
              exportFileName: 'Operadores',
              exportCsv: (columns, data) => {
                downloadExcel("Operadores")
              },              
              headerStyle: {
                backgroundColor: '#8e24aa',//'#01579b',
                color: '#FFF'
              },              
              rowStyle: rowData => ({
                backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
              })          
            }}   
             
          />
        {/* </CardBody> */}
        </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
