import React from 'react';
import {Chip, Grid, InputLabel, MenuItem, Select,  FormControl, Paper, TextField } from "@material-ui/core"
//import TextField from '@mui/material/TextField';
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";

export interface Props {
  OperadorList: any,
  DepositoList: any,  
  opPropio: string,
  rutOpPropio: string,
  codOpPropio: string,
  traspasonew: any
}

const useStylesType = makeStyles(styles)
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

export default function TraspasoTATCExistente (props: Props) {
  const classes = useStyles()  
  const classesTypo = useStylesType()
    return (
      <React.Fragment>    
        {/* <Grid  id="cont_DatosTATCExist" 
          container spacing={2}
          style={{marginBottom: 15}}
		      item xs={12} md={12}>
          <Paper elevation={3} style={{width: '100%',  height: 'auto'}} >
              <Grid id="cont_Aduana_Operador_Numero" item xs={12} md={12} style={{marginBottom: 10}}> */}
        <GridContainer  style={{marginTop: -20}}>
          <GridItem xs={12} sm={6} md={4} >    
              <FormControl  id="ctl_OpPropio" style={{width:'100%'}}>
                {/* <InputLabel id="demo-simple-select-required-label">Operador</InputLabel> */}
                <TextField
                  label="Operador"                  
                  id="demo-simple-select-required"
                  select
                  inputProps={{ readOnly: true , style: { fontSize: 14 } }}
                  value={props.opPropio}
                  defaultValue="0"
                  displayEmpty={false}
                  fullWidth={true}
                  style={{marginTop: 10}} 
                  size = 'small'
                  variant="outlined"                  
                >
                <MenuItem value="0" style= {{ fontSize: "14px" }}>                  
                    {<div className={classesTypo.mutedText}>Seleccione Operador</div>} 
                </MenuItem>
                {props.OperadorList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                    
                </TextField>
              </FormControl>
          </GridItem> 

          <GridItem xs={12} sm={6} md={4} >  
              <TextField id="ctl_CodOpPropio" style={{ marginTop: 10}}
                id="text"
                label="Código Operador"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.codOpPropio}
              />             
          </GridItem>

          <GridItem xs={12} sm={6} md={4} >  
              <TextField id="ctl_RutOpPropio" style={{ marginTop: 10}}
                id="text"
                label="RUT"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.rutOpPropio}
              />             
          </GridItem>

          <GridItem xs={12} sm={6} md={4} >  
              <TextField id="ctl_TATC_Numero" style={{ marginTop: 10}}
                  id="text"
                  label="TATC"
                  type="text"
                  variant="outlined"
                  fullWidth
                  size = 'small'
                  inputProps={{ readOnly: true, style: { fontSize: 14 } }}
                  value={props.traspasonew.tatc_numero}
              />   
          </GridItem>

          <GridItem xs={12} sm={6} md={4} >  
              <FormControl id="ctl_TATC_Deposito" style={{width:'100%',marginTop: 10}}
                  required>
                  {/* <InputLabel id="demo-simple-select-label">Depósito</InputLabel> */}
                  <TextField
                    select
                    label = "Depósito"
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={props.traspasonew.tatc_deposito}
                    defaultValue="0"
                    inputProps={{ readOnly: true, style: { fontSize: 14 } }}
                    displayEmpty={false}
                    fullWidth={true}
                    variant="outlined"                 
                    size = 'small'                  
                    >                  
                    <MenuItem value="0" style= {{ fontSize: "14px" }}>                  
                      {<div className={classesTypo.mutedText}>Seleccione Depósito</div>} 
                    </MenuItem>
                    {props.DepositoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                    
                  </TextField>
              </FormControl>
          </GridItem>
          {/* <GridItem xs={12} sm={6} md={4} > 
              <TextField id="ctl_TATC_FechaIngreso" style={{ marginTop: 10}}
                  label="Ingreso al País"
                  type="date"
                  value={props.traspasonew.fecha_ingreso_pais_str_dif}
                  variant="outlined"
                  fullWidth
                  size = 'small'
                  inputProps={{ readOnly: true , style: { fontSize: 14 } }}
                  //sx={{ width: 220 }}
                  InputLabelProps={{
                    shrink: true,
                  }}     
              />
          </GridItem> */}
      </GridContainer>    
              {/* </Grid> 
        </Paper>
        </Grid> */}
    </React.Fragment> 
    )
}


