import React from "react";
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Hidden from "@material-ui/core/Hidden";
import Poppers from "@material-ui/core/Popper";
import Divider from "@material-ui/core/Divider";
// @material-ui/icons
import Person from "@material-ui/icons/Person";

// import UserAvatar from 'react-user-avatar';
// core components

import Button from "../../components/CustomButtonsMdr/Button.js";
import ChangePassPage from "../../components/Usuarios/ChangePassPage";

import styles from "../../assets/jss/material-dashboard-react/components/headerLinksStyle.js";
import Swal from 'sweetalert2'
const useStyles = makeStyles(styles);

export default function AdminNavbarLinks(props) {
  const classes = useStyles();
  const [openProfile, setOpenProfile] = React.useState(null);
  const [showDialog, setShowDialog] = React.useState(false);  
  

  const handleClickProfile = (event) => {
    if (openProfile && openProfile.contains(event.target)) {
      setOpenProfile(null);
    } else {
      setOpenProfile(event.currentTarget);
    }
  };
  const handleCloseProfile = (props) => {
    setOpenProfile(null);
    //props.logOut;
  };
  const handleCloseApp = () => {
    //handleCloseProfile();    
    Swal.fire({
      // class:{{style: {zIndex: "10000"}}},
      position: 'top-center',
      icon: 'question',
      title: 'Desea cerrar la Session?',
      showCloseButton: true,
      showCancelButton: true,
      showConfirmButton: true,
      popup: 'swal2-hide',
      backdrop: false,
      timer: 4500
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        props.logOut()
      } 
    })
    
  };
  const handleCambiarPass = () => {
     setShowDialog(true);
  };

  return (
    <div>
      
      <div className={classes.manager}>       
        <Button 
          style={{marginRight:'10px'}}
          round= {window.innerWidth > 959 ? "true":null}
          color={window.innerWidth > 959 ? "primary" : "white"}
          justIcon={window.innerWidth > 959}
          simple={!(window.innerWidth > 959)}
          aria-owns={openProfile ? "profile-menu-list-grow" : null}
          aria-haspopup="true"
          onClick={handleClickProfile}
          className={classes.buttonLink}
        >
          
          {/* <div className={classes.linkText} style={window.innerWidth > 959 ?{display: 'absolute' , alignItems: 'center'}:null}>
            <h5 style = {{marginRight:'10px', marginTop:'15px' }}>{props.userCurrent}</h5>
            <Button color="primary"  style = {{marginRight:'30px'}} justIcon round>
              <Person />
            </Button>           
          </div> */}         
          <Person className={classes.icons} /> 
          <Hidden mdUp implementation="css">                       
            <p  style={{color:"primary", marginRight:'10px', marginTop:'15px' }}>{props.userCurrent}</p>            
          </Hidden>

        </Button>
         <Poppers style={{marginRight:'22px'}} 
          open={Boolean(openProfile)}
          anchorEl={openProfile}
          transition
          disablePortal
          className={
            classNames({ [classes.popperClose]: !openProfile }) +
            " " +
            classes.popperNav
          }
        >
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              id="profile-menu-list-grow"
              style={{
                transformOrigin:
                  placement === "bottom" ? "center top" : "center bottom",
              }}
            >
              <Paper>
                <ClickAwayListener onClickAway={handleCloseProfile}>
                  <MenuList role="menu">
                    <MenuItem
                      onClick={handleCambiarPass}
                      className={classes.dropdownItem}
                    >
                      Cambiar Contraseña
                    </MenuItem>
                    <Divider light />
                    <MenuItem
                      onClick={handleCloseApp}
                      className={classes.dropdownItem}
                    >
                      Salir
                    </MenuItem>
                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Poppers>
      </div>
      <div>
      <ChangePassPage showDialog = {showDialog} setShowDialog = {setShowDialog} />
    </div>      
    </div>

  );
}
