import React from 'react'
import FiltroBusquedaControlInv from './FiltroBusquedaControlInv'
import ResultadosControlInv from './ResultadosControlInv'
import RateReviewRounded from "@mui/icons-material/RateReviewRounded";
import PageHeader from '../PageHeader'


import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
//import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/CardMdr/CardBody.js";
import CardFooter from "../../components/CardMdr/CardFooter.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";

import * as api from '../../api';
import { host, client } from '../../conf';


const styles = {
    cardCategoryWhite: {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    cardTitleWhite: {
      color: "#FFFFFF",
      marginTop: "0px",
      minHeight: "auto",
      fontWeight: "300",
      fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
      marginBottom: "3px",
      textDecoration: "none",
    },
  };
  const useStyleCards = makeStyles(styles);

const ControlInventarios = () => {
    const [expresionFiltroTitulos, setexpresionFiltroTitulos] = React.useState('');
    const [buscando, setBuscando] = React.useState(false);
    const classesCards = useStyleCards();
    return (
        <React.Fragment>
            <GridContainer >
            <GridItem md={12}>
            <Card  >
            <CardHeader color="primary">
                <CardIcon color="primary">
                    <RateReviewRounded fontSize="large" />
                </CardIcon>
                <h4 className={classesCards.cardTitleWhite}>Control de Inventarios</h4>
                <p className={classesCards.cardCategoryWhite}>Consultas de Datos para efectuar un exitoso Control de Inventarios</p>
            </CardHeader>
            <CardBody >
                <FiltroBusquedaControlInv
                    setexpresionFiltroTitulos={setexpresionFiltroTitulos}
                    setBuscando = {setBuscando}
                /> </CardBody>              
                <ResultadosControlInv
                    expresionFiltroTitulos={expresionFiltroTitulos}
                    buscando = {buscando}
                />                
            
            </Card>                          
            </GridItem>
           
           
                            
            </GridContainer>
                 
        </React.Fragment>
    )
}

export default ControlInventarios
