const Traspaso = (props: any) => {

  const [autoC, setautoC] = React.useState(useRef(null));  
  const [autoC2, setautoC2] = React.useState(useRef(null)); 

  const [TATCList, updateTATCList] = React.useState([]); 
  const [AduanaList, updateAduanaList] = React.useState([]);
  const [OperadorList, updateOperadorList] = React.useState([]); 
  const [DepositoList, updateDepositoList] = React.useState([]); 
  const [ContenedorList, updateContenedorList] = React.useState([]); 
  const [ContenedorDispList, updateContenedorDispList] = React.useState([]);   
  const [TipoBultoList, updateTipoBultoList] = React.useState([]);
  const [TipoTamanoList, updateTipoTamanoList] = React.useState([]);
  const [TipoContenedorList, updateTipoContenedorList] = React.useState([]);

  const [errorFecha, setErrorFecha] = React.useState(false);  
  const [errorFechaIngreso, setErrorFechaIngreso] = React.useState(false);
  const [errorNumeroTATC, setErrorNumeroTATC] = React.useState(true);  
  const [errorNumeroTATCExterno, setErrorNumeroTATCExterno] = React.useState(true);  
  const [errorTatc, setErrorTatc] = React.useState(true);
  const [errorAduana, setErrorAduana] = React.useState(true);  
  const [errorOpExterno, setErrorOpExterno] = React.useState(true);
  const [errorDepositoExterno, setErrorDepositoExterno] = React.useState(true);
  const [errorDepositoTatc, setErrorDepositoTatc] = React.useState(true);
  const [errorContenedorActual, setErrorContenedorActual] = React.useState(true);   
  const [errorTipoContenedorActual, setErrorTipoContenedorActual] = React.useState(true); 
  const [errorTipoTamanoActual, setErrorTipoTamanoActual] = React.useState(true);     
  const [errorValor, setErrorValor] = React.useState(true);
  
  const [rutOpPropio, updateRutOpPropio] = React.useState('');
  const [opPropio, updateOpPropio] = React.useState('0'); 
  const [codOpPropio, updateCodOpPropio] = React.useState('000'); 
  const [numeroPropio, updateNumeroPropio] = React.useState(''); 
  const [numContPropio, updateNumContPropio] = React.useState('');  
  const [tipoBulto, setTipoBulto] = React.useState('');    
  const [valor, setValor] = React.useState('');   


  const [codOpExterno, updateCodOpExterno] = React.useState('');  
  const [rutOpExterno, updateRutOpExterno] = React.useState('');
 

  const [codAduana, updateCodAduana] = React.useState('00');


  const [traspasonew, updateTraspasoNew] = React.useState({ tipo: "R", fecha: new Date(), fecha_str: "",  aduana: "0", tatc: "", operador_externo: "0", tatc_externo: "", deposito_externo: "0", tatc_numero: "", tatc_operador: "0", tatc_deposito: "0", contenedor: "0", contenedor_numero: '', tipocontenedor: "0", tamano: "0", fecha_ingreso_pais: new Date(), fecha_ingreso_pais_str_dif: "",fecha_ingreso_pais_str_dif2: "", cliente:client})   
  //tatc_externo: "000000000001",  291121

  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const classesCards = useStyleCards();
  

  const updateAllAduana = () => {
    api.getData(host + '/api/fiscalizacion_controladuana/?estado=1').then(x => {
      if(x.status === 200){
        updateAduanaList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }  

  const updateAllTATC = () => {
    api.getData(host + '/api/tatc_tstctitulo/?tipooper__id=1&estado=A&cliente__id='+client+'&aduana__id='+traspasonew.aduana).then(x => {
      if(x.status === 200){
        updateTATCList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const updateAllOperador = () => {
    api.getData(host + '/api/fiscalizacion_controloperador/?estado=1').then(x => {
      if(x.status === 200){
        updateOperadorList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  } 

  const updateAllDeposito = () => {
    api.getData(host + '/api/inventariosdeposito/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        updateDepositoList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }   

  const updateContenedorDisponibles = () => {
    api.getData(host + '/api/inventarioscontenedor/listdisponibles/').then(x => {
      if(x.status === 200){
        updateContenedorDispList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }  

  const updateAllContenedor = () => {
    api.getData(host + '/api/inventarioscontenedor/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        updateContenedorList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }  

  const updateAllTipoBulto = () => {
    api.getData(host + '/api/inventariostipobulto/?estado=1').then(x => {
      if(x.status === 200){
        updateTipoBultoList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const updateAllTipoTamano = () => {
    api.getData(host + '/api/inventariostipotamano/?estado=1').then(x => {
      if(x.status === 200){
        updateTipoTamanoList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const updateAllTipoContenedor = () => {
    api.getData(host + '/api/inventariostipocontenedor/?estado=1').then(x => {
      if(x.status === 200){
        updateTipoContenedorList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }  

  const createTraspaso = () => {
    let messages: any = [];
    if (traspasonew.tipo == "E")
    {
      api.putData(host + '/api/tatc_tstctitulo/'+ traspasonew.tatc + '/enviartraspaso/')
      .then(data => {
          if(data.status !== 200){
            Object.keys(data.data).map((key) => 
            {

              let mlabel = data.data[key]
              messages.push((key!='0'?(key + ": ") : "") + mlabel)
            })
            setNotify({isOpen: true, message: 'Error al realizar entrega del TATC del Traspaso: ', errors: messages, type: 'error'}) 
                        
          } 
        
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });  
    }
    if (messages.length === 0) {
        api.postData(host + '/api/tatc_tstctraspaso/', traspasonew)
        .then(data => {
            if(data.status !== 201){
              Object.keys(data.data).map((key) => 
              {
                let mlabel = data.data[key]
                if (key == "Datos Repetidos")
                {
                  switch(data.data[key]) { 
                    case 'nombre': { 
                      mlabel = 'Número TATC' 
                       break; 
                    } 
                 } 
                } 
                
                messages.push((key!='0'?(key + ": ") : "") + mlabel)
              });
              setNotify({isOpen: true, message: 'Error al realizar el Traspaso: ', errors: messages, type: 'error'})           
            } else {
              setNotify({isOpen: true, message: 'Traspaso Realizado con éxito', errors: [], type: 'success'});           
              updateContenedorDisponibles();
              updateAllTATC();
              updateNumeroPropio('');  
              updateNumContPropio('');
              setErrorAduana(true);  
              setErrorTatc(true);
              setErrorOpExterno(true);
              setErrorDepositoExterno(true); 
              setErrorDepositoTatc(true); 
              setErrorContenedorActual(true); 
              setErrorTipoContenedorActual(true); 
              setErrorTipoTamanoActual(true); 

              if (autoC.current != null) 
              {              
                const ele = autoC.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
                if (ele) ele.click();
              } 
              if (autoC2.current != null) 
              {
                const ele2 = autoC2.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
                if (ele2) ele2.click();                                         
              }
              inicializar();          
            }
        }).catch( e=>
          { 
            setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
          });    
    }
  }

  const resetTraspaso = () => {
      let today = new Date();
      let today_str1 = today.toISOString();
      let today_str = today.toISOString()
      let todayt_str =  today_str 
      // let today_str = today.toISOString().split('T')[0];
      // let todayt_str =  today_str + 'T08:30'
      let cactual = "0"    
      updateContenedorDisponibles();
      updateAllTATC();
      updateTraspasoNew({...traspasonew,   fecha: today, fecha_str:today_str1,  aduana: "0", tatc: "", operador_externo: "0", tatc_externo: "", deposito_externo: "0", tatc_numero: "", tatc_operador: "0", tatc_deposito: "0", contenedor: "0", contenedor_numero: '', tipocontenedor: "0", tamano: "0", fecha_ingreso_pais:  today, fecha_ingreso_pais_str_dif: todayt_str, fecha_ingreso_pais_str_dif2: todayt_str, cliente:client});
      //tatc_externo: "000000000001"  291121
      updateNumeroPropio('');  
      setTipoBulto('');
      setValor('');    
      updateNumContPropio('');  
      updateCodOpExterno('');  
      updateRutOpExterno('');
      updateCodAduana('00');
  
      setErrorNumeroTATC(true);  
      setErrorAduana(true);     
      setErrorTatc(true);
      setErrorOpExterno(true);
      setErrorDepositoExterno(true); 
      setErrorDepositoTatc(true); 
      setErrorContenedorActual(true); 
      setErrorTipoContenedorActual(true); 
      setErrorTipoTamanoActual(true);   

        if (autoC.current != null) 
        {              
          const ele = autoC.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
          if (ele) ele.click();
        } 
        if (autoC2.current != null) 
        {
          const ele2 = autoC2.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
          if (ele2) ele2.click();                                         
        }
         
 
  }  

  const inicializar = () => {
    let today = new Date();
    let today_str1 = today.toISOString();
    let today_str = today.toISOString()
    let todayt_str =  today_str 
    let cactual = "0"
    let error = true
  
    api.getData(host + '/api/fiscalizacion_controloperador/?cliente__id='+client+'&is_active=true&estado=1').then(x => {
      if(x.status === 200){
        if (x.data.length > 0) {
          cactual = x.data[0].id
          updateRutOpPropio( x.data[0].rut)
          updateOpPropio(x.data[0].id)
          updateCodOpPropio(x.data[0].codigo)
          error = false
          updateTraspasoNew({tipo: "R", fecha: today,  fecha_str: today_str1, aduana: "0", tatc: "0", operador_externo: "0", tatc_externo: "", deposito_externo: "0", tatc_numero: "", tatc_operador: cactual, tatc_deposito: "0", contenedor: "0", tipocontenedor: "0", contenedor_numero: "", tamano: "0",   fecha_ingreso_pais: today, fecha_ingreso_pais_str_dif: todayt_str, fecha_ingreso_pais_str_dif2: todayt_str, cliente:client});
          //tatc_externo: "000000000001" 291121
        }
        else {
          setNotify({isOpen: true, message: 'Error: No existe Operador definido para el Cliente ...', errors: [], type: 'error'});          
        }
      }
      else
      {
        setNotify({isOpen: true, message: 'Error: No se puede obtener Operador para el Cliente...', errors: [], type: 'error'});
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      }); 

    if (error)
      {
        updateTraspasoNew({tipo: "R", fecha: today,  fecha_str: today_str1,  aduana: "0", tatc: "0", operador_externo: "0", tatc_externo: "", deposito_externo: "0", tatc_numero: "", tatc_operador: cactual, tatc_deposito: "0", contenedor: "0", tipocontenedor: "0", contenedor_numero: "", tamano: "0",  fecha_ingreso_pais: today, fecha_ingreso_pais_str_dif: todayt_str, cliente:client}); 
        // tatc_externo: "000000000001" 291121
    }
  } 
  
  const updateCorrelativoActual = (aduana: string, codad: string) => {
    let setear = false
    let cactual = '0000001'    
    setear = (traspasonew.aduana == "0") || (codOpPropio=="0")
    if (!setear)
    {
      api.getData(host + '/api/fiscalizacion_controladuanacorrelativo/?cliente__id='+client+'&aduana__id='+aduana+'&operador__id='+opPropio+'&tipooper__id=1').then(x => {
        if(x.status === 200){
          if (x.data.length > 0) {
            cactual = x.data[0].correlativo_actual.toString()
            cactual = '0'.repeat(7-cactual.length) + cactual

            updateNumeroPropio(codad + codOpPropio + cactual)
            setErrorNumeroTATC(false)  

          }
          else {
            setear = true
            updateNumeroPropio('')
            setErrorNumeroTATC(true) 
            setErrorAduana(true) 
            setNotify({isOpen: true, message: 'Error: No está definido Correlativo Actual ', errors:[], type: 'error'})             
          }
        }
        else {
          setear = true
          updateNumeroPropio('')
          setErrorNumeroTATC(true) 
          setErrorAduana(true) 
          setNotify({isOpen: true, message: 'Error obteniendo Correlativo Actual ', errors:[], type: 'error'})           
        }
      }).catch( e=>
        { 
          updateNumeroPropio('')
          setErrorNumeroTATC(true) 
          setErrorAduana(true)           
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
    }

 
  } 

  const updateValorActual = () => {
    let setear = false
    setear = (traspasonew.tipocontenedor == "0") || (traspasonew.tamano == "0") 
    if (!setear)
    {
      api.getData(host + '/api/inventarioscontenedorvalores/?cliente__id='+client+'&tipocontenedor__id='+traspasonew.tipocontenedor+'&tamano__id='+traspasonew.tamano).then(x => {
        if(x.status === 200){
          if (x.data.length > 0) {
            let cactual = x.data[0].valorCIF.toString()
            setValor(cactual) 
          }
          else {
            setValor('');
            setNotify({isOpen: true, message: 'Error: No está definido Valor', errors:[], type: 'error'}) 
            
          }
         
        }
        else {
          setValor('');
          setNotify({isOpen: true, message: 'Error obteniendo Valor CIF...', errors:[], type: 'error'})          
        }
      }).catch( e=>
        { 
          setValor('');
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
    }
    // if (setear)
    // {
    //   setValor('0')  
    // }
  }   
  
  React.useEffect(() => {
    resetTraspaso()
}, [traspasonew.tipo])  

  React.useEffect(() => {
    if (traspasonew.tipo == "E")
    {
      if (traspasonew.tatc_numero != "") 
      {
        let obj = TATCList.find(o => o.numero === traspasonew.tatc_numero);
        if (obj) {

          updateTraspasoNew({...traspasonew, tatc: obj.id, contenedor: obj.contenedor, fecha_ingreso_pais_str_dif: obj.fecha_ingreso_pais_str_dif , fecha_ingreso_pais_str_dif2: obj.fecha_ingreso_pais_str_dif2 , tatc_deposito: obj.deposito, tipocontenedor: obj.tipocontenedor_id, contenedor_numero: '', tamano: obj.tamano_id, fecha_ingreso_pais: obj.fecha_ingreso_pais})

          setTipoBulto(obj.contenedor_tipobulto_descripcion);
          setValor(obj.valor);      
        }
        else {
          updateTraspasoNew({...traspasonew, tatc: "0", contenedor: "0", fecha_ingreso_pais_str_dif: "" , tatc_deposito: "0", tipocontenedor:"0", contenedor_numero: "", tamano: "0"})     
          setTipoBulto('');
          setValor('');           
        }        
      }
      else {
        updateTraspasoNew({...traspasonew, tatc: "0", contenedor: "0", fecha_ingreso_pais_str_dif: "" , tatc_deposito: "0", tipocontenedor:"0", contenedor_numero: "", tamano: "0"})    
        setTipoBulto('');
        setValor('');            
      }      
    }
  }, [traspasonew.tatc_numero]) 

  
  React.useEffect(() => {
    if (traspasonew.tipo == "R")
    {
      if (numContPropio != "") 
      {
        let obj = ContenedorList.find(o => o.numero === numContPropio);
        if (obj) {
          updateTraspasoNew({...traspasonew, contenedor: obj.id, contenedor_numero: numContPropio,tipocontenedor: obj.tipocontenedor, tamano: obj.tamano})
          setTipoBulto(obj.tipobulto_descripcion);
          setValor(obj.valorCIF);
          setErrorTipoContenedorActual(false)
          setErrorTipoTamanoActual(false)

        }
        else {
          updateTraspasoNew({...traspasonew, contenedor: "0", contenedor_numero: numContPropio, tipocontenedor: "0", tamano: "0"}) 
          setTipoBulto('');
          setValor('');

        }        
      }
      else {
        updateTraspasoNew({...traspasonew, contenedor: "0", contenedor_numero: numContPropio})     
      }      
    }

  }, [numContPropio]) 

  React.useEffect(() => {
    if (traspasonew.operador_externo != "0") 
    {
      let objo = OperadorList.find(o => o.id === traspasonew.operador_externo);
      if (objo) {
        updateCodOpExterno(objo.codigo)
        updateRutOpExterno(objo.rut)
      }
    }
    else {
      updateCodOpExterno("")
      updateRutOpExterno("")
    }
    //setErrorNumeroTATCExterno(false)  291121
    setErrorNumeroTATCExterno(true)
}, [traspasonew.operador_externo]) 

React.useEffect(() => {
  if (traspasonew.tipo == "R")
  {
    let codad = '00' 
    if (traspasonew.aduana != "0") 
    {
      let obja = AduanaList.find(o => o.id === traspasonew.aduana);
      if (obja) {
        codad = obja.codigo_aduana
        updateCodAduana(obja.codigo_aduana)
        updateCorrelativoActual(traspasonew.aduana, codad); 
      }
          
    }  
    else {
      updateCodAduana("00")
      setErrorAduana(true)
      updateNumeroPropio('')
      setErrorNumeroTATC(true)
    }
  }
  else 
  {
    updateAllTATC()
  }

}, [traspasonew.aduana])

React.useEffect(() => {

  setErrorValor(valor=='')
 
}, [valor])

React.useEffect(() => {
  if (traspasonew.tipo == "R") {
    if (traspasonew.tipocontenedor != "") 
    {
      let obj = TipoContenedorList.find(o => o.id === traspasonew.tipocontenedor);
      if (obj) {
        updateValorActual();
        setTipoBulto(obj.tipobulto_descripcion);
        setErrorContenedorActual(false)
      }
      else {
        setTipoBulto('');
        setValor('');
      }        
    }
    else {
      setTipoBulto('');
      setValor('');       
    } 
  }
}, [traspasonew.tipocontenedor, traspasonew.tamano])



React.useEffect(() => {
  updateTraspasoNew({...traspasonew, tatc_externo:  '', })
  //updateTraspasoNew({...traspasonew, tatc_externo: codAduana + codOpExterno + '0000001', }) 291121
  //setErrorNumeroTATCExterno(false) 291121
  setErrorNumeroTATCExterno(true) 
}, [codAduana, codOpExterno])

  React.useEffect(() => {
    updateAllAduana(); 
    updateAllTATC(); 
    updateAllOperador(); 
    updateAllDeposito();             
    updateAllContenedor();
    updateContenedorDisponibles();    
    updateAllTipoBulto();     
    updateAllTipoContenedor(); 
    updateAllTipoTamano();         
    // updateAllContenedorValores();    

    inicializar();
  }, [])

  return (
      <GridContainer >       
        <GridItem xs={12} sm={12} md={12}>
        <Card>
        <CardHeader color="primary">
          <CardIcon color="primary">
            <RepeatRoundedIcon fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Traspasos</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de Documentos que permite intercambiar Contenedores entre distintos Operadores</p>
        </CardHeader> 
        <CardBody>            
            <TraspasoBody
              autoC= {autoC}
              autoC2= {autoC2}              
              TATCList= {TATCList}
              AduanaList= {AduanaList}
              OperadorList={OperadorList}
              DepositoList= {DepositoList} 
              ContenedorList= {ContenedorList}
              ContenedorDispList= {ContenedorDispList}              
              TipoContenedorList={TipoContenedorList}
              TipoTamanoList= {TipoTamanoList}                            
              TipoBultoList= {TipoBultoList}                
              traspasonew= {traspasonew}
              opPropio={opPropio}
              rutOpPropio={rutOpPropio}
              codOpPropio={codOpPropio}
              numeroPropio={numeroPropio}
              errorNumeroTATC={errorNumeroTATC}
              errorAduana= {errorAduana}
              errorTatc= {errorTatc}
              errorOpExterno = {errorOpExterno} 
              errorDepositoTatc = {errorDepositoTatc}
              rutOpExterno = {rutOpExterno}                           
              codOpExterno = {codOpExterno} 
              errorDepositoExterno = {errorDepositoExterno}
              errorContenedorActual =  {errorContenedorActual}
              errorTipoContenedorActual= {errorTipoContenedorActual}
              errorTipoTamanoActual =  {errorTipoTamanoActual} 
              errorValor =  {errorValor} 
              numContPropio = {numContPropio}           
              setErrorAduana= {setErrorAduana}
              setErrorTatc= {setErrorTatc}
              setErrorOpExterno= {setErrorOpExterno}
              setErrorDepositoExterno = {setErrorDepositoExterno}
              setErrorDepositoTatc = {setErrorDepositoTatc}
              setErrorContenedorActual =  {setErrorContenedorActual}
              setErrorTipoContenedorActual= {setErrorTipoContenedorActual}
              setErrorTipoTamanoActual =  {setErrorTipoTamanoActual}              
              updateTraspasoNew= {updateTraspasoNew}
              updateNumeroPropio= {updateNumeroPropio} 
              createTraspaso = {createTraspaso} 
              resetTraspaso = {resetTraspaso}
              updateNumContPropio  = {updateNumContPropio} 
              errorFecha = {errorFecha}
              errorFechaIngreso = {errorFechaIngreso}              
              setErrorFecha = {setErrorFecha}
              setErrorFechaIngreso = {setErrorFechaIngreso}  
              setErrorValor = {setErrorValor}
              tipoBulto =  {tipoBulto}  
              valor =  {valor}  
              errorNumeroTATCExterno= {errorNumeroTATCExterno}
              setErrorNumeroTATCExterno = {setErrorNumeroTATCExterno}                                                          
            >
            </TraspasoBody>        
        </CardBody>
        </Card>        
        <Notification
            notify = {notify}
            setNotify = {setNotify}
        > 
        </Notification> 
        </GridItem>         
      </GridContainer>
  )
}

export default Traspaso
