
import {Button, TextField, Select, MenuItem } from "@material-ui/core"

import React from 'react';
import Modal from '@material-ui/core/Modal';
// import TituloaPDF from './TituloaPDF'
import ReactToPdf from 'react-to-pdf';
import Box from '@material-ui/core/Box';
import TituloaPDF from './TituloaPDF'

import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogActions from '@material-ui/core/DialogActions';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import LockRoundedIcon from '@material-ui/icons/LockRounded';
import AccessAlarmIcon from '@material-ui/icons/AccessAlarm';
import ImportContactsIcon from '@material-ui/icons/ImportContacts';
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Divider from '@material-ui/core/Divider';

const prorrogaModalBody = (TTTC: any, updateTTTC: any, updateTTTCDetail: any, errors : never []) => {  


  return (  
    
          <div >    
          <DialogContent>
          <GridContainer style={{marginTop: 0}}>
            <DialogContentText>
                <List>
                  <ListItem> 
                    <ListItemAvatar>
                      <Avatar style={{backgroundColor: '#8e24aa'}}>
                        <ImportContactsIcon />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText primary="Para PRORROGAR el Título Temporal de Contenedor debe considerar que esto cambia su Fecha de Vigencia Actual al Próximo Año Calendario."  />
                    <Divider variant="inset" component="li" />
                  </ListItem>
                  
                </List> 
              </DialogContentText>

            {errors && 
            <div className="form-errors" style={{ color:"red", fontWeight:"bold"}} >
                      {
                          errors.map((error, key) => <div key={key}>
                              <p className="error-message">{error}</p>
                          </div>)
                      }
            </div>}  
            
            <GridItem xs={12} sm={6} md={6}> 
            <TextField
              autoFocus
              margin="dense"
              id="correlativo"
              label="Correlativo"
              type="text"
              fullWidth
              variant="standard"
              value={TTTC.numero}
            />
            </GridItem>
            <br/> 
            <GridItem xs={12} sm={6} md={6}> 
            <TextField
              margin="dense"
              id="tipo"
              label="Tipo"
              type="text"
              fullWidth
              variant="standard"
              value={TTTC.tipooper_name}
            />
             </GridItem>
            <br/>
            <GridItem xs={12} sm={6} md={6}> 
            <TextField
              margin="dense"
              id="nocontenedor"
              label="No.Contenedor"
              type="text"
              fullWidth
              variant="standard"
              value={TTTC.contenedor_numero}
            />
            <br/>
            </GridItem>
            <GridItem xs={12} sm={6} md={6}>
            <TextField
              margin="dense"
              id="fechavigencia"
              label="Fecha Vigencia Actual"
              type="text"
              fullWidth
              variant="standard"
              value={TTTC.fecha_vigencia_str}
            />
            </GridItem>
            <br/> <br/> 
            <br/>
           
            <DialogContentText>
              <List>
                <ListItem>
                  <ListItemAvatar>
                    <Avatar style={{backgroundColor: '#8e24aa'}}>
                      <CalendarTodayIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText primary={TTTC.datos_prorroga}  />
                </ListItem>
                {/* <GridItem xs={12} sm={12} md={12}>                
                <Divider variant="inset" component="li" />
                </GridItem> */}
                {/* <ListItem> */}
                
                {/* </ListItem> */}
                {/* <Divider variant="inset" component="li" />     */}
              </List>
              <GridItem xs={12} sm={12} md={12}>
              {/* <ListItem>  */}
                  <ListItemText primary="Al realizar la prórroga la Fecha Vigencia Actual se actualizará a:" secondary ={TTTC.fecha_vigencia_prorroga} />
              {/* </ListItem> */}
              </GridItem> 
            </DialogContentText>
            
          </GridContainer>
          </DialogContent>
          <DialogActions>
            <Button startIcon={<AccessAlarmIcon />} color="primary" style={{backgroundColor: '#8e24aa', color: '#ffffff'}} onClick={updateTTTC}>Prorrogar</Button>           
          </DialogActions>
      
      </div>   
    
    )}; 
 
 const cerrarModalBody = (TTTCC: any, updateTTTCC: any, updateTTTCCDetail: any, errors : never [], MotivoCierreList: 
  any [], DetalleAnulacionList: any [], esAnulacion: any, esNacional: any, ficheroNombre: any, setFicheroNombre: any) => { 



   return (           
    <div >    
        <DialogContent>
          <DialogContentText>
                <List>
                  <ListItem>
                    <ListItemAvatar>
                      <Avatar style={{backgroundColor: '#8e24aa'}}>
                        <ImportContactsIcon />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText primary="Para CERRAR el Título Temporal de Contenedor debe suministrar datos importantes como Motivo Cierre *, Detalle de Anulación, No. DI (Documento de Internación) y su fichero asociado."  />
                  </ListItem>
                  <Divider variant="inset" component="li" />
                </List> 
          </DialogContentText>          

          {errors && 
          <div className="form-errors" style={{ color:"red", fontWeight:"bold"}} >
                    {
                        errors.map((error, key) => <div key={key}>
                            <p className="error-message">{error}</p>
                        </div>)
                    }
          </div>}  
          <GridContainer style={{marginTop: 0}}>
          <GridItem xs={12} sm={5} md={5}> 
          <TextField
            autoFocus
            margin="dense"
            id="correlativo"
            label="Correlativo"
            type="text"
            fullWidth
            variant="standard"
            value={TTTCC.numero}
          />
           </GridItem>
          <br/> 
          <GridItem xs={12} sm={2} md={2}> 
          <TextField
            margin="dense"
            id="tipo"
            label="Tipo"
            type="text"
            fullWidth
            variant="standard"
            value={TTTCC.tipooper_name}
          />
          </GridItem>
          <br/>
          <GridItem xs={12} sm={5} md={5}> 
          <TextField
            margin="dense"
            id="nocontenedor"
            label="No.Contenedor"
            type="text"
            fullWidth
            variant="standard"
            value={TTTCC.contenedor_numero}
          />
          </GridItem>
          <br/>
          <GridItem xs={12} sm={12} md={12}>
          {/* <div> */}
              <br/>            
              <h8>Motivo de Cierre * </h8>
              <Select
                  labelId="demo-simple-select-outlined-label"
                  id="motivoCierre"
                  onChange={(event: any) => {
                    updateTTTCCDetail({...TTTCC, motivocierre: event.target.value})
                   
                  }}                  
                  value={TTTCC.motivocierre}
                  defaultValue="0"
                  displayEmpty={false}
                  variant="outlined"
                  
                  fullWidth={true}
              >
                  <MenuItem value="0">
                      <em>Seleccione Motivo de Cierre</em>
                  </MenuItem>

                  {MotivoCierreList.map((c:any, key) => {return <MenuItem key={key} value={c.id}>{c.nombre}</MenuItem>})}
              </Select>
          {/* </div>           */}
          </GridItem>
          {esAnulacion && 
           <GridItem xs={12} sm={12} md={12}>
              <br/>            
              <h8>Detalle de Anulación</h8>
              <Select
                  // labelId="demo-simple-select-outlined-label"
                  id="detalleAnulacion"
                  onChange={(event: any) => {updateTTTCCDetail({...TTTCC, detalleanulacion: event.target.value})}}                 
                  value={TTTCC.detalleanulacion}
                  required
                  defaultValue="0"
                  displayEmpty={false}
                  variant="outlined"
                  fullWidth={true}
              >
                  <MenuItem value="0">
                      <em>Seleccione Detalle de Anulación</em>
                  </MenuItem>
                  {DetalleAnulacionList.map((c:any, key) => {return <MenuItem key={key} value={c.id}>{c.nombre}</MenuItem>})}
              </Select>
              <br/>
            </GridItem>
          }

          {esNacional &&
          <GridItem xs={12} sm={12} md={12}> 
          <TextField
            margin="dense"
            id="nodi"
            label="No.DI"
            type="text"
            fullWidth
            variant="standard"
            value={TTTCC.nodi}
            onChange={(event) => {updateTTTCCDetail({...TTTCC, nodi: event.target.value})}}            
          />
          </GridItem>
          }
          <br/>     

          {esNacional && 
            <GridItem xs={12} sm={12} md={12}> 
            <Button
              variant="contained"
              component="label"              
            >
              Seleccionar Archivo
              <input
                type="file"
                hidden 
                onChange={(event) => {
                  let fselect = event.target.files[0]
                  if (fselect && fselect.name)
                  {
                    updateTTTCCDetail(
                      {...TTTCC, filedi: fselect.name})
                      setFicheroNombre(' '+fselect.name)
                  }                    
                }}               
              />
            </Button>
            <TextField
            margin="dense"
            fullWidth
            variant="standard"
            value = {ficheroNombre}

          />     
            

            {/* <TextField
            margin="dense"
            id="filedi"
            label="Fichero DI"
            type="file"
            fullWidth
            variant="standard"
            onChange={(event) => {
              let fselect = event.target.files[0]
              if (fselect && fselect.name)
              {
                updateTTTCCDetail(
                  {...TTTCC, filedi: fselect.name})
                }}
              } 
          /> */}
         </GridItem>}
          <br/>                 
        </GridContainer>
        </DialogContent>
        <DialogActions>
          <Button  
          startIcon={<LockRoundedIcon />} 
          color="primary"
          // style={{backgroundColor: '#01579b', color: '#ffffff'}}  
          onClick={updateTTTCC}
          disabled={ (TTTCC.motivocierre === "0")  || (esAnulacion ? (TTTCC.detalleanulacion === "0") : false)}
          //style={{backgroundColor: '#8e24aa', color: '#ffffff'}}  
          >
            Cerrar
          </Button>
                  
        </DialogActions>

    </div>
)};

const pdfModalBody = (classes: any, TTTCToPDF: any, errors : never [], modal_style: any) => {  

  return (           
    <div > 
      <div >
        <Box color="text.primary"  > 
          <TituloaPDF  titulo={TTTCToPDF}></TituloaPDF>          
          <div>
           </div>
        </Box>
      </div>

    </div>

)};

export {prorrogaModalBody,  cerrarModalBody, pdfModalBody};