
import React from "react";
import {Bar } from 'react-chartjs-2';

import Paper from '@material-ui/core/Paper';


  export interface Props {
    data: any;  

  }

  const SemBarChart = (props: Props) => {



   // const classes = useStyles();



    return (
      <Paper elevation={3}>
      <div>
        <Bar
            data= {props.data}  
            height= {358}
            width= {200}
            options={{ 
              plugins: {
                // title: {
                //     display: true,
                //     text: 'Balance Últimos 6 Meses'
                // }
            },            
              // plugins: {
              //   custom_canvas_background_color: {
              //     color: '#14c6eb'
              //   },
              // },
   
              responsive: true,         
              //label: false,
              maintainAspectRatio: false ,  
           
              scales: {
                    xAxes: [{
                      gridLines: {
                        display: false
                      },
                      ticks: {
                        fontSize: 8

                      }                     
                    }],
              
                    yAxes: [
                      {
                        ticks: {
                          beginAtZero: true,
                        },
                        gridLines: {
                          display: false
                        }                        
                      },
                    ],
                  },
                  legend: {
                     labels: {
                      fontSize: 25,
                    },
                  },
            }}

 
        />    

      </div>
      </Paper>
    )
  }

  export default SemBarChart