import * as api from '../../api';
import * as React from 'react';


import { host, client } from '../../conf';

import { DialogContent,  DialogActions, Button, TextField} from "@material-ui/core"

import LibraryBooksRoundedIcon from '@material-ui/icons/LibraryBooksRounded';

import Notification from '../Notification'
import MaterialTable from 'material-table';
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'
import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import UploadRoundedIcon from '@mui/icons-material/UploadRounded';
import { makeStyles } from "@material-ui/core/styles";

import DialogScroll from '../DialogScroll';



const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  
};
const useStyleCards = makeStyles(styles);

export default function Documento(props: any) {

  const [DocumentoList, updateDocumentoList] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [extensionList,updateExtensionList]= React.useState({}) 
  const classesCards = useStyleCards(); 
  const [maxWidth] = React.useState('md'); 

  const [selectedRow, setSelectedRow] = React.useState("");
  const [ficheroOrigen, setFicheroOrigen] = React.useState();
  const [ficheroDestino, setFicheroDestino] = React.useState("");
  const [ficheroNombre, setFicheroNombre] = React.useState(" Archivo no Seleccionado");    
  const [dataUpload, setDataUpload] = React.useState({file: [], fname: ""}) 
  const [showModalUpload, updateshowModalUpload] = React.useState(false);

  const downloadExcel=()=>{

    const colExcel = ['Codigo','Nombre', 'Extensión']
    
    const fieldsExcel = ['codigo', 'nombre', 'ext_codigo']
    

    const dataorig = DocumentoList.filter(x => x.estado === "1").map((rowData) =>
    {
        return rowData
    })

    const newData = []
    dataorig.map((obj) => {
      newData.push(pick(fieldsExcel, obj, colExcel))
      return true
    })
   
   
    const workSheet=XLSX.utils.json_to_sheet(newData)

    let header = colExcel
    const jsonKeys = header || Object.keys(newData[0])

    const objectMaxLength = []
    jsonKeys.forEach((key) => {
      objectMaxLength.push(
        pixelWidth(key, {
          size: 3,
        })
      )
    })

    newData.forEach((data, i) => {
      const value = newData[i]
      jsonKeys.forEach((key, j) => {
        const l = value[jsonKeys[j]]
          ? pixelWidth(value[jsonKeys[j]], {
              size: 3,
            })
          : 0
        objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

      })
    })



    const wscols = objectMaxLength.map((w: number) => ({width: w}))
    workSheet["!cols"] = wscols

    const workBook=XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workBook,workSheet, "Documentos")
    //Buffer
    //let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
    //Binary string
    XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
    //Download
    XLSX.writeFile(workBook, "Documentos.xlsx")


  } 

  const subirDocumento = (rowData) => {
    //setFicheroDestino(process.env.PUBLIC_URL + '\\doc\\'  + rowData.nombre+'.'+rowData.ext_codigo)
    setFicheroDestino(rowData.url)
    setFicheroNombre(" Archivo no Seleccionado")      
    updateshowModalUpload(true)
 };

 const closeModalUpload = () => {updateshowModalUpload(false)};

 const fileDocUpload = () => {
  setTimeout(() => {

  const formData: FormData = new FormData();
   //formData.append('file', URL.createObjectURL(ficheroOrigen));
   formData.append('file', ficheroOrigen);
   formData.append('pathdoc', ficheroDestino);
   //let fdata = {file: ficheroOrigen}


 
 
  api.postData2(host + '/api/documentosfileupload/',formData).then(x => {
    if ((x.status === 200) || (x.status === 201)){
      setNotify({isOpen: true, message: 'Fichero subido con éxito...', errors: [], type: 'success'}) 
      updateshowModalUpload(false)
    }
  }).catch( e=>
    { 
      setNotify({isOpen: true, message: 'Error Subiendo Documento...', errors: [], type: 'error'});
    });

}, 1000)

  } 


  const uploadModalBody = () => { 

    return (           
     <div >    
         <DialogContent>
 
 
             <Button
               variant="contained"
               component="label"
               
             >
               Seleccionar Archivo
               <input
                 type="file"
                 hidden
  
                 onChange={(event) => {
                   let fselect = event.target.files[0]
                   if (fselect && fselect.name)
                   {
                     setFicheroOrigen(event.target.files[0])
                     setFicheroNombre(' '+fselect.name)
                   }
                   }
                   }                 
               />
             </Button>
 
             <TextField
             margin="dense"
             fullWidth
             variant="standard"
             value = {ficheroNombre}
 
           />            
         </DialogContent>
         <DialogActions>
           <Button  startIcon={<UploadRoundedIcon />} color="secondary" style={{backgroundColor: '#8e24aa', color: '#ffffff'}} onClick={fileDocUpload}>Subir Documento</Button>           
         </DialogActions>
 
     </div>
 )};

 const cambiarEstado = (rowData) => {
  new Promise((resolve, reject) => {
    setTimeout(() => {
      const dataUpdate = [...DocumentoList]
      const index = rowData.tableData.id
      rowData.cliente = client
      rowData.isactive = 'true'
      rowData.estado = rowData.estado === "0" ? "1" : "0"
      api.putData(host +'/api/documentosdocumento/' + rowData.id + "/",  rowData)
      .then(data => {
        let messages: any = [];
        if(data.status !== 200){
          Object.keys(data.data).map((key) => 
          {
            let mlabel =  data.data[key]
            messages.push( key + ": " + mlabel)
            return true
          });
              setNotify({isOpen: true, message: 'Error al cambiar Estado de Documento', errors: messages, type: 'error'});
              reject();    
 
          } else {
              setNotify({isOpen: true, message: 'Cambio de Estado de Documento realizado con éxito', errors: [], type: 'success'}); 
              rowData = data.data                                      
              dataUpdate[index] = rowData;                    
              updateDocumentoList([...dataUpdate]); 
              resolve(1);                             
          }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
    }, 1000)
})}
    

  const updateAllExtension = () => {
      api.getData(host + '/api/documentosextension/?cliente__id='+client).then(x => {
        if(x.status === 200){
          const extensionList={}
          x.data.map(row=>extensionList[row.id]=row.codigo)
          updateExtensionList(extensionList)          

        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
    }

    const updateAllDocumentos = () => {
      api.getData(host + '/api/documentosdocumento/').then(x => {
        if(x.status === 200){
          updateDocumentoList(x.data)
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
  }

  React.useEffect(() => {
   setDataUpload(dataUpload, {file: ficheroOrigen, fname: ficheroDestino})
}, [ficheroOrigen])

  React.useEffect(() => {
      updateAllExtension(); 
      updateAllDocumentos();         
  }, [])

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <LibraryBooksRoundedIcon fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Documentos</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>         */}

        <MaterialTable
          data = {DocumentoList}
          columns= {[
            { field: 'id', title: 'Id' , hidden: true, export: false},
            { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                 
            { field: 'codigo', title: 'Código', defaultSort: 'asc',
            editComponent: (editProps) =>  (
              <TextField
                 autoFocus={true}
                  value={editProps.value}
                          onChange={(e) => editProps.onChange(e.target.value)}
                  helperText={editProps.helperText}
                  error={editProps.error}
                  variant="standard"
              />              
            ),           
            validate: (rowData) =>
              rowData.codigo === undefined
              ? { isValid: false, helperText: 'Código Requerido' }
              : rowData.codigo === ''
              ? { isValid: false, helperText: 'Código Requerido' }
              : true
            },
            { field: 'nombre', title: 'Nombre',
            validate: (rowData) =>
            rowData.nombre === undefined
              ? { isValid: false, helperText: 'Nombre Requerido' }
              : rowData.nombre === ''
              ? { isValid: false, helperText: 'Nombre Requerido' }
              : true            
            }, 
            { field: 'ext_codigo', title: 'Extensión' ,  export: true, hidden: true},               
            { field: 'extension', title: 'Tipo de Documento', lookup: extensionList, export: false, initialEditValue: '1',
            validate: (rowData) =>
            rowData.extension === undefined
              ? { isValid: false, helperText: 'Tipo Requerido' }
              : rowData.extension === ''
              ? { isValid: false, helperText: 'Tipo Requerido' }
              : true            
          },          
          { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},                     
            { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
            { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              
          ]}           
         
          editable={{
            isEditable: rowData => rowData.tiene_derivados === false,
            //isEditHidden:rowData => true,              
            isDeletable: rowData => rowData.tiene_derivados === false,  
            onRowAdd: newData =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  newData.cliente = client
                  newData.isactive = 'true'

                  let messages: any = [];                  
                  api.postData(host + '/api/documentosdocumento/', newData)
                  .then(data => {
                      if(data.status !== 201){
                        Object.keys(data.data).map((key) => 
                        {
                          let mlabel =  (key === "Datos Repetidos") ? "Código" : data.data[key]
                          messages.push( key + ": " + mlabel)
                          return true
                        });
                        setNotify({isOpen: true, message: 'Error en la Adición de Documento' , errors: messages, type: 'error'});
                        reject();
                      } else {
                          setNotify({isOpen: true, message: 'Adición de Documento realizada con éxito', errors: [], type: 'success'});
                          newData = data.data   
                          updateDocumentoList([...DocumentoList, newData]);   
                      }
                  }).catch( e=>
                    { 
                      setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                    });                 
                  resolve();
                }, 1000)
              }),
            onRowUpdate: (newData, oldData) =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  const dataUpdate = [...DocumentoList];
                  const index = oldData.tableData.id;
                  newData.cliente = client
                  newData.isactive = 'true'
                  api.putData(host +'/api/documentosdocumento/' + oldData.id + "/",  newData)
                  .then(data => {
                    let messages: any = [];
                    if(data.status !== 200){
                      Object.keys(data.data).map((key) => 
                      {
                        let mlabel =  (key === "Datos Repetidos") ? "Código" : data.data[key]
                        messages.push( key + ": " + mlabel)
                        return true
                      });
                          setNotify({isOpen: true, message: 'Error en la Edición de Documento' , errors: messages, type: 'error'});
                          reject();    
             
                      } else {
                          setNotify({isOpen: true, message: 'Edición de Documento realizada con éxito', errors: [], type: 'success'}); 
                          newData = data.data                                      
                          dataUpdate[index] = newData;                    
                          updateDocumentoList([...dataUpdate]); 
                          resolve();                             
                      }
                  }).catch( e=>
                    { 
                      setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                    });
                }, 1000)
              }),
            onRowDelete: oldData =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  const index = oldData.tableData.id;
                  let messages: any = [];
                  api.deleteData(host + '/api/documentosdocumento/' + oldData.id + '/').then(data => {
                    if(data.status !== 204){
                        Object.keys(data.data).map(key => messages.push(key + ": " + data.data[key]));
                        setNotify({isOpen: true, message: 'Error en la Eliminación de Documento' , errors: messages, type: 'error'}); 
                        reject();                           
                    } else {
                      setNotify({isOpen: true, message: 'Eliminación de Documento realizada con éxito', errors: [], type: 'success'}); 
                      const dataDelete = [...DocumentoList];
                      dataDelete.splice(index, 1);
                      updateDocumentoList([...dataDelete]);   
                      resolve()                  
                    }        
                  }).catch( e=>
                    { 
                      setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                    });                 
                  

                }, 1000)
              }),
          }}          
          localization={{
            pagination: {
                labelDisplayedRows: '{from}-{to} de {count}',
                labelRowsSelect: 'filas'
            },
            toolbar: {
                nRowsSelected: '{0} filas(s) seleccionadas',
                searchTooltip: 'Buscar',
                searchPlaceholder: 'Buscar',
                exportTitle: 'Exportar',
                exportAriaLabel: 'Exportar',
                exportCSVName: 'EXCEL',
                exportPDFName: 'PDF'
            },
            header: {
                actions: 'Acciones'
            },
            grouping: {
              groupedBy: 'Agrupar por',
              placeholder: 'Arraste la columna para agrupar'
            },
            body: {
                emptyDataSourceMessage: 'No existen filas para visualizar',
                deleteTooltip : 'Eliminar', 

                editRow:{
                      saveTooltip: "Aceptar",
                      cancelTooltip: "Cancelar",                    
                      deleteText: "¿Está seguro que quiere borrar este Documento?"
                },
                editTooltip : 'Editar', 
                addTooltip: "Adicionar",
                filterRow: {
                    filterTooltip: 'Filtrar'
                }
            }
          }}              
          onRowClick={((evt, selectedRow) => 
            {
              setSelectedRow(selectedRow.tableData.id)
            }
          )} 
          options={{
            filtering: true, 
            grouping: true, 
            actionsColumnIndex: -1,
            addRowPosition: "first",
            showTitle: false,  
            exportAllData: true,                    
            exportButton: true,
            exportFileName: "Documentos",
            exportCsv: (columns, data) => {
              downloadExcel()
            },              
            headerStyle: {
              backgroundColor: '#8e24aa',//'#01579b',
              color: '#FFF'
            },              
            rowStyle: rowData => ({
              backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
            })          
          }}                        
          actions={[
             rowData => ({
              icon: 'check' ,
              //iconProps: { style: {  color: "green" } },                    
              tooltip: 'Cambiar Estado',
              onClick: (event, rowData) => cambiarEstado(rowData)

            }),
            rowData => ({
              icon: 'upload' ,
              //iconProps: { style: {  color: "green" } },                    
              tooltip: 'Subir Documento',
              onClick: (event, rowData) => subirDocumento(rowData)

            }),            
          ]} 
          />      

        {/* </CardBody> */}
        </Card>  
        <DialogScroll
            showDialog={showModalUpload}
            titleBar="Subir Documento"
            maxWidth={maxWidth}
            closeFunction={closeModalUpload}
            showBotonPDF={false}  
          >
            {uploadModalBody()}              
          </DialogScroll>          
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
