
import React from "react";
import {Bar } from 'react-chartjs-2';
import useStyles from '../Common/styles';
import Paper from '@material-ui/core/Paper';

  export interface Props {
    data: any;  

  }

  const TipoCBarChart = (props: Props) => {

    return (
      <Paper elevation={3}>
      <div>
        <Bar
            data= {props.data}  
            height= {250}
            width= {200}
            options={{ 
              // plugins: {
              //   custom_canvas_background_color: {
              //     color: '#14c6eb'
              //   },
              // },
              label: false,
              maintainAspectRatio: false ,  
              scales: {
                    xAxes: [{
                      gridLines: {
                        display: false
                      }
                    }],
              
                    yAxes: [
                      {
                        ticks: {
                          beginAtZero: true,
                        },
                        gridLines: {
                          display: false
                        }                        
                      },
                    ],
                  },
                  legend: {
                     labels: {
                      fontSize: 25,
                    },
                  },
            }}

 
        />    

      </div>
      </Paper>
    )
  }

  export default TipoCBarChart