import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

import {Toolbar,AppBar, IconButton, Hidden} from "@material-ui/core";

// @material-ui/icons
import Menu from "@material-ui/icons/Menu";
import MenuIcon from '@mui/icons-material/Menu';
// core components
import AdminNavbarLinks from "./AdminNavbarLinks.js";
//import RTLNavbarLinks from "./RTLNavbarLinks.js";


import styles from "../../assets/jss/material-dashboard-react/components/headerStyle.js";

const useStyles = makeStyles(styles);

export default function Header(props) {
  const classes = useStyles();
  //const routeName = useRouteName();
  const { color } = props;
  const appBarClasses = classNames({
    [" " + classes[color]]: color,
  });
  return (
    <AppBar className={classes.appBar + appBarClasses}>
      <Toolbar className={classes.container}>
        {/* <DashboardRounded color='secondary'  className={classes.icons} fontSize="large" /> */}
        <Hidden mdDown implementation="css">
        <IconButton            
            size="large"
            edge="start"
            color="primary"
            aria-label="open drawer"
            sx={{ mr: 1 }}
            //onClick={props.handleDrawerToggle}
          >
            <MenuIcon />
        </IconButton>
        </Hidden>
        <div className={classes.flex}>
          <div className={classes.title}>Cliente TATC</div>
        </div>
        {/* <div className={classes.flex}>
          {/* <Button color="transparent" href="" className={classes.title}>
            {routeName}
          </Button> */}
        {/* </div> */}
        <div >
         <p style={{marginRight:5}} className={classes.title} >{props.userCurrent}</p>
        </div>

        <Hidden smDown implementation="css">        
          <AdminNavbarLinks logOut = {props.logOut} userCurrent = {props.userCurrent}/>
        </Hidden>
        
        <Hidden mdUp implementation="css">
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={props.handleDrawerToggle}
          >
            <Menu />
          </IconButton>
        </Hidden>
        
      </Toolbar>
    </AppBar>
  );
}

Header.propTypes = {
  color: PropTypes.oneOf(["primary", "info", "success", "warning", "danger"]),
  rtlActive: PropTypes.bool,
  handleDrawerToggle: PropTypes.func,
  logOut: PropTypes.func,
  routes: PropTypes.arrayOf(PropTypes.object),
};
