import React from 'react';
import {Chip, Grid, InputLabel, MenuItem, Select,  FormControl, Paper } from "@material-ui/core"
import TextField from '@mui/material/TextField';

import { makeStyles } from "@material-ui/core/styles";
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

export interface Props {
  ContenedorList: any,
  TipoContenedorList: any,
  TipoTamanoList: any,                            
  traspasonew: any,
  tipoBulto :string,  
  valor: string    
}
const useStylesTypo = makeStyles(styles);
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

export default function TraspasoContenedorExistente (props: Props) {
  const classes = useStyles()
  const classesTypo = useStylesTypo()  
    return (
      <React.Fragment>    
        {/* <Grid
                   container spacing={2}
                   style={{marginBottom: 15}}
                       item xs={12} md={12}>
         <Paper elevation={3} style={{width: '100%',  height: 'auto'}} >
          <Grid id="cont_Aduana_Operador_Numero" item xs={12} md={12} style={{marginBottom: 10}}> */}
        <GridContainer  style={{marginTop: -20}}>
		<GridItem xs={12} sm={6} md={4}>  
          <FormControl id="ctl_TATC_Contenedor" style={{width:'100%', marginTop: 10}}>
              {/* <InputLabel id="demo-simple-select-label">Contenedor</InputLabel> */}
              <TextField
                label = "Contenedor"
                select
                value={props.traspasonew.contenedor}
                defaultValue="0"
                displayEmpty={false}
                variant="outlined"
                fullWidth
                size = 'small' 
                inputProps={{ readOnly: true ,style: { fontSize: 14 }}}                                              
              >              
              <MenuItem value="0" style= {{ fontSize: "14px" }}>              
			    {<div className={classesTypo.mutedText}>Seleccione Contenedor</div>} 
              </MenuItem>
                {props.ContenedorList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.numero}</div></MenuItem>})}
              </TextField>
          </FormControl>    
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>  
          <FormControl id="ctl_TATC_TipoContenedor" style={{width:'100%', marginTop: 10}} >
              {/* <InputLabel >Tipo de Contenedor</InputLabel> */}
              <TextField
                select
                label = "Tipo de Contenedor"
                value={props.traspasonew.tipocontenedor}
                defaultValue="0"
                displayEmpty={false}
                fullWidth={true}
                size = 'small'
				variant="outlined"    
                inputProps={{ readOnly: true,style: { fontSize: 14 } }}                                              
              >              
              <MenuItem value="0" style= {{ fontSize: "14px" }}>              
				{<div className={classesTypo.mutedText}>Seleccione Tipo Contenedor</div>} 
			  </MenuItem>
                {props.TipoContenedorList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.tipo}</div></MenuItem>})}
              </TextField>
          </FormControl>
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>  
          <FormControl id="ctl_TATC_Tamano" style={{width:'100%', marginTop: 10}} >
              {/* <InputLabel >Tamaño</InputLabel> */}
              <TextField
                select
                label = "Tamaño"
                value={props.traspasonew.tamano}
                defaultValue="0"
                displayEmpty={false}
                fullWidth={true}
                size = 'small'
				variant="outlined"  
                inputProps={{ readOnly: true,style: { fontSize: 14 } }}                                              
              >              
              <MenuItem value="0" style= {{ fontSize: "14px" }}>              
				{<div className={classesTypo.mutedText}>Seleccione Tamaño</div>} 
			  </MenuItem>
                {props.TipoTamanoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.tipo}</div></MenuItem>})}
              </TextField>
          </FormControl>
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>  
		  <TextField id="ctl_TipoBulto" 
                style={{ marginTop: 10}}
                label="Tipo Bulto"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.tipoBulto}
            />  
        </GridItem>
        <GridItem xs={12} sm={6} md={4}> 
			<TextField id="ctl_Valor" 
                style={{ marginTop: 10}}
                label="Valor"
                type="number"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.valor}
            />
        </GridItem> 
        </GridContainer>
            {/* </Grid>                       
         </Paper>                                     
        </Grid> */}

      </React.Fragment> 
    )
}


