import React from 'react'
import FiltroBusquedaFisControl from './FiltroBusquedaFisControl'
import ResultadosFisControl from './ResultadosFisControl'
import LocalActivityRounded from "@mui/icons-material/LocalActivityRounded";
import PageHeader from '../PageHeader'

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
//import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/CardMdr/CardBody.js";
import CardFooter from "../../components/CardMdr/CardFooter.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";

import GetCsv from '../GetCsv';
import * as api from '../../api';
import { host, client } from '../../conf';


const styles = {
    cardCategoryWhite: {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    cardTitleWhite: {
      color: "#FFFFFF",
      marginTop: "0px",
      minHeight: "auto",
      fontWeight: "300",
      fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
      marginBottom: "3px",
      textDecoration: "none",
    },
  };
  const useStyleCards = makeStyles(styles);

const FiscalizacionControl = () => {
    const [expresionFiltroTitulos, setexpresionFiltroTitulos] = React.useState('');
    const [buscando, setBuscando] = React.useState(false);
    const classesCards = useStyleCards();
    return (
        <React.Fragment>
            <GridContainer >
            <GridItem md={12}>
            <Card>
            <CardHeader color="primary">
                <CardIcon color="primary">
                    <LocalActivityRounded fontSize="large" />
                </CardIcon>
                <h4 className={classesCards.cardTitleWhite}>Control de Plazos</h4>
                <p className={classesCards.cardCategoryWhite}>Gestión de Prórrogas, Cierre de TATC e Impresión de Reportes de Aduana</p>
            </CardHeader>
            <CardBody>
                <FiltroBusquedaFisControl
                        setexpresionFiltroTitulos={setexpresionFiltroTitulos}
                        setBuscando = {setBuscando}
                /></CardBody>            
                <ResultadosFisControl
                    expresionFiltroTitulos={expresionFiltroTitulos}
                    buscando = {buscando}
                />
                     
            </Card>
            </GridItem>           
            </GridContainer>
        </React.Fragment>
    )
}

export default FiscalizacionControl
