import React from 'react';
import {MenuItem, FormControl, Paper } from "@material-ui/core"
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
//import Autocomplete from "@material-ui/lab/Autocomplete";
//import Paper from "@material-ui/core/Paper";

import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
const useStylesTypo = makeStyles(styles);

export interface Props {

  autoC: any,
  ContenedorDispList: any,
  TipoContenedorList: any,
  TipoTamanoList: any,                            
  TipoBultoList: any,  
  titulo: any,
  numContPropio: string,
  tipoBulto: string,                      
  valor: string,    
  
  errorContenedor : boolean, 
  errorTipoContenedor: boolean, 
  errorTipoTamano : boolean,
  errorValorCIF : boolean,
  errorDestinoContenedor:boolean,   

  setErrorContenedor : Function, 
  setErrorTipoContenedor: Function, 
  setErrorTipoTamano : Function,
  setErrorDestinoContenedor: Function,
  setTitulo: Function,
  setNumContPropio : Function 
}


function fgValidaContenedor(varContenedor: string): boolean
{
	//if (varContenedor.toUpperCase().trim().substr(3,1) != 'U') {return (false)}
	let i = 0;
	let intdig = 0;
	let intValor = 0;
  	let strDigito = 0;
  	let ch = "";
	for (i = 0; i <= (varContenedor.length - 1); i++)   	/*extrae sólo los caracteres*/
	{
		ch = varContenedor.toUpperCase().trim().substr(i,1);   	/*numéricos sin dígito verificador*/
		if(ch >= "0" && ch <= "9")
		{
			intValor = Number(ch);
		}
		else
		{
			if(ch == "A")
			{
				intValor = 10;
			}
			if(ch == "B")
			{
				intValor = 12;
			}
			if(ch == "C")
			{
				intValor = 13;
			}
			if(ch == "D")
			{
				intValor = 14;
			}
			if(ch == "E")
			{
				intValor = 15;
			}
			if(ch == "F")
			{
				intValor = 16;
			}
			if(ch == "G")
			{
				intValor = 17;
			}
			if(ch == "H")
			{
				intValor = 18;
			}
			if(ch == "I")
			{
				intValor = 19;
			}
			if(ch == "J")
			{
				intValor = 20;
			}
			if(ch == "K")
			{
				intValor = 21;
			}
			if(ch == "L")
			{
				intValor = 23;
			}
			if(ch == "M")
			{
				intValor = 24;
			}
			if(ch == "N")
			{
				intValor = 25;
			}
			if(ch == "O")
			{
				intValor = 26;
			}
			if(ch == "P")
			{
				intValor = 27;
			}
			if(ch == "Q")
			{
				intValor = 28;
			}
			if(ch == "R")
			{
				intValor = 29;
			}
			if(ch == "S")
			{
				intValor = 30;
			}
			if(ch == "T")
			{
				intValor = 31;
			}
			if(ch == "U")
			{
				intValor = 32;
			}
			if(ch == "V")
			{
				intValor = 34;
			}
			if(ch == "W")
			{
				intValor = 35;
			}
			if(ch == "X")
			{
				intValor = 36;
			}
			if(ch == "Y")
			{
				intValor = 37;
			}
			if(ch == "Z")
			{
				intValor = 38;
			}
		}
		i = i + 1;
		if((i - 1) == 0)
		{
			intdig = (intdig + (intValor * 1));
		}
		if((i - 1) == 1)
		{
			intdig = (intdig + (intValor * 2));
		}
		if((i - 1) == 2)
		{
			intdig = (intdig + (intValor * 4));
		}
		if((i - 1) == 3)
		{
			intdig = (intdig + (intValor * 8));
		}
		if((i - 1) == 4)
		{
			intdig = (intdig + (intValor * 16));
		}
		if((i - 1) == 5)
		{
			intdig = (intdig + (intValor * 32));
		}
		if((i - 1) == 6)
		{
			intdig = (intdig + (intValor * 64));
		}
		if((i - 1) == 7)
		{
			intdig = (intdig + (intValor * 128));
		}
		if((i - 1) == 8)
		{
			intdig = (intdig + (intValor * 256));
		}
		if((i - 1) == 9)
		{
			intdig = (intdig + (intValor * 512));
		}
		i = i - 1;
	}

	strDigito = (intdig % 11);
	if(strDigito == 10)
	{
		strDigito = 0;
	}

	ch = varContenedor.trim().substr(i-1,1);
	return (Number(ch) == strDigito);

}

const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

  const CustomPaper = (props) => {
	return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
  };

export default function TTCContenedor (props: Props) {

  const [errorMessageContenedor, setErrorMessageContenedor] = React.useState(''); 	
  const [nameClassContenedor, setNameClassContenedor] = React.useState(''); 
  const [nameClassTipoContenedor, setNameClassTipoContenedor] = React.useState('');
  const [nameClassTipoTamano, setNameClassTipoTamano] = React.useState('');
  const [nameClassValor, setNameClassValor] = React.useState('');
  const [nameClassDestinoContenedor, setNameClassDestinoContenedor] = React.useState('');  

  const classes = useStyles()
  const classesTypo = useStylesTypo()  

  const defaultProps = {
    options: props.ContenedorDispList,
    getOptionLabel: (option) => option.numero,
  };
  
  React.useEffect(() => {
    setNameClassTipoContenedor(props.errorTipoContenedor == true ? classes.textFieldRed: classes.textField )
  }, [props.errorTipoContenedor])

  React.useEffect(() => {
    setNameClassTipoTamano(props.errorTipoTamano == true ? classes.textFieldRed: classes.textField )
  }, [props.errorTipoTamano])  

  React.useEffect(() => {
    setNameClassContenedor(props.errorContenedor == true ? classes.textFieldRed: classes.textField )
  }, [props.errorContenedor])

  React.useEffect(() => {
    setNameClassValor(props.errorValorCIF == true ? classes.textFieldRed: classes.textField )
  }, [props.errorValorCIF])

  React.useEffect(() => {
    setNameClassDestinoContenedor(props.errorDestinoContenedor == true ? classes.textFieldRed: classes.textField )
  }, [props.errorDestinoContenedor])

    return (	   
      <React.Fragment>
		<GridContainer  style={{marginTop: -20}}>        
          <GridItem xs={12} sm={12} md={4} >
		  <FormControl id="ctl_NumContenedor" style={{width:'100%',marginTop: 10}}
			//error={props.errorContenedor} 			  
			required>				   
			<Autocomplete 
				style= {{ width:'100%' }}												
				//disablePortal 
				{...defaultProps}											 
				ref={props.autoC}						
				noOptionsText={'No existen opciones....'}
				id="numcontenedor"						
				PaperComponent={CustomPaper}						
				freeSolo																					inputValue={props.numContPropio}	
				onInputChange={(event, newInputValue) => {
					newInputValue = newInputValue.toUpperCase().trim()
					props.setNumContPropio(newInputValue)
					props.setErrorContenedor(true)
					setErrorMessageContenedor('')
					if (newInputValue.length != 11) {setErrorMessageContenedor('Debe contener 11 caracteres')}
					else if (newInputValue.substr(3,1) != 'U') {setErrorMessageContenedor('El cuarto caracter debe ser U')}
					else if (!fgValidaContenedor(newInputValue)) {setErrorMessageContenedor('Número incorrecto')} 
					else {
						props.setErrorContenedor(false)
					}                        
				}}																																		
				renderInput={(params) =>							
					<TextField  
						classes={{ root: classes.customTextField }}
						className={nameClassContenedor}																											
						{...params}								
						//autoFocus												
						required							
						helperText={errorMessageContenedor} 
						variant="outlined" 
						label = 'Contenedor'						
						size = 'small'																		
				/>}						
			/>                
          </FormControl> 
		  </GridItem>
		  
          <GridItem xs={12} sm={12} md={4}>
		  <FormControl id="ctl_TATC_TipoContenedor" style={{width:'100%',marginTop: 10}} 
		  	  //error={props.errorTipoContenedor}
			  required 
			  className={nameClassTipoContenedor} 
			  >
              {/* <InputLabel >Tipo de Contenedor</InputLabel> */}
              <TextField
			  	  required
                  value={props.titulo.tipocontenedor}
                  defaultValue="0"
				  label="Tipo de Contenedor"
				  select
                  //displayEmpty={false}				  
                  fullWidth={true}  
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, tipocontenedor: event.target.value})
                    props.setErrorTipoContenedor((event.target.value === "0"))
                  }}
				  variant="outlined"
				  size = 'small'
				  inputProps={{
                    style: { fontSize: 14 },                                     
                  }}                                              
              >             
			  <MenuItem value="0" style= {{ fontSize: 14 }}>
                  {<div className={classesTypo.mutedText}>Seleccione Tipo Contenedor</div>} 
              </MenuItem>
              {props.TipoContenedorList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: 14 }}>{c.tipo}</div></MenuItem>})}
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
          </FormControl>				
		  </GridItem>							
          
		  <GridItem xs={12} sm={12} md={4}>
          <FormControl id="ctl_TATC_Tamano" style={{width:'100%', marginTop: 10}} 
		      //error={props.errorTipoTamano}
			  required 
			  className={nameClassTipoTamano} >              
              <TextField
			  	  required
                  value={props.titulo.tamano}
                  defaultValue="0"
				  label="Tamaño"
				  select                 
                  fullWidth={true}
				  size = 'small'  
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, tamano: event.target.value})
                    props.setErrorTipoTamano((event.target.value === "0"))
                  }}                                             
              >
              <MenuItem value="0" style= {{ fontSize: "14px" }}>              
			  {<div className={classesTypo.mutedText}>Seleccione Tamaño</div>} 
              </MenuItem>
              {props.TipoTamanoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.tipo}</div></MenuItem>})}
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
          </FormControl>
		  </GridItem>
		  
		  <GridItem xs={12} sm={6} md={4}>
		  <TextField id="ctl_TipoBulto"                 
                label="Código Tipo de Bulto"
                type="text"
                style={{ marginTop: 10}}
				variant="outlined"
				fullWidth
				size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.tipoBulto}
            />  
		  </GridItem>
		  
		  <GridItem xs={12} sm={6} md={4}>
		  <TextField id="ctl_Valor" 
                style={{ marginTop: 10}}
				required 
				className={nameClassValor}				
                label={props.titulo.tipooper === 2? "Valor FOB":"Valor CIF"}
                type="number"
				variant="outlined"
				fullWidth
				size = 'small'
			
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.valor}
            /> 
		  </GridItem>
		{props.titulo.tipooper === 2 &&
		  <GridItem xs={12} sm={6} md={4}>                 
            <TextField id="ctl_Dest_Cont" 
             style={{ marginTop: 10}}
             variant="outlined"
             fullWidth
             size = 'small'
              inputProps={{
                  style: { fontSize: 14 },                                     
              }}
              label="Destino Contenedor"
              type="text"
              required
              //error = {props.errorDocTpte}              
              className={nameClassDestinoContenedor}
              value={props.titulo.destino_contenedor}
              onChange={(event) => {
                props.setTitulo({...props.titulo, destino_contenedor: event.target.value})
                props.setErrorDestinoContenedor((!event.target.value || (event.target.value.toString().length === 0)));
              }}                
          /> 
        </GridItem>
		}	 			
   
		</GridContainer>
      </React.Fragment> 
    )
}


