import React, { useRef } from 'react';
import Select from '@material-ui/core/Select';

import NumberFormat from 'react-number-format';

import * as myInterfaces from '../TATC-TSTC/interfaces';
import useStyles from '../Common/styles';
import * as api from '../../api';
import { host, client } from '../../conf';

import {DialogActions, DialogContent, Paper} from "@material-ui/core"
import {Box, Grid, InputLabel, List, ListItem, Divider, FormControl} from "@material-ui/core"

import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@mui/material/TextField';
import Button from '@material-ui/core/Button';
import Autocomplete from '@mui/material/Autocomplete';
import CustomButtons from '../../components/CustomButtons/Button.js';

import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import SearchOffRoundedIcon from '@mui/icons-material/SearchOffRounded';


import DateRangePicker from '@mui/lab/DateRangePicker';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import esLocale from 'date-fns/locale/es';
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
import Notification from '../Notification';
import { makeStyles } from "@material-ui/core/styles";

import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";


export interface Props {
    setexpresionFiltroTitulos: Function; 
    setBuscando: Function;         
}
const useStylesType = makeStyles(styles)
const useTextFieldStyles = makeStyles({	
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

const CustomPaper = (props) => {
    return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
};

function isDigitCode(n) {
    //return(n >= charCodeZero && n <= charCodeNine);
    return(n >= "0" && n <= "9")
 }  

const FiltroBusquedaControlInv = (props: Props) => {

    const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});

    const classesTypo = useStylesType(styles)
    const classesTextField = useTextFieldStyles()

    const [contenedorList,          setContenedorList]          = React.useState([]);
    const [tipoContenedorList,      setTipoContenedorList]      = React.useState([]);
    const [tipoTamanoList,          setTipoTamanoList]          = React.useState([]);    
    const [tituloList,              setTituloList]              = React.useState([]);    
    const [depositoList,            setDepositoList]            = React.useState([]);
    const [tipoOperList,            setTipoOperList]            = React.useState([]);  
    const [aduanaList,              setAduanaList]              = React.useState([]);  

    const [tipoContenedorId,        setTipoContenedorId]        = React.useState(0);    
    const [tipoTamanoId,            setTipoTamanoId]            = React.useState(0);      
    const [depositoId,              setDepositoId]              = React.useState(0);           
    const [desdeFE,                 setDesdeFE]                 = React.useState('');
    const [hastaFE,                 setHastaFE]                 = React.useState('');
    const [estado,                  setEstado]                  = React.useState('0');
    const [numero,                  setNumero]                  = React.useState('');
    const [tipoOperId,              setTipoOperId]              = React.useState(0);   
    const [aduanaId,                setAduanaId]                = React.useState(0);           
    const [numero2,                 setNumero2]                 = React.useState('');      

    const getTipoContenedorList = () => {
        api.getData(host + '/api/inventariostipocontenedor/').then(x => {
            if (x.status === 200) {
                setTipoContenedorList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getTipoTamanoList = () => {
        api.getData(host + '/api/inventariostipotamano/').then(x => {
            if (x.status === 200) {
                setTipoTamanoList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getContenedorList = () => {
        api.getData(host + '/api/inventarioscontenedor/').then(x => {
            if (x.status === 200) {
                setContenedorList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getTituloList = () => {
        api.getData(host + '/api/tatc_tstctitulo/').then(x => {
            if (x.status === 200) {
                setTituloList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }    

    const getDepositoList = () => {
        api.getData(host + '/api/inventariosdeposito/').then(x => {
            if (x.status === 200) {
                setDepositoList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getTipoOperList = () => {
        api.getData(host + '/api/fiscalizacion_controltipooper/').then(x => {
            if (x.status === 200) {
                setTipoOperList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }    

    const getAduanasList = () => {
        api.getData(host + '/api/fiscalizacion_controladuana/').then(x => {
            if (x.status === 200) {
                setAduanaList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }    

    const defaultProps = {
        options: tituloList,
        getOptionLabel: (option) => option.numero,
      };   
      
      const defaultProps2 = {
        options: contenedorList,
        getOptionLabel: (option) => option.numero,
    };       

    const generarExpresionFiltros = () => {

        var condition1 = (tipoContenedorId>0) ? 'contenedor__tipocontenedor__id='+ tipoContenedorId.toString() : '';      
        var condition2 = (numero2!= '') ? 'contenedor__numero='+ numero2.replace("-", "") : '';
        var condition3 = (numero!= '') ? 'numero='+ numero : ''; 
        var condition4 = (depositoId>0) ? 'deposito__id='+ depositoId.toString() : '';
        var condition5 = (tipoTamanoId>0) ? 'contenedor__tamano__id='+ tipoTamanoId.toString() : '';        
        var condition6 = (aduanaId>0) ? 'aduana__id='+ aduanaId.toString() : '';       
        var condition7 = (desdeFE != '') ? 'min_fecha_emision='+ desdeFE.toString().replace('-','/') : '';
        var condition8 = (hastaFE != '') ? 'max_fecha_emision='+ hastaFE.toString().replace('-','/') : '';                     
        var condition9 = (estado != '0') ? 'estado='+ estado : '';  
        var condition10 = (tipoOperId>0) ? 'tipooper__id='+ tipoOperId.toString() : '';          

        var expresionFiltroTitulos = '';
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition1.length>0 ?'&':'') + condition1;        
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition2.length>0 ?'&':'') + condition2;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition3.length>0?'&':'') + condition3;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition4.length>0?'&':'') + condition4;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition5.length>0?'&':'') + condition5;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition6.length>0?'&':'') + condition6;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition7.length>0?'&':'') + condition7;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition8.length>0?'&':'') + condition8; 
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition9.length>0?'&':'') + condition9;  
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition10.length>0?'&':'') + condition10;               
        
        props.setexpresionFiltroTitulos(expresionFiltroTitulos);
        props.setBuscando(true) 

    }      
    
    const [value, setValue] = React.useState([null, null]);  
    

    const setearExpresionFiltros = () => {

        props.setexpresionFiltroTitulos('')
       
        setValue([null,null])
        setTipoOperId(0)
        setTipoContenedorId(0) 
        setTipoTamanoId(0) 
        setDepositoId(0)   
        setAduanaId(0)                          
        setNumero('')        
        setNumero2('')           
        setDesdeFE('')
        setHastaFE('')
        setEstado('0')
  

        const ele = autoC.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
        if (ele) ele.click();

        const ele2 = autoC2.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
        if (ele2) ele2.click();          

    } 

    const autoC = useRef(null); 
    const autoC2 = useRef(null);       


    React.useEffect(() => {
        getTipoContenedorList();
        getTipoTamanoList();        
        getContenedorList();
        getTituloList();              
        getDepositoList();
        getTipoOperList();
        getAduanasList();        
    }, [])

    return (
<React.Fragment>
<GridContainer style={{marginTop: 10}}>    
    <GridItem xs={12} sm={6} md={4} >
    <FormControl style={{width:'100%',marginTop: 10}} 
        size = 'small'>
        <Autocomplete
            {...defaultProps}
            ref={autoC}
            noOptionsText={'No existen opciones....'}
            id="controllable-states-demo"
            inputValue={numero}
            PaperComponent={CustomPaper}
            onInputChange={(event, newInputValue) => {
                setNumero(newInputValue);
            }}
            //sx={{ width: 300 }}
            renderInput={(params) => 
            <TextField {...params} 
            label = 'Número TATC/TSTC' 
            classes={{ root: classesTextField.customTextField }} 
            onKeyPress={(event) => {
                if (!isDigitCode(event?.key)) {
                  event.preventDefault();
                }
              }}             
            size = 'small' 
            variant="outlined"/>}
        />
    </FormControl> 
    </GridItem>

    <GridItem xs={12} sm={6} md={4} >
        <FormControl style={{width:'100%'}}>               
        <TextField                    
            id="demo-simple-select"
            label="Tipo"
            select
            value={tipoOperId}
            onChange={(event: any) => {setTipoOperId(event.target.value)}}
            defaultValue="0"
            displayEmpty={false}                   
            variant="outlined"                             
            style={{marginTop: 10}} 
            size = 'small'                   
            inputProps={{
                style: { fontSize: 14 },                                     
            }}                  
            >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>
                {<div className={classesTypo.mutedText}>Seleccione Tipo</div>} 
            </MenuItem>
            {tipoOperList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}
        </TextField>
        </FormControl> 
     </GridItem>

     <GridItem xs={12} sm={6} md={4} >
    <FormControl  style={{width:'100%'}}>               
        <TextField                   
            id="demo-simple-select-required"                    
            select
            value={estado}
            onChange={(event: any) => {setEstado(event.target.value)}}
            label="Estado"
            variant="outlined"                             
            style={{marginTop: 10}} 
            size = 'small'                                        
            inputProps={{
                style: { fontSize: 14 },                                     
            }} 
            >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>
                {<div className={classesTypo.mutedText}>Seleccione Estado</div>} 
            </MenuItem>
            <MenuItem value={'A'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Abierto</div></MenuItem>
            <MenuItem value={'C'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Cerrado</div></MenuItem>
        </TextField>
    </FormControl> 
    </GridItem>

    <GridItem xs={12} sm={6} md={4} >
        <FormControl style={{width:'100%',marginTop: 10}}>
        <Autocomplete
            {...defaultProps2}
            ref={autoC2}
            openOnFocus
            noOptionsText={'No existen opciones....'}
            id="controllable-states-demo"
            PaperComponent={CustomPaper}
            inputValue={numero2}
            onInputChange={(event, newInputValue) => {
                setNumero2(newInputValue);
            }}                   
            renderInput={(params) => 
            <TextField {...params} 
            label = 'Contenedor' 
            classes={{ root: classesTextField.customTextField }} 
            size = 'small'
            variant="outlined"/>}
        />
        </FormControl> 
    </GridItem>

    <GridItem xs={12} sm={6} md={4} >
    <FormControl style={{width:'100%'}}>
        {/* <InputLabel id="demo-simple-select-label">Tipo de  Contenedor</InputLabel> */}
        <TextField
            select
            label="Tipo de Contenedor"
            id="tipoContenedor"
            value={tipoContenedorId}
            onChange={(event: any) => {setTipoContenedorId(event.target.value)}}
            defaultValue="0"
            style={{marginTop: 10}} 
            displayEmpty={false}
            fullWidth={true}
            size = 'small'                   
                inputProps={{
                    style: { fontSize: 14 },                                     
            }} 
        >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>                
                {<div className={classesTypo.mutedText}>Seleccione Tipo Contenedor</div>} 
            </MenuItem>
            {tipoContenedorList.map((tipoContenedor: myInterfaces.tipoContenedorType) => <MenuItem key={tipoContenedor.id} value={tipoContenedor.id}><div style= {{ fontSize: "14px"}}>{tipoContenedor.tipo}</div></MenuItem>)}
        </TextField>
    </FormControl>
    </GridItem>

    <GridItem xs={12} sm={6} md={4} >    
    <FormControl style={{width:'100%'}}>
        {/* <InputLabel id="demo-simple-select-label">Tamaño</InputLabel> */}
        <TextField            
            id="tipotamanoContenedor"
            label="Tamaño"
            select
            value={tipoTamanoId}
            onChange={(event: any) => {setTipoTamanoId(event.target.value)}}
            defaultValue="0"
            displayEmpty={false}
            fullWidth={true}
            variant="outlined"                             
            style={{marginTop: 10}} 
            size = 'small'                   
            inputProps={{
                style: { fontSize: 14 },                                     
            }} 
        >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>                
                {<div className={classesTypo.mutedText}>Seleccione Tamaño</div>} 
            </MenuItem>
            {tipoTamanoList.map((tipoTamano: myInterfaces.tipoTamanoType) => <MenuItem key={tipoTamano.id} value={tipoTamano.id}><div style= {{ fontSize: "14px"}}>{tipoTamano.tipo}</div></MenuItem>)}
        </TextField>
    </FormControl> 
    </GridItem>

    <GridItem xs={12} sm={6} md={4} >  
    <FormControl style={{width:'100%'}}>        
        <TextField
            select
            label="Aduana"
            id="simple-select-Aduana"
            value={aduanaId}
            onChange={(event: any) => {setAduanaId(event.target.value)}}
            defaultValue="0"
            displayEmpty={false}
            fullWidth={true}
            variant="outlined"                             
            style={{marginTop: 10}} 
            size = 'small'                                        
            inputProps={{
                style: { fontSize: 14 },                                     
            }}                   
        >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>            
            {<div className={classesTypo.mutedText}>Seleccione Aduana</div>} 
            </MenuItem>
            {aduanaList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}
        </TextField>
    </FormControl>                        
    </GridItem>

    <GridItem xs={12} sm={6} md={4} > 
    <FormControl style={{width:'100%'}}>        
        <TextField
            select
            label="Depósito"
            id="deposito"
            value={depositoId}
            onChange={(event: any) => {setDepositoId(event.target.value)}}
            defaultValue="0"
            displayEmpty={false}
            fullWidth={true}
            variant="outlined"                             
            style={{marginTop: 10}} 
            size = 'small'                                        
            inputProps={{
                style: { fontSize: 14 },                                     
            }} 
        >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>
                {<div className={classesTypo.mutedText}>Seleccione Depósito</div>} 
            </MenuItem>
            {depositoList.map((deposito: myInterfaces.depositoType) => <MenuItem key={deposito.id} value={deposito.id}><div style= {{ fontSize: "14px"}}>{deposito.nombre}</div></MenuItem>)}
        </TextField>
    </FormControl> 
    </GridItem>

    
    <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
    <GridItem xs={12} sm={6} md={4} > 
        <FormControl style={{width:'100%',marginTop: 10}}>                                
        <DateRangePicker 
            startText="Fecha Emisión Desde"
            endText="Hasta"
            size = 'small' 
            value={value}
            onChange={(newValue) => {
                setValue(newValue);
                setDesdeFE(newValue[0] !== null ? newValue[0].toLocaleDateString():'')
                setHastaFE(newValue[1] !== null ? newValue[1].toLocaleDateString():'')
            }}
            renderInput={(startProps, endProps) => (
            <React.Fragment>
                <TextField 
                {...startProps} 
                variant="outlined"
                size = 'small'
                style={{width: '100%'}} 
                />
                <Box sx={{ mx: 2 }}> a </Box>
                <TextField
                {...endProps}
                variant="outlined"
                size = 'small'
                style={{width: '100%'}}  
                />
            </React.Fragment>
            )}
        />
        </FormControl>
    </GridItem>  
    </LocalizationProvider> 
         
    <Divider  />
    <GridItem md={12}  style={{width:'100%',marginTop: 10}}>    
        <CustomButtons variant="contained" round 
            startIcon={<SearchRoundedIcon />}
            color="primary"  
            //style={{ backgroundColor: '#01579b', color: '#ffffff', marginLeft: 15}}
            variant="contained"  
            onClick={() => { generarExpresionFiltros(); }}>
            Buscar
        </CustomButtons>  
        <CustomButtons variant="contained" round  
            startIcon={<SearchOffRoundedIcon />} 
            //style={{ backgroundColor: '#01579b', color: '#ffffff', marginLeft: 15}} 
            variant="contained"
            style={{ marginLeft: 10}}  
            color="primary"  
            onClick={() => { setearExpresionFiltros(); }}>
            Inicializar
        </CustomButtons>            
    </GridItem>  
    <Notification
        notify = {notify}
        setNotify = {setNotify}
    > 
    </Notification>
</GridContainer>  
</React.Fragment>
 )}

export default FiltroBusquedaControlInv
