import * as api from '../../api';
import * as React from 'react';
import { host, client } from '../../conf';

import {TextField} from "@material-ui/core"
import DriveEtaRoundedIcon from '@material-ui/icons/DriveEtaRounded';

import Notification from '../Notification'
import CustomMaterialTable from '../CustomMaterialTable'

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

var Fn = {
  validaRut: function(rutCompleto) {
      rutCompleto = rutCompleto.toUpperCase().trim()
      if (!/^[0-9]+-[0-9kK]{1}$/.test(rutCompleto))
          return false;
      var tmp = rutCompleto.split('-');
      var digv = tmp[1];
      var rut = tmp[0];
      return (Fn.dv(rut).toString() === digv);
  },
  dv: function(T) {
      var M = 0,
          S = 1;
      for (; T; T = Math.floor(T / 10))
          S = (S + T % 10 * (9 - M++ % 6)) % 11;
      return S ? S - 1 : 'K';
  }
}



export default function Chofer(props: any) {

  const [ChoferList, updateChoferList] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [empTpteList,updateEmpTpteList]= React.useState({}) 
  const classesCards = useStyleCards();  
  const updateAllEmpTpte = () => {
      api.getData(host + '/api/fiscalizacion_controlemptpte/?cliente__id='+client).then(x => {
        if(x.status === 200){
          const empTpteList={}
          x.data.map(row=>empTpteList[row.id]=row.nombre)
          updateEmpTpteList(empTpteList)          

        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
    }

  React.useEffect(() => {
      updateAllEmpTpte();    
  }, [])

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
              <CardIcon color="primary">
                  <DriveEtaRoundedIcon fontSize="large" />
              </CardIcon>
              <h4 className={classesCards.cardTitleWhite}>Chóferes</h4>
              <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody> */}
          <CustomMaterialTable
            baseList = {ChoferList}
            setBaseList= {updateChoferList}
            baseColumns= {[
              { field: 'id', title: 'Id' , hidden: true, export: false},
              { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                 
              { field: 'rut', title: 'RUT', defaultSort: 'asc',
              editComponent: (editProps) =>  (
                <TextField
                    autoFocus={true}
				            value={editProps.value}
                    onChange={(e) => editProps.onChange(e.target.value)}
				            helperText={editProps.helperText}
				            error={editProps.error}
				            variant="standard"
                />
              ),
              render: rowData => rowData.rut.toUpperCase().trim(),                
              validate: (rowData) =>
                rowData.rut === undefined
                ? { isValid: false, helperText: 'Código Requerido' }
                : rowData.rut === ''
                ? { isValid: false, helperText: 'Código Requerido' }
                : rowData.rut.length !== 10 
                ? 'RUT debe tener 10 caracteres'  
                : rowData.rut.substr(8,1) !== '-' 
                ? 'Dígito 9 debe ser -' 
                : !(Fn.validaRut(rowData.rut)) 
                ? 'RUT no Válido'                                                   
                : true

              },
              { field: 'patente_camion', title: 'Patente',
              validate: (rowData) =>
              rowData.patente_camion === undefined
                ? { isValid: false, helperText: 'Patente Requerida' }
                : rowData.patente_camion === ''
                ? { isValid: false, helperText: 'Patente Requerida' }
                : true            
              }, 
              { field: 'emptpte_name', title: 'Empresa Transportista' , hidden: true, export: true},               
              { field: 'empresa_transportista', title: 'Empresa Transportista', lookup: empTpteList, export: false, 
              validate: (rowData) =>
              rowData.empresa_transportista === undefined
                ? { isValid: false, helperText: 'Empresa Requerida' }
                : rowData.empresa_transportista === ''
                ? { isValid: false, helperText: 'Empresa Requerida' }
                : true            
            },          
            { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},                     
              { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
              { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              
            ]} 
            baseModelName= "Chófer"
            baseModelRoute= "fiscalizacion_controlchofer"
            filename= "Choferes"
            setNotify = {setNotify}
            msgErrorUnique = "RUT, Patente y Empresa Transportista deben ser Únicos"
          />
       {/* </CardBody> */}
        </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
