import * as api from '../../api';
import * as React from 'react';

import { host, client } from '../../conf';

import { TextField} from "@material-ui/core"
import ApartmentRounded from "@mui/icons-material/ApartmentRounded";

import Notification from '../Notification'
import CustomMaterialTable from '../CustomMaterialTable'
import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

export default function TipoContenedor(props: any) {
  const classesCards = useStyleCards(); 
  const [TipoContenedorList, updateTipoContenedorList] = React.useState([]);
  const [tipoBultoList,updateTipoBultoList]= React.useState({}) 
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [genericoContenedorList,updateGenericoContenedorList]= React.useState({}) 

  const updateAllGenericoContenedor = () => {
    api.getData(host + '/api/inventariosgenericocontenedor/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        const genericoContenedorList={}
        x.data.map(row=>genericoContenedorList[row.id]=row.nombre)
        updateGenericoContenedorList(genericoContenedorList)          

      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const updateAllTipoBulto = () => {
    api.getData(host + '/api/inventariostipobulto/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        //const tipoTamanoList={}
        x.data.map(row=>tipoBultoList[row.id]=row.descripcion)
        updateTipoBultoList(tipoBultoList)          
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  React.useEffect(() => {
      updateAllTipoBulto();     
      updateAllGenericoContenedor();
   
 
  }, [])

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <ApartmentRounded fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Tipos de Contenedor</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>*/}      
          <CustomMaterialTable
           baseList = {TipoContenedorList}
           setBaseList= {updateTipoContenedorList}
           baseColumns= {[
            { field: 'id', title: 'Id' , hidden: true, export: false},
            { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                     
            { field: 'tipo', title: 'Tipo de Contenedor', defaultSort: 'asc', 
            editComponent: (editProps) =>  (
              <TextField
                 autoFocus={true}
                  value={editProps.value}
                          onChange={(e) => editProps.onChange(e.target.value)}
                  helperText={editProps.helperText}
                  error={editProps.error}
                  variant="standard"
              />              
            ),            
            validate: (rowData) =>
              rowData.tipo === undefined
              ? { isValid: false, helperText: 'Tipo Requerido' }
              : rowData.tipo === ''
              ? { isValid: false, helperText: 'Tipo Requerido' }
              : true
            },
            { field: 'descripcion', title: 'Descripción', defaultSort: 'asc',
            render: rowData =>{ return (rowData.descripcion === undefined || rowData.descripcion === '' || rowData.descripcion === 'NONE'
              ? ''
              : rowData.descripcion) },
          },            
            { field: 'generico', title: 'Grupo', lookup: genericoContenedorList, export: false,
            validate: (rowData) =>
            rowData.generico === undefined
              ? { isValid: false, helperText: 'Grupo Requerido' }
              : rowData.generico === ''
              ? { isValid: false, helperText: 'Grupo Requerido' }
              : true            
            },    
            
            { field: 'tipobulto', title: 'Tipo Bulto', lookup: tipoBultoList, export: false,
            validate: (rowData) =>
            rowData.tipobulto === undefined
              ? { isValid: false, helperText: 'Tipo Bulto Requerido' }
              : rowData.tipobulto === ''
              ? { isValid: false, helperText: 'Tipo Bulto Requerido' }
              : true            
            },          
            { field: 'generico_nombre', title: 'Grupo' ,  export: true, hidden: true},                    
            { field: 'tipobulto_descripcion', title: 'Tipo Bulto' ,  export: true, hidden: true}, 
            { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},   
            { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
            { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              

        ]} 
           baseModelName= "Tipo de Contenedor"
           baseModelRoute= "inventariostipocontenedor"
           filename= "TiposContenedor"
           setNotify = {setNotify}


          />
       {/* </CardBody> */}
       </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
