export default function TituloaPDF(props: any) {
    const { titulo, showDialog, children, closeFunction } = props;
    const [tituloDialogoReporte, updateTituloDialogoReporte] = React.useState('Reporte Titulo Temporal de Contenedor');
    const [filename, updateFilename] = React.useState('TTTC.pdf');  
    const [maxWidth, updateMaxWidth] = React.useState('md'); 
    const [showBotonPDF, updateshowBotonPDF] = React.useState(true);     
    
   

    React.useEffect(() => {
        updateFilename(props.titulo.tipooper_name + props.titulo.numero + ".pdf"); 
      }, [props.titulo.numero])


    return (
        <div>

                <DialogScroll
                    showDialog={showDialog}
                    titleBar={tituloDialogoReporte}
                    maxWidth={maxWidth}
                    showBotonPDF={showBotonPDF}
                    PDFfilename={filename}
                    PDFref ={ref}
                    closeFunction={closeFunction}
                >
                    <div ref={ref} >
                        <BodyPDF
                            titulo={props.titulo}
                        >
                        </BodyPDF>
                    </div>
                </DialogScroll>                
        </div>
    )
}

