import * as api from '../../api';
import * as React from 'react';
import useStyles from '../Common/styles';
import { host, client } from '../../conf';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import MaterialTable from 'material-table';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import DialogScroll from '../DialogScroll';
import Notification from '../Notification';
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";

export interface Props {
  expresionFiltroTitulos: string;    
  buscando: boolean;  
}

export default function ResultadosFisControl(props: any) {

  const [TTTCList, updateTTTCList] = React.useState([]);
  const [TTTCList2, updateTTTCList2] = React.useState([]);  
  const [loadData, setLoadData] = React.useState(false);
  
  const updateAllTTTC = () => {
    api.getData(host + '/api/tatc_tstctitulo/?'+props.expresionFiltroTitulos).then(x => {
      if(x.status === 200){
        updateTTTCList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
      });
  }

  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});  
  const box_style = {background: "white"}

  const downloadExcel=(filename)=>{
    const newData= TTTCList.map((row)=>{
      return {
        TATC_TSTC: row.numero,
        Tipo: row.tipooper_name,
        Contenedor: row.contenedor_numero,
        Emisión: row.fecha_emision_str,
        Vigencia: row.fecha_vigencia_str,
        Prórrogas: row.prorroga,
        Traspaso: row.titulo_traspaso,
        Estado: row.titulo_estado,        
        Depósito: row.deposito_nombre,  
        Aduana: row.aduana_nombre

      }
    })

    const workSheet=XLSX.utils.json_to_sheet(newData)

    let header = ['TATC_TSTC', 'Tipo', 'Contenedor', 'Emisión',  'Vigencia','Prórrogas', 'Traspaso', 'Estado', 'Depósito', 'Aduana' ]
    const jsonKeys = header || Object.keys(newData[0])

    const objectMaxLength = []
    jsonKeys.forEach((key) => {
      objectMaxLength.push(
        pixelWidth(key, {
          size: 3,
        })
      )
    })

    newData.forEach((data, i) => {
      const value = newData[i]
      jsonKeys.forEach((key, j) => {
        const l = value[jsonKeys[j]]
          ? pixelWidth(value[jsonKeys[j]], {
              size: 3,
            })
          : 0
        objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

      })
    })



    const wscols = objectMaxLength.map((w: number) => ({width: w}))
    workSheet["!cols"] = wscols

    const workBook=XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workBook,workSheet,filename)
    //Buffer
    let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
    //Binary string
    XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
    //Download
    XLSX.writeFile(workBook, filename+".xlsx")


  }    

  React.useEffect(() => {
    if (props.buscando)
    {
      setLoadData(true) 
      api.getData(host + '/api/tatc_tstctitulo/?'+props.expresionFiltroTitulos).then(x => {
        if(x.status === 200){
          updateTTTCList(x.data)
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
        }).finally(() => {setLoadData(false)})
    }
    else {updateTTTCList([])}    
  }, [props.expresionFiltroTitulos, props.buscando])


return (
  <React.Fragment>
    <GridContainer  style={{marginTop: 10}}>
      <GridItem  md={12} > 

              <MaterialTable
                style={{width:'100%'}}
                data = {TTTCList}
                isLoading = {loadData} 
                localization={{
                  pagination: {
                      labelDisplayedRows: '{from}-{to} de {count}',
                      labelRowsSelect: 'filas'
                  },
                  toolbar: {
                      nRowsSelected: '{0} filas(s) seleccionadas',
                      searchTooltip: 'Buscar',
                      searchPlaceholder: 'Buscar',
                      exportTitle: 'Exportar',
                      exportAriaLabel: 'Exportar',
                      exportCSVName: 'EXCEL',
                      exportPDFName: 'PDF'
                  },
                  header: {
                      actions: 'Acciones'
                  },
                  grouping: {
                    groupedBy: 'Agrupar por',
                    placeholder: 'Arraste la columna para agrupar'
                  },
                  body: {
                      emptyDataSourceMessage: 'No existen filas para visualizar',
                      filterRow: {
                          filterTooltip: 'Filtrar'
                      }
                  }
                }}              
                columns={[
                  { field: 'numero', title: 'Título' },
                  { field: 'tipooper_name', title: 'Tipo' },
                  { field: 'contenedor_numero', title: 'Contenedor' },
                  { field: 'fecha_emision', title: 'Vigencia de Título',  type: 'date',
                  dateSetting: {
                    format: 'dd/MM/yyyy'
                  } },                  
                  { field: 'fecha_vigencia', title: 'Fecha de Vencimiento' ,  type: 'date',
                  dateSetting: {
                    format: 'dd/MM/yyyy'
                  }}, 
                  { field: 'prorroga', title: 'Prórrogas' },                  
                  { field: 'titulo_traspaso', title: 'Traspaso' }, 
                  { field: 'titulo_estado', title: 'Estado',  lookup: { 'Abierto': 'Abierto', 'Cerrado': 'Cerrado' } },
                  { field: 'deposito_nombre', title: 'Depósito' },                   
                  { field: 'aduana_nombre', title: 'Aduana' }  

                ]}  
                options={{
                  grouping: true, 
                  actionsColumnIndex: -1,
                  headerStyle: {
                    backgroundColor: '#8e24aa',//'#01579b',
                    color: '#FFF'
                  },               
                  showTitle: false, 
                  exportAllData: true,                     
                  exportButton: true,
                  exportFileName: 'FiscalizacionAduanas', 
                  exportCsv: (columns, data) => {
                    downloadExcel("FiscalizacionAduanas")
                  },                                
                }}   
           
              />
      </GridItem>
    </GridContainer>
  </React.Fragment>
)
}
