import React from 'react';
import {Chip, Grid, InputLabel, MenuItem, Select,  FormControl, FormHelperText, Paper } from "@material-ui/core"
import TextField from '@mui/material/TextField';

import {DatePicker, DateTimePicker} from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import esLocale from 'date-fns/locale/es';
import { makeStyles } from "@material-ui/core/styles";
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

export interface Props {
  OperadorList: any,
  DepositoList: any,  
  opPropio: string,
  codOpPropio: string,
  rutOpPropio: string,
  traspasonew: any,
  numeroPropio: string,
  errorNumeroTATC: boolean,
  errorDepositoTatc: boolean,
  updateTraspasoNew: Function, 
  setErrorDepositoTatc: Function , 

 
}
const useStylesTypo = makeStyles(styles);
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })


export default function TraspasoTATCNuevo (props: Props) {

  const [nameClassDepositoTatc, setNameClassDepositoTatc] = React.useState('');

  const classes = useStyles()  
  const classesTypo = useStylesTypo()



  React.useEffect(() => {
    setNameClassDepositoTatc(props.errorDepositoTatc == true ? classes.textFieldRed: classes.textField )
  }, [props.errorDepositoTatc])    

    return (
      <React.Fragment> 

        <GridContainer  style={{marginTop: -20}}>
          <GridItem xs={12} sm={6} md={4} > 
              <FormControl  id="ctl_OpPropio" 
                style={{width:'100%'}}           
              >
              {/* <InputLabel id="demo-simple-select-required-label">Operador</InputLabel> */}
              <TextField
                label = "Operador"               
                id="demo-simple-select-required"
                select
                inputProps={{ readOnly: true, style: { fontSize: 14 } }}
                value={props.opPropio}
                defaultValue="0"
                fullWidth={true}
                style={{marginTop: 10}} 
                size = 'small'
                variant="outlined"                   
              >              
              <MenuItem value="0" style= {{ fontSize: "14px" }}>                  
                {<div className={classesTypo.mutedText}>Seleccione Operador</div>} 
              </MenuItem>
                {props.OperadorList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                    
              </TextField>                                
              </FormControl>
          </GridItem> 

          <GridItem xs={12} sm={6} md={4} > 
              <TextField id="ctl_CodOpPropio" style={{ marginTop: 10}}
                id="text"
                label="Código Operador"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.codOpPropio}
              />             
          </GridItem>  

          <GridItem xs={12} sm={6} md={4} > 
              <TextField id="ctl_RutOpPropio" style={{ marginTop: 10}}
                id="text"
                label="RUT"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.rutOpPropio}
              />             
          </GridItem>  

          <GridItem xs={12} sm={6} md={4} > 
              <TextField id="ctl_TATC_Numero" style={{ marginTop: 10}}
                  label="TATC"
                  type="text"   
           
                  variant="outlined"
                  fullWidth
                  size = 'small'                
                  inputProps={{ readOnly: true,  style: { fontSize: 14 } }}
                  value={props.numeroPropio}
              /> 
          </GridItem>

          <GridItem xs={12} sm={6} md={4} > 
              <FormControl id="ctl_TATC_Deposito" 
                style={{width:'100%',marginTop: 10}}    
                required 
                className={nameClassDepositoTatc}
                >

                <TextField
                  select
                  label = 'Depósito'
                  required 
                  value={props.traspasonew.tatc_deposito}
                  defaultValue="0"
                  fullWidth={true}
                  inputProps={{ style: { fontSize: 14 } }}            
                  onChange={(event) => {
                    props.updateTraspasoNew({...props.traspasonew, tatc_deposito: event.target.value})
                    props.setErrorDepositoTatc((event.target.value === "0"))
                  }}                  
                  variant="outlined"                 
                  size = 'small'                                        
                >                
                <MenuItem value="0" style= {{ fontSize: "14px" }}>                  
                      {<div className={classesTypo.mutedText}>Seleccione Depósito</div>} 
                </MenuItem>
                  {props.DepositoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                    
                </TextField>                      
              </FormControl>
          </GridItem>


        </GridContainer>
    </React.Fragment> 
    )
}


