
import * as api from '../../api';
import * as React from 'react';



import Modal from '@material-ui/core/Modal';
import {prorrogaModalBody,  cerrarModalBody, pdfModalBody } from './modalBody';
import useStyles from '../Common/styles';
import { host, client } from '../../conf';
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";



import TituloaPDF from './TituloaPDF';
import DialogScroll from '../DialogScroll';

import MaterialTable from 'material-table';
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'



import pdfMake from 'pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
import Paper from '@material-ui/core/Paper';

import CalendarTodayOutlinedIcon from '@mui/icons-material/CalendarTodayOutlined';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import PrintOutlinedIcon from '@mui/icons-material/PrintOutlined';

import Notification from '../Notification';


export interface Props {
  expresionFiltroTitulos: string;  
  buscando: boolean;  
}

pdfMake.vfs = pdfFonts.pdfMake.vfs;

export default function ResultadosFisControl(props: any) {

  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});

  const [TTTCList, updateTTTCList] = React.useState([]);
  const [showModalProrroga, updateshowModalProrroga] = React.useState(false);
  const [showModalCerrar, updateshowModalCerrar] = React.useState(false);
  const [showModalPDF, updateshowModalPDF] = React.useState(false);  
  const [TTTC, updateTTTCDetail] = React.useState({id:"", 
  numero:"", tipooper_name: "", contenedor_numero: "", fecha_vigencia: "", fecha_vigencia_str: "" , fecha_vigencia_prorroga: "", datos_prorroga: ""})

  const [TTTCToClose, updateTTTCToClose] = React.useState(0);
  const [TTTCToProrroga, updateTTTCToProrroga] = React.useState(0);
  const [TTTCToPDF, updateTTTCToPDF] = React.useState(0);  
  const closeModalProrroga = () => {updateshowModalProrroga(false)};
  const closeModalCerrar = () => {updateshowModalCerrar(false)};
  const closeModalPDF = () => {updateshowModalPDF(false)};  
  const [errors, updateErrors] = React.useState([]) 
  const [MotivoCierreList, updateMotivoCierreList] = React.useState([]); 
  const [DetalleAnulacionList, updateDetalleAnulacionList] = React.useState([]);
  const [TTTCC, updateTTTCCDetail] = React.useState({id:"", numero:"", tipooper_name: "", contenedor_numero: "", motivocierre: "", detalleanulacion: "0" , nodi: "" , filedi: " "})   
  const [TTTCP, updateTTTCPDetail] = React.useState({id:"", numero:"", operador_codigo: "", contenedor_numero: "", aduana_codigo: "", operador_nombre: "" , operador_resolucion: "", operador_fecha_resolucion: "", operador_fecha_vencim_suspension: "", tipocontenedor_tipo: "" , contenedor_tamano: "", fecha_emision_str: "",
  fecha_ingreso_pais_str: "", usuario_fullname: "", motivocierre_nacionalizacion: "", motivocierre_abandono: "", motivocierre_traspaso: "", motivocierre_salida: "", motivocierre_anulacion: "", contenedor_grupo_dry: "", contenedor_grupo_reefer: "",contenedor_grupo_otro: "", tamano_grupo_20: "", tamano_grupo_40: "", tipooper_name: ""}) 

  const [maxWidth, updateMaxWidth] = React.useState('md'); 
  const [tituloDialogoCerrar, updateTituloDialogoCerrar] = React.useState('Cerrar Título Temporal de Contenedor');
  const [showBotonPDF, updateshowBotonPDF] = React.useState(false);  
  const [tituloDialogoProrrogar, updateTituloDialogoProrrogar] = React.useState('Prorrogar Título Temporal de Contenedor');

  const [esAnulacion, updateesAnulacion] = React.useState(false); 
  const [esNacional, updateesNacional] = React.useState(false); 
  const [errorDetalleAnulac, setErrorDetalleAnulac] = React.useState(true) 
  const [ficheroNombre, setFicheroNombre] = React.useState(" Archivo no Seleccionado") 
  const [loadData, setLoadData] = React.useState(false);
  const downloadExcel=(filename)=>{

    try {

      
        const newData= TTTCList.map((row)=>{
          return {
            TATC_TSTC: row.numero,
            Tipo: row.tipooper_name,
            Contenedor: row.contenedor_numero,
            Emisión: row.fecha_emision_str,
            Vigencia: row.fecha_vigencia_str,
            Prórroga: row.prorroga,        
            Estado: row.titulo_estado      
          }
        })

        const workSheet=XLSX.utils.json_to_sheet(newData)

        let header = ['TATC_TSTC', 'Tipo', 'Contenedor', 'Emisión', 'Vigencia', 'Prórroga', 'Estado']
        const jsonKeys = header || Object.keys(newData[0])

        const objectMaxLength = []
        jsonKeys.forEach((key) => {
          objectMaxLength.push(
            pixelWidth(key, {
              size: 3,
            })
          )
        })

        newData.forEach((data, i) => {
          const value = newData[i]
          jsonKeys.forEach((key, j) => {
            const l = value[jsonKeys[j]]
              ? pixelWidth(value[jsonKeys[j]], {
                  size: 3,
                })
              : 0
            objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

          })
        })



        const wscols = objectMaxLength.map((w: number) => ({width: w}))
        workSheet["!cols"] = wscols

        const workBook=XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(workBook,workSheet,filename)
        //Buffer
        let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
        //Binary string
        XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
        //Download
        XLSX.writeFile(workBook, filename+".xlsx")

        setNotify({isOpen: true, message: 'Exportación a Excel realizada con éxito...', errors: [], type: 'success'});
    
   }
    catch(e) 
      { 
        setNotify({isOpen: true, message: 'Error exportando a Excel...', errors: [], type: 'error'});
      };

  }  
 
  const updateAllMotivoCierre = () => {
    api.getData(host + '/api/fiscalizacion_controlmotivocierre/listdisponibles/').then(x => {

      if(x.status === 200){
        updateMotivoCierreList(x.data)
      }
   
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const updateAllDetalleAsignacion = () => {
    api.getData(host + '/api/fiscalizacion_controldetalleanulacion/?estado=1').then(x => {
      if(x.status === 200){
        updateDetalleAnulacionList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos... ', errors: [], type: 'error'});
      });
  }

  const prorrogarTTTC = () => {
      api.putData(host +'/api/tatc_tstctitulo/'+ TTTCToProrroga.toString() + "/prorrogar/" )
      .then(data => {
        let messages: any = [];
        if(data.status !== 200){
            Object.keys(data.data).map(key => messages.push(key + ": " + data.data[key]));
            updateErrors(messages);
            setNotify({isOpen: true, message: 'Error: No se pudo realizar prórroga por error en el Servidor', errors: messages, type: 'error'});             
                      
        } else {
            setNotify({isOpen: true, message: 'Prórroga realizada con éxito', errors: [], type: 'success'});                        
            updateErrors(messages);            
            updateAllTTTC();
            updateshowModalProrroga(false);
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
  }

  const cerrarTTTC = () => {
    api.putData(host +'/api/tatc_tstctitulo/'+ TTTCToClose.toString()  + "/cerrar/",  TTTCC)
    .then(data => {
      let messages: any = [];
      if(data.status !== 200){
          Object.keys(data.data).map(key => messages.push(key + ": " + data.data[key]));
          updateErrors(messages);
          setNotify({isOpen: true, message: 'Error al cerrar Título:', errors: messages, type: 'error'});            
       
        } else {
          setNotify({isOpen: true, message: 'Título Cerrado con éxito', errors: [], type: 'success'});                          
          updateErrors(messages);            
          updateAllTTTC();
          updateshowModalCerrar(false);
        }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
      });
}

  const pdfTTTC = () => {
        updateshowModalPDF(false);
}

  const updateAllTTTC = () => {
    api.getData(host + '/api/tatc_tstctitulo/?'+props.expresionFiltroTitulos).then(x => {
      if(x.status === 200){
        updateTTTCList(x.data)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
      });
  }

  const prorrogar = (rowData) => {
      updateshowModalProrroga(true);
      updateTTTCToProrroga(rowData.id);
      updateTTTCDetail({id:rowData.id, numero:rowData.numero, tipooper_name: rowData.tipooper_name, contenedor_numero: rowData.contenedor_numero, fecha_vigencia: rowData.fecha_vigencia, fecha_vigencia_str: rowData.fecha_vigencia_str, fecha_vigencia_prorroga: rowData.fecha_vigencia_prorroga, datos_prorroga: rowData.datos_prorroga}); 
  };


  const cerrar = (rowData) => {
      updateTTTCToClose(rowData.id);
      updateshowModalCerrar(true);
      setFicheroNombre(" Archivo no Seleccionado")
      updateTTTCCDetail({id:rowData.id, numero:rowData.numero,tipooper_name: rowData.tipooper_name, contenedor_numero: rowData.contenedor_numero, motivocierre: rowData.motivocierre, detalleanulacion: rowData.detalleanulacion === null ? "0" : rowData.detalleanulacion , nodi: rowData.nodi, filedi: rowData.filedi});
   };

  const exportarPDF = (rowData) => {

    updateTTTCToPDF(rowData.id);
    updateshowModalPDF(true);
    updateTTTCPDetail({id:rowData.id, operador_codigo:rowData.operador_codigo,aduana_codigo: rowData.aduana_codigo, numero: rowData.numero, operador_nombre:rowData.operador_nombre,  operador_resolucion: rowData.operador_resolucion, operador_fecha_resolucion: rowData.operador_fecha_resolucion, operador_fecha_vencim_suspension: rowData.operador_fecha_vencim_suspension, contenedor_numero: rowData.contenedor_numero, tipocontenedor_tipo: rowData.tipocontenedor_tipo, contenedor_tamano: rowData.contenedor_tamano, fecha_emision_str: rowData.fecha_emision_str, fecha_ingreso_pais_str: rowData.fecha_ingreso_pais_str, usuario_fullname: rowData.usuario_fullname, motivocierre_nacionalizacion: rowData.motivocierre_nacionalizacion, motivocierre_abandono: rowData.motivocierre_abandono, motivocierre_traspaso: rowData.motivocierre_traspaso, motivocierre_salida: rowData.motivocierre_salida, motivocierre_anulacion: rowData.motivocierre_anulacion, contenedor_grupo_dry: rowData.contenedor_grupo_dry, contenedor_grupo_reefer: rowData.contenedor_grupo_reefer,contenedor_grupo_otro: rowData.contenedor_grupo_otro, tamano_grupo_20: rowData.tamano_grupo_20, tamano_grupo_40: rowData.tamano_grupo_40, tipooper_name: rowData.tipooper_name });
  }

  const box_style = {background: "white"}

  React.useEffect(() => {
    if (TTTCC.motivocierre != "0") 
    {
      let obj = MotivoCierreList.find(o => o.id === TTTCC.motivocierre);
      if (obj) {
        updateesAnulacion(obj.nombre.substring(0,5) === "ANULA")
        updateesNacional(obj.nombre.substring(0,5) ==="NACIO")        
      }
      else {
        updateesAnulacion(false)
        updateesNacional(false)     
      }       
    }
    else {
      updateesAnulacion(false)
      updateesNacional(false)     
    } 
      
  }, [TTTCC.motivocierre])

  React.useEffect(() => {

      updateesAnulacion(false)
      updateesNacional(false)  
      updateTTTCCDetail({...TTTCC, motivocierre: '0'})         
      
  }, [TTTCC.id])

  React.useEffect(() => {
    updateAllDetalleAsignacion(); 
    updateAllMotivoCierre(); 
     
  }, [])

  React.useEffect(() => {
    if (props.buscando)
    {
      setLoadData(true) 
      api.getData(host + '/api/tatc_tstctitulo/?'+props.expresionFiltroTitulos).then(x => {
        if(x.status === 200){
          updateTTTCList(x.data)
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
        }).finally(() => {setLoadData(false)})
    }
    else {updateTTTCList([])}
  }, [props.expresionFiltroTitulos, props.buscando])

  return (
      <React.Fragment>
        <GridContainer  style={{marginTop: 10}}>
        <GridItem  md={12} >              
              <MaterialTable
                style={{width:'100%'}}
                data = {TTTCList}
                isLoading ={loadData}
                localization={{
                  pagination: {
                      labelDisplayedRows: '{from}-{to} de {count}',
                      labelRowsSelect: 'filas'
                  },
                  toolbar: {
                      nRowsSelected: '{0} filas(s) seleccionadas',
                      searchTooltip: 'Buscar',
                      searchPlaceholder: 'Buscar',
                      exportTitle: 'Exportar',
                      exportAriaLabel: 'Exportar',
                      exportCSVName: 'EXCEL',
                      exportPDFName: 'PDF'
                  },
                  header: {
                      actions: 'Acciones'
                  },
                  grouping: {
                    groupedBy: 'Agrupar por',
                    placeholder: 'Arraste la columna para agrupar'
                  },
                  body: {
                      emptyDataSourceMessage: 'No existen filas para visualizar',
                      filterRow: {
                          filterTooltip: 'Filtrar'
                      }
                  }
                }}              
                columns={[
                    { field: 'numero', title: 'Número TATC/TSTC' },
                    { field: 'tipooper_name', title: 'Tipo' },
                    { field: 'contenedor_numero', title: 'Contenedor' },
                    { field: 'fecha_emision', title: 'Emisión' , type: 'date',   dateSetting: {
                      format: 'dd/MM/yyyy'
                    }}, 
                    { field: 'fecha_vigencia', title: 'Vigencia', type: 'date',
                    dateSetting: {
                      format: 'dd/MM/yyyy'
                    } }, 
                    { field: 'prorroga', title: 'Prórroga' }, 
                    { field: 'titulo_estado', title: 'Estado',  lookup: { 'Abierto': 'Abierto', 'Cerrado': 'Cerrado' } },
                    { field: 'operador_resolucion', title: 'operador_resolucion', hidden: true },
                    { field: 'operador_fecha_resolucion', title: 'operador_fecha_resolucion', hidden: true },
                    { field: 'operador_fecha_vencim_suspension', title: 'operador_fecha_vencim_suspension', hidden: true }, 
                    { field: 'motivocierre_nacionalizacion', title: 'motivocierre_nacionalizacion', hidden: true },
                    { field: 'motivocierre_abandono', title: 'motivocierre_abandono', hidden: true },
                    { field: 'motivocierre_traspaso', title: 'motivocierre_traspaso', hidden: true },
                    { field: 'motivocierre_salida', title: 'motivocierre_salida', hidden: true },
                    { field: 'motivocierre_anulacion', title: 'motivocierre_anulacion', hidden: true },
                    { field: 'contenedor_grupo_dry', title: 'contenedor_grupo_dry', hidden: true },                                                                                         { field: 'contenedor_grupo_reefer', title: 'contenedor_grupo_reefer', hidden: true },
                    { field: 'contenedor_grupo_otro', title: 'contenedor_grupo_otro', hidden: true },
                    { field: 'tamano_grupo_20', title: 'tamano_grupo_20', hidden: true },
                    { field: 'tamano_grupo_40', title: 'tamano_grupo_40', hidden: true }                          
                ]}  
                options={{
                  grouping: true, 
                  actionsColumnIndex: -1,
                  showTitle: false,        
                  exportAllData: true,                    
                  exportButton: true,
                  exportFileName: 'ControlPlazos',
                  exportCsv: (columns, data) => {
                    downloadExcel("ControlPlazos")
                  },                    
                  headerStyle: {
                    backgroundColor: '#8e24aa',//'#01579b',
                    color: '#FFF'
                  }
      
                }}   
                actions={[
                  rowData => ({
                    icon: 'alarmon',
                    // iconProps: { style: { color: "green" } },                    
                    tooltip: 'Realizar Prórroga',
                    onClick: (event, rowData) => prorrogar(rowData),
                    disabled: rowData.estado === "C" || rowData.prorroga > "1"
                  }),
                  rowData => ({
                    icon: 'lock' ,
                    // iconProps: { style: {  color: "red" } },                    
                    tooltip: 'Cerrar TATC',
                    onClick: (event, rowData) =>  cerrar(rowData),
                    disabled: rowData.estado === "C"
                  }),
                  {
                    icon: 'print',
                    // iconProps: { style: {  color: "blue" } },                    
                    tooltip: 'Informe Aduana en PDF',
                    onClick: (event, rowData) => exportarPDF(rowData), 
                  },                  
                ]}                
              />
            {/* </div> */}

          {/* </Paper> */}
          <Notification
                notify = {notify}
                setNotify = {setNotify}
              > 
          </Notification>  

          <DialogScroll
            showDialog={showModalProrroga}
            titleBar={tituloDialogoProrrogar}
            maxWidth={maxWidth}
            closeFunction={closeModalProrroga}
            showBotonPDF={showBotonPDF}  
          >
            {prorrogaModalBody(TTTC, prorrogarTTTC, updateTTTCDetail, errors)}  
            <Notification
              notify = {notify}
              setNotify = {setNotify}
            > 
            </Notification>                          
          </DialogScroll> 

          <DialogScroll
            showDialog={showModalCerrar}
            titleBar={tituloDialogoCerrar}
            maxWidth={maxWidth}
            closeFunction={closeModalCerrar}
            showBotonPDF={showBotonPDF}  
          >
            {cerrarModalBody(TTTCC, cerrarTTTC, updateTTTCCDetail, errors, MotivoCierreList, DetalleAnulacionList, esAnulacion, esNacional, ficheroNombre, setFicheroNombre)}              
          </DialogScroll>           

          <TituloaPDF
            titulo={TTTCP}
            showDialog={showModalPDF}
            closeFunction={closeModalPDF}            
          >
          <div>
            este   {TTTCP.id}
          </div>
          </TituloaPDF>
        </GridItem>
        </GridContainer>
      </React.Fragment>
  );
}
