/*eslint-disable*/
import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import { NavLink, useLocation } from "react-router-dom";
import { AnimatePresence, motion } from 'framer-motion';
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Icon from "@material-ui/core/Icon";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
// core components
import AdminNavbarLinks from "../../components/Navbars/AdminNavbarLinks.js";
//import RTLNavbarLinks from "../../components/Navbars/RTLNavbarLinks.js";
import Button from "../../components/CustomButtonsMdr/Button.js";
import styles from "../../assets/jss/material-dashboard-react/components/sidebarStyle.js";

const useStyles = makeStyles(styles);
export default function Sidebar(props) {
  const classes = useStyles();
  let location = useLocation();
  const { color, logo, image, logoText, routes } = props;
  const [collapsed, setCollapsed] = React.useState(false);
 
  // verifies if routeName is the one active (in browser input)
  function activeRoute(routeName) {
    return location.pathname === routeName;
  }

  var links = routes.map((prop, key) => {
        var activePro = " ";
        var listItemClasses;
        if (prop.path === "/") {
          activePro = classes.activePro + " ";
          listItemClasses = classNames({
            [" " + classes[color]]: true,
          });
        } else {
          listItemClasses = classNames({
            [" " + classes[color]]: activeRoute(prop.layout + prop.path),
          });
        }
        const whiteFontClasses = classNames({
          [" " + classes.whiteFont]: activeRoute(prop.layout + prop.path),
        });      
        
        const [subItem, setSubItem] = React.useState(false);
        const ShowSubItem = () => setSubItem(!subItem);
        
        const hasSubmenus = !!prop.SubRoutes.length;
        const sublinks = prop.SubRoutes.map((subMenuItem, subMenuItemIndex) => {          
          var activePro = " ";
          var listItemClasses;
          if (subMenuItem.path === "/") {
            activePro = classes.activePro + " ";
            listItemClasses = classNames({
              [" " + classes[color]]: true,
            });
          } else {
            listItemClasses = classNames({
              [" " + classes[color]]: activeRoute(subMenuItem.layout + subMenuItem.path),
            });
          }
          const whiteFontClasses = classNames({
            [" " + classes.whiteFont]: activeRoute(subMenuItem.layout + subMenuItem.path),
          }); 

          return (
            <div >                     
            <NavLink
              to={subMenuItem.layout+ subMenuItem.path}
              //onClick={hasSubmenus && ShowSubItem}
              className={ classes.item}
              activeClassName="active"            
              key={subMenuItemIndex}
            >
              <ListItem button className={classes.subitemLink + listItemClasses}>
                {typeof subMenuItem.icon === "string" ? (
                  <Icon
                    className={classNames(classes.subitemIcon, whiteFontClasses, {
                      [classes.itemIconRTL]: props.rtlActive,
                    })}
                  >
                    {subMenuItem.icon}
                  </Icon>
                ) : (
                  <subMenuItem.icon
                    className={classNames(classes.subitemIcon, whiteFontClasses, {
                      [classes.itemIconRTL]: props.rtlActive,
                    })}
                  />
                )}              
                <ListItemText
                  primary={subMenuItem.name}
                  className={classNames(classes.subitemText, whiteFontClasses, {
                    [classes.itemTextRTL]: props.rtlActive,
                  })}
                  disableTypography={true}                             
                />                                                             
              </ListItem>              
            </NavLink>
              
          </div>  
          )
        })
        
        return (
          <div key = {key}>                     
          <NavLink
            to={prop.layout + prop.path}
            onClick={prop.SubRoutes && ShowSubItem}
            className={ classes.item}
            activeClassName="active"            
          >
            <ListItem button className={classes.itemLink + listItemClasses}>
              {typeof prop.icon === "string" ? (
                <Icon
                  className={classNames(classes.itemIcon, whiteFontClasses, {
                    [classes.itemIconRTL]: props.rtlActive,
                  })}
                >
                  {prop.icon}
                </Icon>
              ) : (
                <prop.icon
                  className={classNames(classes.itemIcon, whiteFontClasses, {
                    [classes.itemIconRTL]: props.rtlActive,
                  })}
                />
              )}              
              <ListItemText
                primary={prop.name}
                className={classNames(classes.itemText, whiteFontClasses, {
                  [classes.itemTextRTL]: props.rtlActive,
                })}
                disableTypography={true}                             
              />
              {hasSubmenus && subItem ?
               <ExpandLessIcon className={classes.whiteFont}/>:
               hasSubmenus ?
               <ExpandMoreIcon className={classes.whiteFont} />:null
              }                                                
            </ListItem>            
          </NavLink>
          <AnimatePresence>
            {hasSubmenus && subItem &&(
            
            <div>
              
              <motion.nav 
              initial={{ opacity: 0, y: -25 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.50 }}
              exit={{ opacity: 0, x: -30 }}
            >
              {sublinks}
            </motion.nav>
              
            </div>
             
            )}            
            </AnimatePresence>   
          </div>          
        );
      }     
  );  

  var brand = (
    <div className={classes.logo}>
      <a
        // href="https://www.creative-tim.com?ref=mdr-sidebar"
        className={classNames(classes.logoLink, {
          [classes.logoLinkRTL]: props.rtlActive,
        })}
        target="_blank"
      >
        <div className={classes.logoImage}>
          <img src={logo} alt="logo" className={classes.img} />
        </div>
        {logoText}
      </a>
    </div>
  );
  
  return (
    <div>
       {/* Manu Movil Mode */} 
      <Hidden mdUp implementation="css"> 
        <Drawer
          variant="temporary"
          anchor={props.rtlActive ? "left" : "right"}
          open={props.open}
          classes={{
            paper: classNames(classes.drawerPaper, {
              [classes.drawerPaperRTL]: props.rtlActive,
            }),
          }}
          onClose={props.handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
        >
          {brand}          
          <div className={classes.sidebarWrapper}>                    
            <List className={classes.list} >
              <div style={{marginLeft: -15}}>
                <AdminNavbarLinks logOut = {props.logOut} userCurrent = {props.userCurrent} />  
              </div>            
              {links}
            </List>            
          </div>
          {image !== undefined ? (
            <div
              className={classes.background}
              style={{ backgroundImage: "url(" + image + ")" }}
            />
          ) : null}
        </Drawer>
      </Hidden>
      {/* Menu Modo Escritorio */}
      <Hidden smDown implementation="css">
        <Drawer
          anchor={props.rtlActive ? "right" : "left"}
          variant="permanent"
          open
          classes={{
            paper: classNames(classes.drawerPaper, {
              [classes.drawerPaperRTL]: props.rtlActive,
            }),
          }}
        >
          {brand}          
          <div className={classes.sidebarWrapper}>
          <List className={classes.list} >
            {links}
          </List>
          </div>
          {image !== undefined ? (
            <div
              className={classes.background}
              style={{ backgroundImage: "url(" + image + ")" }}
            />
          ) : null}
        </Drawer>
      </Hidden>
    </div>
  );
}
Sidebar.propTypes = {
  rtlActive: PropTypes.bool,
  handleDrawerToggle: PropTypes.func,
  bgColor: PropTypes.oneOf(["purple", "blue", "green", "orange", "red"]),
  logo: PropTypes.string,
  image: PropTypes.string,
  logoText: PropTypes.string,
  routes: PropTypes.arrayOf(PropTypes.object),
  open: PropTypes.bool,
};
