import React, { useRef } from 'react';

import {Dialog, DialogTitle, DialogActions, DialogContentText, DialogContent, Paper} from "@material-ui/core"
import {Box, Grid, InputLabel, List, ListItem, Divider, FormControl} from "@material-ui/core"
import {Select, MenuItem,  FormGroup, Button} from '@material-ui/core';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { makeStyles } from "@material-ui/core/styles";
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import SearchOffRoundedIcon from '@mui/icons-material/SearchOffRounded';
import CustomButtons from '../../components/CustomButtons/Button.js';
import * as myInterfaces from '../TATC-TSTC/interfaces';

import useStyles from '../Common/styles';
import * as api from '../../api';
import { host, client } from '../../conf';

import DateRangePicker from '@mui/lab/DateRangePicker';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import esLocale from 'date-fns/locale/es';
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Notification from '../Notification';
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

export interface Props {
    setexpresionFiltroTitulos: Function; 
    setBuscando: Function;           
}

const useStylesType = makeStyles(styles)
const useTextFieldStyles = makeStyles({	
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

const CustomPaper = (props) => {
    return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
};

function isDigitCode(n) {
    //return(n >= charCodeZero && n <= charCodeNine);
    return(n >= "0" && n <= "9")
 }  

const FiltroBusquedaFisAduana = (props: Props) => {

    const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});

    //const classes = useStyles();
    const classesTypo = useStylesType(styles)
    const classesTextField = useTextFieldStyles()

    const [tipoOperList,    setTipoOperList]    = React.useState([]);
    const [contenedorList,  setContenedorList]  = React.useState([]);
    const [aduanaList,      setAduanaList]      = React.useState([]);
    const [tituloList,      setTituloList]      = React.useState([]);
    const [numero,          setNumero]          = React.useState('');
    const [tipoOperId,      setTipoOperId]      = React.useState(0);    
    const [aduanaId,        setAduanaId]        = React.useState(0);           
    const [desdeFE,         setDesdeFE]         = React.useState('');
    const [hastaFE,         setHastaFE]         = React.useState('');
    const [estado,          setEstado]          = React.useState('0');
    const [traspaso,        setTraspaso]        = React.useState('0');    
    const [numero2,         setNumero2]         = React.useState(''); 

    const getTipoOperList = () => {
        api.getData(host + '/api/fiscalizacion_controltipooper/').then(x => {
            if (x.status === 200) {
                setTipoOperList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getContenedorList = () => {
        api.getData(host + '/api/inventarioscontenedor/').then(x => {
            if (x.status === 200) {
                setContenedorList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getAduanasList = () => {
        api.getData(host + '/api/fiscalizacion_controladuana/').then(x => {
            if (x.status === 200) {
                setAduanaList(x.data);
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }

    const getTituloList = () => {
        api.getData(host + '/api/tatc_tstctitulo/').then(x => {
            if (x.status === 200) {
                setTituloList(x.data);
     
            }
        }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });
    }    

    const defaultProps = {
        options: tituloList,
        getOptionLabel: (option) => option.numero,
      };   
      
    const defaultProps2 = {
        options: contenedorList,
        getOptionLabel: (option) => option.numero,
    };            

     const generarExpresionFiltros = () => {

        var condition1 = (tipoOperId>0) ? 'tipooper__id='+ tipoOperId.toString() : '';
        var condition3 = (aduanaId>0) ? 'aduana__id='+ aduanaId.toString() : '';
        var condition4 = (numero2!== '') ? 'contenedor__numero='+ numero2.replace("-", "") : '';        
        var condition5 = (desdeFE !== '') ? 'min_fecha_emision='+ desdeFE.toString().replace('-','/') : '';
        var condition6 = (hastaFE !== '') ? 'max_fecha_emision='+ hastaFE.toString().replace('-','/') : '';
        var condition7 = (estado !== '0') ? 'estado='+ estado : '';                              
        var condition8 = (traspaso !== '0') ? 'sintraspaso=' + ((traspaso === 'N') ? 'true' : 'false')  : ''; 
        var condition9 = (numero!== '') ? 'numero='+ numero : '';                        
               
        var expresionFiltroTitulos = '';
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition1.length>0 ?'&':'') + condition1;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition3.length>0?'&':'') + condition3;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition4.length>0?'&':'') + condition4;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition5.length>0?'&':'') + condition5;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition6.length>0?'&':'') + condition6;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition7.length>0?'&':'') + condition7;
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition8.length>0?'&':'') + condition8; 
        expresionFiltroTitulos = expresionFiltroTitulos + (expresionFiltroTitulos.length>0 && condition9.length>0?'&':'') + condition9; 
        props.setexpresionFiltroTitulos(expresionFiltroTitulos);
        props.setBuscando(true)  

    }       

    const [value, setValue] = React.useState([null, null]);    

    const setearExpresionFiltros = () => {

        props.setexpresionFiltroTitulos('')
       
        setValue([null,null])
        setTipoOperId(0)
        setNumero('')   
        setNumero2('')             
        setAduanaId(0)  
        setDesdeFE('')
        setHastaFE('')
        setEstado('0')
        setTraspaso('0')


        const ele = autoC.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
        if (ele) ele.click();

        const ele2 = autoC2.current.getElementsByClassName('MuiAutocomplete-clearIndicator')[0];
        if (ele2) ele2.click();          

    } 

    const autoC = useRef(null);  
    const autoC2 = useRef(null);         

    React.useEffect(() => {
        getTipoOperList();
        getContenedorList();
        getAduanasList();
        getTituloList();              
    
    }, [])

    return (
    <React.Fragment>    
        <GridContainer  style={{marginTop: 10}}>        
        <GridItem xs={12} sm={6} md={4} >                                
        <FormControl style={{width:'100%',marginTop: 10}} 
        size = 'small'>
        <Autocomplete
            {...defaultProps}
            ref={autoC}
            openOnFocus
            noOptionsText={'No existen opciones....'}
            id="controllable-states-demo"
            inputValue={numero}
            PaperComponent={CustomPaper}
            onInputChange={(event, newInputValue) => {
                setNumero(newInputValue);
            }}                   
            renderInput={(params) => 
            <TextField {...params}                                       
            classes={{ root: classesTextField.customTextField }} 
            onKeyPress={(event) => {
                if (!isDigitCode(event?.key)) {
                  event.preventDefault();
                }
              }}             
            label = 'Número TATC/TSTC'
            size = 'small' 
            variant="outlined"/>                   
            }
        />
        </FormControl>   
        </GridItem> 

         <GridItem xs={12} sm={6} md={4} >                                 
         <FormControl style={{width:'100%'}}>               
            <TextField                    
                id="demo-simple-select"
                label="Tipo"
                select
                value={tipoOperId}
                onChange={(event: any) => {setTipoOperId(event.target.value)}}
                defaultValue="0"
                displayEmpty={false}                   
                variant="outlined"                             
                style={{marginTop: 10}} 
                size = 'small'                   
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}                  
                >
                <MenuItem value="0" style= {{ fontSize: "14px" }}>
                    {<div className={classesTypo.mutedText}>Seleccione Tipo</div>} 
                </MenuItem>
                {tipoOperList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}
            </TextField>
        </FormControl>
        </GridItem>
        <GridItem xs={12} sm={6} md={4} >  
        <FormControl  style={{width:'100%'}}>               
                <TextField                   
                    id="demo-simple-select-required"                    
                    select
                    value={estado}
                    onChange={(event: any) => {setEstado(event.target.value)}}
                    label="Estado"
                    variant="outlined"                             
                    style={{marginTop: 10}} 
                    size = 'small'                                        
                    inputProps={{
                      style: { fontSize: 14 },                                     
                    }} 
                    >
                    <MenuItem value="0" style= {{ fontSize: "14px" }}>
                        {<div className={classesTypo.mutedText}>Seleccione Estado</div>} 
                    </MenuItem>
                    <MenuItem value={'A'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Abierto</div></MenuItem>
                    <MenuItem value={'C'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Cerrado</div></MenuItem>
                </TextField>
            </FormControl> 
        </GridItem>
        <GridItem xs={12} sm={6} md={4} > 
            <FormControl style={{width:'100%'}}>
                {/* <InputLabel id="demo-simple-select-label">Traspaso</InputLabel> */}
                <TextField
                    select
                    id="demo-simple-select-required"
                    value={traspaso}
                    onChange={(event: any) => {setTraspaso(event.target.value)}}
                    label="Traspaso"
                    variant="outlined"                             
                    style={{marginTop: 10}} 
                    size = 'small'                                        
                    inputProps={{
                      style: { fontSize: 14 },                                     
                    }}
                >
                    <MenuItem value="0" style= {{ fontSize: "14px" }}>
                        {<div className={classesTypo.mutedText}>Seleccione Traspaso</div>} 
                    </MenuItem>                    
                    <MenuItem value={'S'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Si</div></MenuItem>
                    <MenuItem value={'N'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>No</div></MenuItem>
                </TextField>
            </FormControl>
        </GridItem>
        <GridItem xs={12} sm={6} md={4} > 
            <FormControl style={{width:'100%',marginTop: 10}}>
                <Autocomplete
                    {...defaultProps2}
                    ref={autoC2}
                    openOnFocus
                    noOptionsText={'No existen opciones....'}
                    id="controllable-states-demo"
                    PaperComponent={CustomPaper}
                    inputValue={numero2}
                    onInputChange={(event, newInputValue) => {
                        setNumero2(newInputValue);
                    }}                   
                    renderInput={(params) => 
                    <TextField {...params} 
                    label = 'Contenedor' 
                    classes={{ root: classesTextField.customTextField }} 
                    size = 'small'
                    variant="outlined"/>}
                />
            </FormControl> 
        </GridItem>
        <GridItem xs={12} sm={6} md={4} > 
            <FormControl style={{width:'100%'}}>
                {/* <InputLabel id="demo-simple-select-label">Aduana</InputLabel> */}
                <TextField                
                    id="demo-simple-select-Aduana"
                    select
                    label="Aduana"
                    value={aduanaId}
                    onChange={(event: any) => {setAduanaId(event.target.value)}}
                    defaultValue="0"
                    displayEmpty={false}
                    fullWidth={true}
                    variant="outlined"                             
                    style={{marginTop: 10}} 
                    size = 'small'                                        
                    inputProps={{
                    style: { fontSize: 14 },                                     
                    }}                   
                    >                
                    <MenuItem value="0" style= {{ fontSize: "14px" }}>
                        {<div className={classesTypo.mutedText}>Seleccione Aduana</div>} 
                    </MenuItem>
                    {aduanaList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}
                </TextField>
            </FormControl>  
        </GridItem>
        <GridItem xs={12} sm={6} md={4} > 
            <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
            <FormControl style={{width:'100%',marginTop: 10}}>                                
                    <DateRangePicker 
                        startText="Fecha Emisión Desde"
                        endText="Hasta"
                        size = 'small' 
                        value={value}
                        onChange={(newValue) => {
                            setValue(newValue);
                            setDesdeFE(newValue[0] !== null ? newValue[0].toLocaleDateString():'')
                            setHastaFE(newValue[1] !== null ? newValue[1].toLocaleDateString():'')
                        }}
                        renderInput={(startProps, endProps) => (
                            <React.Fragment>
                                <TextField 
                                {...startProps} 
                                variant="outlined"
                                size = 'small'
                                style={{width: '100%'}}  
                                />
                                <Box sx={{ mx: 2 }}> a </Box>
                                <TextField
                                {...endProps}
                                variant="outlined"
                                size = 'small'
                                style={{width: '100%'}}                 
                                />
                            </React.Fragment>
                        )}
                    />
                </FormControl>
            </LocalizationProvider> 
        </GridItem>                  
              
        <Divider  />
        <GridItem md={12}  style={{width:'100%',marginTop: 10}}>        
            <CustomButtons variant="contained" round 
                startIcon={<SearchRoundedIcon />}
                color="primary" 
                // style={{ backgroundColor: '#01579b', color: '#ffffff'}} 
                variant="contained"  
                onClick={() => { generarExpresionFiltros(); }}>
                Buscar
            </CustomButtons>  
            <CustomButtons variant="contained" round 
                startIcon={<SearchOffRoundedIcon />} 
                // style={{ backgroundColor: '#01579b', color: '#ffffff', marginLeft: 15}} 
                variant="contained"
                color="primary"
                style={{ marginLeft: 10}}   
                onClick={() => { setearExpresionFiltros(); }}>
                Inicializar
            </CustomButtons>                
        </GridItem>
        <Notification
            notify = {notify}
            setNotify = {setNotify}
        > 
        </Notification> 
        </GridContainer>   
    </React.Fragment> 
 )
}

export default FiltroBusquedaFisAduana
