import * as api from '../../api';
import * as React from 'react';
import useStyles from '../Common/styles';
import { host, client } from '../../conf';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import MaterialTable from 'material-table';
import Paper from '@material-ui/core/Paper';
import MTableToolbar from 'material-table';
import Chip from '@material-ui/core/Chip';

import LockOpenOutlinedIcon from '@material-ui/icons/LockOutlined';
import LockOutlinedIcon from '@material-ui/icons/LockOpenOutlined';
import LockTwoToneIcon from '@material-ui/icons/LockTwoTone';
import FolderOutlinedIcon from '@material-ui/icons/FolderOutlined';

import Notification from '../Notification';
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'

import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";

export interface Props {
  expresionFiltroTitulos: string;  
  buscando: boolean;  
}


export default function ResultadosControlInv(props: any) {

  const [TTTCList, updateTTTCList] = React.useState([]);
  const [TATCList, updateTATCList] = React.useState([]);
  const [TSTCList, updateTSTCList] = React.useState([]);
  const [TATCCant, updateTATCCant] = React.useState('Abiertos: 0');
  const [TSTCCant, updateTSTCCant] = React.useState('Cerrados: 0');
  const [TTTCCant, updateTTTCCant] = React.useState('TOTAL: 0');
  const [loadData, setLoadData] = React.useState(false);

  const classes = useStyles();
  const modal_style = {  top:'5%', left:'5%',  overflow:'scroll',  height:'95%', width:'95%',
  display:'block'};

  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const box_style = {background: "white"}

  const downloadExcel=(filename)=>{
    const newData= TTTCList.map((row)=>{
      return {
        TATC_TSTC: row.numero,
        Tipo: row.tipooper_name,
        Contenedor: row.contenedor_numero,
        TipoContenedor: row.tipocontenedor_tipo,        
        Tamaño: row.contenedor_tamano_tipo,         
        Depósito: row.deposito_nombre,  
        Aduana: row.aduana_nombre,                  
        Emisión: row.fecha_emision_str,
        Valor: row.valor,
        Estado: row.titulo_estado 
      }
    })

    const workSheet=XLSX.utils.json_to_sheet(newData)

    let header = ['TATC_TSTC', 'Tipo', 'Contenedor', 'TipoContenedor', 'Tamaño', 'Depósito', 'Emisión',  'Valor', 'Estado']
    const jsonKeys = header || Object.keys(newData[0])

    const objectMaxLength = []
    jsonKeys.forEach((key) => {
      objectMaxLength.push(
        pixelWidth(key, {
          size: 3,
        })
      )
    })

    newData.forEach((data, i) => {
      const value = newData[i]
      jsonKeys.forEach((key, j) => {
        const l = value[jsonKeys[j]]
          ? pixelWidth(value[jsonKeys[j]], {
              size: 3,
            })
          : 0
        objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

      })
    })



    const wscols = objectMaxLength.map((w: number) => ({width: w}))
    workSheet["!cols"] = wscols

    const workBook=XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workBook,workSheet,filename)
    //Buffer
    let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
    //Binary string
    XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
    //Download
    XLSX.writeFile(workBook, filename+".xlsx")


  }  

  React.useEffect(() => {

      if (props.buscando)
      {
        setLoadData(true) 
        api.getData(host + '/api/tatc_tstctitulo/?'+props.expresionFiltroTitulos).then(x => {
          if(x.status === 200){
            updateTTTCList(x.data)
          }
        }).catch( e=>
          { 
            setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
          }).finally(() => {setLoadData(false)})
      }
      else {updateTTTCList([])}      

  }, [props.expresionFiltroTitulos, props.buscando])

  React.useEffect(() => {
           var ctemp = TTTCList.filter(x => x.estado == "A").length
            updateTATCCant('Abiertos: '+ ctemp)
            updateTSTCCant('Cerrados: '+ (TTTCList.length-ctemp))
            updateTTTCCant('TOTAL: '+ TTTCList.length)            
  }, [TTTCList])

  return (
    <React.Fragment>
    {/* // <Paper elevation={0} >
      // <GridContainer  >
      // <GridItem  md={12}  > */}

     <GridContainer>
     <GridItem  md={12}>     
      <div style={{position:"static",display:"flex"}}>
      <div style={{marginTop: 10,width:'100%', position:"static"}}>
        <MaterialTable
          data = {TTTCList}
          isLoading = {loadData} 
          style={{width:'100%'}}         
          localization={{
            pagination: {
                labelDisplayedRows: '{from}-{to} de {count}',
                labelRowsSelect: 'filas'
            },
            toolbar: {
                nRowsSelected: '{0} filas(s) seleccionadas',
                searchTooltip: 'Buscar',
                searchPlaceholder: 'Buscar',
                exportTitle: 'Exportar',
                exportAriaLabel: 'Exportar',
                exportCSVName: 'EXCEL',
                exportPDFName: 'PDF'
            },
            header: {
                actions: 'Acciones'
            },
            grouping: {
              groupedBy: 'Agrupar por',
              placeholder: 'Arraste la columna para agrupar'
            },
            body: {
                emptyDataSourceMessage: 'No existen filas para visualizar',
                filterRow: {
                    filterTooltip: 'Filtrar'
                }
            }
          }}              
          columns={[
              { field: 'numero', title: 'Número TATC/TSTC' },
              { field: 'tipooper_name', title: 'Tipo' },
              { field: 'contenedor_numero', title: 'Contenedor' },
              { field: 'tipocontenedor_tipo', title: 'Tipo'},      
              { field: 'contenedor_tamano_tipo', title: 'Tamaño' }, 
              { field: 'deposito_nombre', title: 'Depósito' },    
              { field: 'aduana_nombre', title: 'Aduana' },                             
              { field: 'fecha_emision', title: 'Emisión',  type: 'date',
              dateSetting: {
                format: 'dd/MM/yyyy'
              }}, 
              { field: 'valor', title: 'Valor' }, 
              { field: 'titulo_estado', title: 'Estado', lookup: { 'Abierto': 'Abierto', 'Cerrado': 'Cerrado' } }     
          ]} 
           
          options={{
              grouping: true, 
              actionsColumnIndex: -1,
              showTitle: false, 
              exportAllData: true,                     
              exportButton: true,
              exportFileName: 'ControlInventarios',
              exportCsv: (columns, data) => {
                downloadExcel("ControlInventarios")
              },               
              headerStyle: {
                backgroundColor: '#8e24aa',//'#01579b',
                color: '#FFF'
              }              

            }}   
                      
        />
      </div>
        {/* <GridItem md={12}> */}
        <div style={{position:"absolute", marginTop: 15}}>        
        {/* <div > */}
          <Chip icon={<FolderOutlinedIcon />} label={TTTCCant}  style={{marginTop: 15, marginLeft: 15}}/>         
          <Chip icon={<LockOutlinedIcon />} label={TATCCant} color= "primary" style={{marginTop: 15,marginLeft: 15}} /> 
          <Chip icon={<LockOpenOutlinedIcon />} label={TSTCCant} color= "secondary" style={{marginTop: 15,marginLeft: 15}}/>   
        </div>
        {/* </GridItem > */}
        </div>
      </GridItem>
      </GridContainer>
    {/* // </Paper> */}
  </React.Fragment>
  )
}
