
import * as React from 'react';


import { client } from '../../conf';


import { TextField} from "@material-ui/core"
import LocationOnRoundedIcon from '@material-ui/icons/LocationOnRounded';
import Notification from '../Notification'
import CustomMaterialTable from '../CustomMaterialTable'

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";

import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

export default function Deposito(props: any) {
  const classesCards = useStyleCards(); 
  const [DepositoList, updateDepositoList] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <LocationOnRoundedIcon fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Depósitos</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody> */}
          <CustomMaterialTable
            baseList = {DepositoList}
            setBaseList= {updateDepositoList}
            baseColumns= {[
              { field: 'id', title: 'Id' , hidden: true, export: false},
              { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false}, 
              { field: 'nombre', title: 'Nombre',
              editComponent: (editProps) =>  (
                <TextField
                   autoFocus={true}
                    value={editProps.value}
                            onChange={(e) => editProps.onChange(e.target.value)}
                    helperText={editProps.helperText}
                    error={editProps.error}
                    variant="standard"
                />              
              ),              
              validate: (rowData) =>
              rowData.nombre === undefined
                ? { isValid: false, helperText: 'Nombre Requerido' }
                : rowData.nombre === ''
                ? { isValid: false, helperText: 'Nombre Requerido' }
                : true            
              }, 
              { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},   
              { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
              { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              

          ]} 
            baseModelName= "Depósito"
            baseModelRoute= "inventariosdeposito"
            filename= "Depósitos"
            setNotify = {setNotify}


          />
       {/* </CardBody> */}
        </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
