import * as api from '../../api';
import * as React from 'react';
import { useRef } from 'react';

import { host, client } from '../../conf';

import TextField from '@mui/material/TextField';
import {Paper} from "@material-ui/core"
import Autocomplete from '@mui/material/Autocomplete';
import LocalAtmRoundedIcon from '@material-ui/icons/LocalAtmRounded';

import Notification from '../Notification'
import CustomMaterialTable from '../CustomMaterialTable'

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";
import stylesType from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

const useTextFieldStyles = makeStyles({	
  customTextField: {
   "& input::placeholder": {
     fontSize: "14px"
   },
   "& input": {
     fontSize: "14px"
   }
   }
 })


const useStylesType = makeStyles(stylesType)



export default function ContenedorValores(props: any) {

  const [ContenedorValoresList, updateContenedorValoresList] = React.useState([]);
  const [TipoContenedorLista, setTipoContenedorLista]= React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [tipoContenedorList,updateTipoContenedorList]= React.useState({}) 
  const [tipoTamanoList,updateTipoTamanoList]= React.useState({})  
  const classesCards = useStyleCards(); 
 
  const classesTextField = useTextFieldStyles()
 
  const updateAllTipoContenedor = () => {
    api.getData(host + '/api/inventariostipocontenedor/?cliente__id='+client).then(x => {
      if(x.status === 200){
        const tipoContenedorList={}
        x.data.map(row=>tipoContenedorList[row.id]=row.tipo)
        updateTipoContenedorList(tipoContenedorList)  
        setTipoContenedorLista(x.data);          
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }



  const updateAllTipoTamano = () => {
    api.getData(host + '/api/inventariostipotamano/?cliente__id='+client).then(x => {
      if(x.status === 200){
        const tipoTamanoList={}
        x.data.map(row=>tipoTamanoList[row.id]=row.tipo)
        updateTipoTamanoList(tipoTamanoList)          
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }


  React.useEffect(() => {
      updateAllTipoContenedor();
      updateAllTipoTamano();    
  }, [])

  const autoC2 = useRef(null);  

  const CustomPaper = (props) => {
    return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
};

  const defaultProps2 = {
    options: TipoContenedorLista,
    getOptionLabel: (option) => option.tipo,
  };

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
              <CardIcon color="primary">
                  <LocalAtmRoundedIcon fontSize="large" />
              </CardIcon>
              <h4 className={classesCards.cardTitleWhite}>CIF - FOB</h4>
              <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>      */}
          <CustomMaterialTable
            baseList = {ContenedorValoresList}
            setBaseList= {updateContenedorValoresList}
            baseColumns= {[
              { field: 'id', title: 'Id' , hidden: true, export: false},
              { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                  
              { field: 'tipocontenedor', title: 'Tipo Contenedor', defaultSort: 'asc' , lookup: tipoContenedorList, export: false,
              editComponent: (editProps) =>  (
                <Autocomplete
                  {...defaultProps2}                    
                  ref={autoC2}
                  autoSelect={true}                 
                  //freeSolo
                  onInputChange={(event, newInputValue) => {
                    if (newInputValue != "") 
                    {
                      let obj = TipoContenedorLista.find(o => o.tipo === newInputValue);
                      if (obj) {
                        editProps.onChange(obj.id)     
                      }
                    }
                  }}                      
                  disablePortal
                  defaultValue={editProps.rowData.tipocontenedor === '' || editProps.rowData.tipocontenedor === undefined ? TipoContenedorLista[0] : TipoContenedorLista.find(o => o.id === editProps.rowData.tipocontenedor) }
                  openOnFocus
                  fullWidth={true}
                  noOptionsText={'No existen opciones....'}
                  PaperComponent={CustomPaper}                                       
                  renderInput={(params) => 
                    <TextField {...params}
                      required
                      autoFocus

                      classes={{ root: classesTextField.customTextField }} 
                      size = 'small'

                      variant="standard"/>}
                    />
            ),            
              validate: (rowData) =>
              rowData.tipocontenedor === undefined
                ? { isValid: false, helperText: 'Tipo Contenedor Requerido' }
                : rowData.tipocontenedor === ''
                ? { isValid: false, helperText: 'Tipo Contenedor Requerido' }
                : true            
              }, 
              { field: 'tamano', title: 'Tamaño', lookup: tipoTamanoList, export: false,
              validate: (rowData) =>
              rowData.tamano === undefined
                ? { isValid: false, helperText: 'Tipo Tamaño Requerido' }
                : rowData.tamano === ''
                ? { isValid: false, helperText: 'Tipo Tamaño Requerido' }
                : true            
              },                
              { field: 'valorCIF', title: 'Valor CIF', type: 'numeric' },   
              { field: 'valorFOB', title: 'Valor FOB', type: 'numeric' },              
              { field: 'tipocontenedor_tipo', title: 'Tipo Contenedor' , hidden: true, export: true},  
              { field: 'tipotamano_tipo', title: 'Tamaño' , hidden: true, export: true},                      

              { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},                                
              { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
              { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              
            ]} 
            msgErrorUnique = "Tipo de Contenedor, Tamaño y Tipo Bulto deben se Únicos"
            baseModelName= "Valor CIF FOB"
            baseModelRoute= "inventarioscontenedorvalores"
            filename= "Valores_CIF_FOB"
            setNotify = {setNotify}
          />
        {/* </CardBody> */}
        </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
