import React from 'react';
import { Box, Grid, InputLabel, MenuItem, Select,  FormControl, Paper, FormHelperText } from "@material-ui/core"
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import {DatePicker, DateTimePicker} from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import esLocale from 'date-fns/locale/es';

import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
//import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/CardMdr/CardBody.js";
import CardFooter from "../../components/CardMdr/CardFooter.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

export interface Props {
  autoC: any,
  TATCList: any,
  AduanaList: any,
  traspasonew: any,
  errorAduana: boolean,
  errorTatc: boolean,
  setErrorAduana: Function,
  setErrorTatc: Function,
  updateTraspasoNew: Function,
  
  errorFecha : boolean,
  setErrorFecha :  Function,

  errorFechaIngreso : boolean, 
  setErrorFechaIngreso : Function   
  
}
const useStylesType = makeStyles(styles)
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

const CustomPaper = (props) => {
  return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
  };

  var charCodeZero = "0".charCodeAt(0);
  var charCodeNine = "9".charCodeAt(0);
  
  function isDigitCode(n) {
     //return(n >= charCodeZero && n <= charCodeNine);
     return(n >= "0" && n <= "9")
  }  

export default function TraspasoDatos (props: Props) {

    const defaultProps = {
        options: props.TATCList,
        getOptionLabel: (option) => option.numero,
      };   
      
      const [nameClassAduana, setNameClassAduana] = React.useState(''); 
      const [nameClassFecha, setNameClassFecha] = React.useState('');
      const [nameClassFechaIngreso, setNameClassFechaIngreso] = React.useState('');      
      const [nameClassTatc, setNameClassTatc] = React.useState('');  
    
      const classes = useStyles()  
      const classesTypo = useStylesType()
      React.useEffect(() => {
        setNameClassAduana(props.errorAduana == true ? classes.textFieldRed: classes.textField )
      }, [props.errorAduana])
    
      React.useEffect(() => {
        setNameClassFecha(props.errorFecha == true ? classes.textFieldRed: classes.textField )
      }, [props.errorFecha])  

      React.useEffect(() => {
        setNameClassFechaIngreso(props.errorFechaIngreso == true ? classes.textFieldRed: classes.textField )
      }, [props.errorFechaIngreso])      
    
      React.useEffect(() => {
        setNameClassTatc(props.errorTatc == true ? classes.textFieldRed: classes.textField )
      }, [props.errorTatc])
    


    return (
      <React.Fragment>    

            <GridContainer  style={{marginTop: -20}}>

              <GridItem xs={12} sm={6} md={4} >

                <FormControl id="ctl_Tipo" 
                  required 
                  style={{width:'100%'}}
                  >
                    <TextField
                      select
                      value={props.traspasonew.tipo}
                      label="Tipo *"
                      onChange={(event) => {
                        props.updateTraspasoNew({...props.traspasonew, tipo: event.target.value})
                      }}
                      defaultValue="0"
                      variant="outlined"                             
                      style={{marginTop: 10}} 
                      size = 'small'
                      //required 
                      inputProps={{
                        style: { fontSize: 14 },                                     
                      }}
                      >
                      <MenuItem value={'R'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Recepción</div></MenuItem>
                      <MenuItem value={'E'} style= {{ fontSize: "14px" }}><div style= {{ fontSize: "14px"}}>Entrega</div></MenuItem>
                    </TextField>
                </FormControl>
              </GridItem>            
              
                <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
                  <GridItem xs={12} sm={6} md={4} >
                      <DatePicker  
                          disableFuture
                          //minDate={props.traspasonew.fecha_ingreso_pais}
                          minDate={new Date(props.traspasonew.tipo=="R" ? props.traspasonew.fecha_ingreso_pais_str_dif: props.traspasonew.fecha_ingreso_pais_str_dif2)}

                          label="Fecha Emisión"
                          value={props.traspasonew.fecha_str}
                          onChange={(newValue) => {
                            props.updateTraspasoNew({...props.traspasonew, fecha: newValue, fecha_str: newValue})
                            props.setErrorFecha(!newValue || newValue.toString().length == 0 ||newValue.toString().substring(0,3) == 'Inv')                 
                          }}
                          renderInput={(params) => 
                          <TextField {...params} 
                            required 
                            className={nameClassFecha}
                            size = 'small'
                            style={{marginTop: 10,width: '100%'}} 
                          />}   
                          InputProps={{
                            style: {
                                fontSize: 14,
                                height: 40
                            }
                          }}
                      />
                  </GridItem>                              
                </LocalizationProvider> 

                {props.traspasonew.tipo=="R" && 
                <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
                  <GridItem xs={12} sm={6} md={4} >
                      <DateTimePicker 
                          disableFuture
                          label="Fecha Ingreso al País"
                          value={props.traspasonew.fecha_ingreso_pais_str_dif}
                          //maxDate={props.traspasonew.fecha}
                          onChange={(newValue) => {
                            props.updateTraspasoNew({...props.traspasonew, fecha_ingreso_pais: newValue, fecha_ingreso_pais_str_dif: newValue})
                            props.setErrorFechaIngreso(!newValue || newValue.toString().length == 0 ||newValue.toString().substring(0,3) == 'Inv')                 
                          }}
                          renderInput={(params) => 
                          <TextField {...params} 
                            required 
                            className={nameClassFechaIngreso}
                            size = 'small'
                            style={{marginTop: 10,width: '100%'}} 
                          />}   
                          InputProps={{
                            style: {
                                fontSize: 14,
                                height: 40
                            }
                          }}
                      />
                  </GridItem>                              
                </LocalizationProvider> 
                }
                {props.traspasonew.tipo=="E" && 
                <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
                  <GridItem xs={12} sm={6} md={4} > 
                      <TextField id="ctl_TATC_FechaIngreso" style={{ marginTop: 10}}
                          label="Ingreso al País"
                          type="date"
                          
                          value={props.traspasonew.fecha_ingreso_pais_str_dif}
                          variant="outlined"
                          fullWidth
                          size = 'small'
                          inputProps={{ readOnly: true , style: { fontSize: 14 } }}
                          //sx={{ width: 220 }}
                          InputLabelProps={{
                            shrink: true,
                          }}     
                      />
                  </GridItem>
                  
                </LocalizationProvider> 
              }                  
              
              <GridItem xs={12} sm={6} md={4} >
              <FormControl id="ctl_Aduana" style={{width:'100%'}}
                required 
                //error={props.errorAduana}>
                className={nameClassAduana}>
                <TextField                  
                    labelId="demo-simple-select-label"
                    id="aduana-select"
                    select
                    value={props.traspasonew.aduana}
                    label="Aduana"
                    color="primary"                  
                    onChange={(event) => {
                        props.updateTraspasoNew({...props.traspasonew, aduana: event.target.value})
                        props.setErrorAduana((event.target.value === "0"));
                    }}
                    defaultValue="0"
                    displayEmpty={false}
                    //fullWidth={true}
                    variant="outlined"                             
                    style={{marginTop: 10}} 
                    size = 'small'
                    required 
                    //error={props.errorAduana} 
                    inputProps={{
                      style: { fontSize: 14 },                                     
                    }}                 
                >
                    <MenuItem value="0" style= {{ fontSize: "14px" }}>
                    {<div className={classesTypo.mutedText}>Seleccione Aduana</div>} 
                    </MenuItem>
                    {props.AduanaList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                
                </TextField>
                </FormControl>
              </GridItem>
              <GridItem xs={12} sm={6} md={4} >
                {props.traspasonew.tipo=="E" && 
                <FormControl id="ctl_NumTATC" 
                  style={{width:'100%',marginTop: 10}}
                  //error={props.errorTatc}
                  size = 'small'		
                  required>
                  <Autocomplete style= {{ width:'100%' }}
                        {...defaultProps}
                        ref={props.autoC}
                        noOptionsText={'No existen opciones....'}
                        id="numtatc"
                        PaperComponent={CustomPaper}	
                        
                        onInputChange={(event, newInputValue) => {
                          props.updateTraspasoNew({...props.traspasonew, tatc_numero:newInputValue})
                          props.setErrorTatc((newInputValue.length === 0));                           
                        }}
                        sx={{ width: 300 }}
                        renderInput={(params) => 
                        <TextField {...params}
                        classes={{ root: classes.customTextField }} 

                        autoFocus 
                        required
                        variant="outlined" 
                        size = 'small'
                        onKeyPress={(event) => {
                          if (!isDigitCode(event?.key)) {
                            event.preventDefault();
                          }
                        }}                  
                        className={nameClassTatc}                        
                        label = 'Número TATC/TSTC'
                        />}
                  />                                   
                </FormControl> 
                }
              </GridItem>
            </GridContainer>          

     </React.Fragment> 
    )
}


