import React from 'react';
import { Box, Grid, Paper,InputLabel, MenuItem, Select,  FormControl, FormHelperText} from "@material-ui/core"
import TextField from '@mui/material/TextField';



import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Muted from "../../components/Typography/Muted.js";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
const useStylesTypo = makeStyles(styles);


export interface Props {
  AgenteList: any,
  AlmacenistaList: any,
  DepositoList: any,
  titulo: any,
  errorAgente: boolean,
  errorAlmacenista: boolean,
  errorDeposito: boolean,
  setErrorAgente: Function,
  setErrorAlmacenista: Function,
  setErrorDeposito: Function,
  setTitulo: Function           
}

const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 }
  })

export default function TTCParticipantes(props: Props) {

	
  const [nameClassAgente, setNameClassAgente] = React.useState(''); 
  const [nameClassAlmacenista, setNameClassAlmacenista] = React.useState('');
  const [nameClassDeposito, setNameClassDeposito] = React.useState('');  

  const classes = useStyles()
  const classesTypo = useStylesTypo()  


  React.useEffect(() => {
    setNameClassAgente(props.errorAgente == true ? classes.textFieldRed: classes.textField )
  }, [props.errorAgente])

  React.useEffect(() => {
    setNameClassAlmacenista(props.errorAlmacenista == true ? classes.textFieldRed: classes.textField )
  }, [props.errorAlmacenista])  

  React.useEffect(() => {
    setNameClassDeposito(props.errorDeposito == true ? classes.textFieldRed: classes.textField )
  }, [props.errorDeposito])
       

    return (
      <React.Fragment>    
        <GridContainer  style={{marginTop: -20}}>       
          
          <GridItem xs={12} sm={6} md={4}>
            <FormControl id="ctl_Agente" 
              style={{width:'100%',marginTop: 10}} 
              required
              className={nameClassAgente} 
              //error={props.errorAgente}
              >
              {/* <InputLabel >Agente</InputLabel> */}
              <TextField
                  required
                  value={props.titulo.agente}
                  select
                  label="Agente"
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, agente: event.target.value})
                    props.setErrorAgente((event.target.value === "0"));
                  }}
                  defaultValue="0"
                  //displayEmpty={false}
                  fullWidth={true} 
                  size = 'small'                                  
              >
                  <MenuItem value="0" style= {{ fontSize: "14px" }}>
                  {<div className={classesTypo.mutedText}>Seleccione Agente</div>} 
                  </MenuItem>
                  {props.AgenteList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.agente}</div></MenuItem>})}                
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
            </FormControl> 
          </GridItem>
          
          <GridItem xs={12} sm={6} md={4}>
            <FormControl id="ctl_Almacenista" 
              style={{width:'100%',marginTop: 10}}  
              required
              className={nameClassAlmacenista} 
              //error={props.errorAlmacenista}
              >              
              <TextField
                  required
                  value={props.titulo.almacenista}
                  select
                  label="Almacenista"
                  size = 'small'  
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, almacenista: event.target.value})
                    props.setErrorAlmacenista((event.target.value === "0"));
                  }}
                  defaultValue="0"
                  //displayEmpty={false}
                  fullWidth={true}                  
              >
                  <MenuItem value="0" style= {{ fontSize: "14px" }}>
                  {<div className={classesTypo.mutedText}>Seleccione Almacenista</div>} 
                  </MenuItem>
                  {props.AlmacenistaList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
            </FormControl> 
          </GridItem> 

          <GridItem xs={12} sm={6} md={4}>
            <FormControl id="ctl_Deposito" 
             style={{width:'100%',marginTop: 10}}  
              required 
              className={nameClassDeposito}
              //error={props.errorDeposito}
              >
              {/* <InputLabel >Depósito</InputLabel> */}
              <TextField
                  required
                  value={props.titulo.deposito}
                  select
                  label="Depósito"
                  size = 'small'  
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, deposito: event.target.value})
                    props.setErrorDeposito((event.target.value === "0"));
                  }}
                  defaultValue="0"
                  //displayEmpty={false}
                  fullWidth={true}                  
              >
                  <MenuItem value="0" style= {{ fontSize: "14px" }}>
                  {<div className={classesTypo.mutedText}>Seleccione Depósito</div>} 
                  </MenuItem>
                  {props.DepositoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
            </FormControl>                 
            </GridItem> 
          {/* </Grid>  
         </Paper>       
        </Grid> */}
      </GridContainer>
     </React.Fragment> 
    )
}


