import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {InputAdornment, Dialog} from "@material-ui/core";
import Container from "@material-ui/core/Container";
// @material-ui/icons
import Notification from '../Notification'
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
// core components
import GridContainer from "../Grid/GridContainer.js";
import GridItem from "../Grid/GridItem.js";
import Button from "../CustomButtons/Button";
import Card from "../Card/Card.js";
import CardBody from "../Card/CardBody.js";
import CardHeader from "../Card/CardHeader.js";
import CardFooter from "../Card/CardFooter.js";
import CustomInput from "../CustomInput/CustomInput.js";
import styles from "../../assets/jss/material-kit-react/views/loginPage.js";

// import bg1 from "../../assets/img/bg1.jpg";
// import bg2 from "../../assets/img/bg2.jpg";
// import bg3 from "../../assets/img/bg3.jpg";
// import bg4 from "../../assets/img/bg4.jpg";

import * as api from '../../api';
import { host} from '../../conf';

const useStyles = makeStyles(styles);


export default function ChangePassw(props) {
  const [cardAnimation, setCardAnimation] = React.useState("cardHidden");
  const [user, updateUser] = React.useState({ new_password1: "", new_password2: "" });
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [showPassword, setShowPassword] = React.useState(false);
  //const pictureArray = [bg1, bg2, bg3, bg4];
  //const randomIndex = Math.floor(Math.random() * pictureArray.length);


  setTimeout(function () {
    setCardAnimation("");
  }, 1000);
  const classes = useStyles();
  //const modal_style = {display:'flex',alignItems:'center',justifyContent:'center'};

  //const { ...rest } = props;
  
  const NoData = ()=>{
    setNotify({isOpen: true, message: 'Campos Requeridos en Blanco', errors: [], type: 'error'});
  };

  const Cancelar = ()=>{
    props.setShowDialog(false)
  };

  const cambiarpass = () => {
    api.postData(host + '/rest-auth/password/change/', user)
    .then(data => {
        if(data.status !== 200){
            let messages = [];
            Object.keys(data.data).map(key => messages.push(data.data[key]));
            setNotify({isOpen: true, message: messages, errors: [], type: 'error'});
        } else {
            setNotify({isOpen: true, message: 'Contraseña Actualizada con éxito...', errors: [], type: 'success'});
            
            setTimeout(() => {
              props.setShowDialog(false)  
              
            }, 1000)
            
        }
    });
  };


  const handleTogglePassword = () => setShowPassword(showPassword => !showPassword);

  return (
    <div>
    <Dialog  
            open={props.showDialog} 
            fullWidth            
            PaperProps={{              
              style: {
                backgroundColor: 'transparent',
                boxShadow: 'none',
                backgroundSize: "cover",
                backgroundPosition: "top center",
                height: "100vh",
                width: "100vw",        
                backgroundRepeat: "no-repeat",
                overflow:"hidden",
              },             
            }}            
    >
    <Container >
    <div className={classes.container}>                         
      <GridContainer justify="center">
      {/* <container > */}
        <GridItem xs={10} sm={8} md={8} lg={8}>    
          <Card className={classes[cardAnimation]}>          
          <form className={classes.form}>                      
            <CardHeader color="primary" className={classes.cardHeader}>
            <div style={{ display: "flex"}}>
            <IconButton style={{ marginLeft: "auto", marginBottom:"-30px", marginTop:"-20px" }}
              className={classes.menuButton}
              edge="start"
              aria-label="close"
              color="inherit"
              onClick={Cancelar}
            >                  
                <CloseIcon />
            </IconButton>
            </div>                     
              <h3>Cliente TATC</h3>
              <div className={classes.socialLine}>   
              </div>
            </CardHeader>                 
            <CardBody>
              <CustomInput
                labelText="Contraseña"
                required
                type= "password" 
                value={user.new_password1}
                formControlProps={{
                  fullWidth: true,
                }}
                inputProps={{
                  type: showPassword?"text":"password", 
                  onChange: (event) => {
                    //const value = event.target.value;
                    updateUser({...user, new_password1: event.target.value});
                  },                       
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleTogglePassword}
                      >
                        {showPassword ? <VisibilityIcon className={classes.inputIconsColor} /> : <VisibilityOffIcon className={classes.inputIconsColor}  />}
                      </IconButton>
                    </InputAdornment>
                  ),                       
                  autoComplete: "off",                       
                }}                                 
              />                    
              <CustomInput
                labelText="Confirme Contraseña"
                id="pass"
                value = {user.new_password2}
                required
                type= "password"                        
                formControlProps={{
                  fullWidth: true,
                }}                      
                inputProps={{
                  type: showPassword?"text":"password", 
                  onChange: (event) => {
                    //const value = event.target.value;
                    updateUser({...user, new_password2: event.target.value});
                  },                       
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleTogglePassword}
                      >
                        {showPassword ? <VisibilityIcon className={classes.inputIconsColor} /> : <VisibilityOffIcon className={classes.inputIconsColor}  />}
                      </IconButton>
                    </InputAdornment>
                  ),                       
                  autoComplete: "off",                       
                }}                      
              />
            </CardBody>
            <CardFooter className={classes.cardFooter}>
            {user.new_password1 !== '' && user.new_password2 !== '' ?(
                <Button color="primary" onClick={cambiarpass} round fullWidth>
                  Cambiar Contraseña
                </Button>
              ):(
                <Button color="primary" onClick={NoData} round fullWidth>
                  Cambiar Contraseña
                </Button>
              )}

            </CardFooter>
          </form>
          </Card>
        </GridItem>
        {/* </container> */}
      </GridContainer>
      
      <Notification
        notify = {notify}
        setNotify = {setNotify}
      > 
      </Notification>
    </div>
    </Container>
    </Dialog>
  </div>  
    
  );
}
