import React from 'react'
import FiltroBusquedaFisAduana from './FiltroBusquedaFisAduana'
import ResultadosFisAduana from './ResultadosFisAduana'
import UnarchiveRounded from "@mui/icons-material/UnarchiveRounded";
import PageHeader from '../PageHeader'


import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
//import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/CardMdr/CardBody.js";
import CardFooter from "../../components/CardMdr/CardFooter.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";

import * as api from '../../api';
import { host, client } from '../../conf';


const styles = {
    cardCategoryWhite: {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    cardTitleWhite: {
      color: "#FFFFFF",
      marginTop: "0px",
      minHeight: "auto",
      fontWeight: "300",
      fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
      marginBottom: "3px",
      textDecoration: "none",
    },
  };
  const useStyleCards = makeStyles(styles);

const FiscalizacionAduana = () => {
    
    const [expresionFiltroTitulos, setexpresionFiltroTitulos] = React.useState('');
    const [buscando, setBuscando] = React.useState(false); 
    const classesCards = useStyleCards();      
    return (
        <React.Fragment>
            <GridContainer >
            <GridItem md={12}>
            <Card>
            <CardHeader color="primary">
                <CardIcon color="primary">
                    <UnarchiveRounded fontSize="large" />
                </CardIcon>
                <h4 className={classesCards.cardTitleWhite}>Fiscalización de Aduana</h4>
                <p className={classesCards.cardCategoryWhite}>Consultas de Datos necesarios para realizar una exitosa Fiscalización de Aduana</p>
            </CardHeader>
            <CardBody>
                <FiltroBusquedaFisAduana
                    setexpresionFiltroTitulos={setexpresionFiltroTitulos}               
                    setBuscando = {setBuscando}                
                /> </CardBody>           
                <ResultadosFisAduana
                    expresionFiltroTitulos={expresionFiltroTitulos}
                    buscando = {buscando} 
                />
                     
            </Card>
            </GridItem>           
            </GridContainer>
        </React.Fragment>
    )
}

export default FiscalizacionAduana
