import React from "react";
import TipoCBarChart from './TipoCBarChart'
import AduanaPieChart from './AduanaPieChart'
import AgenteDonutChart from './AgenteDonutChart'
import SemBarChart from './SemBarChart'

import * as api from '../../api';
import { host } from '../../conf';

// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import ChevronLeftRounded from "@material-ui/icons/ChevronLeftRounded";
import ChevronRightRounded from "@material-ui/icons/ChevronRightRounded";
import Repeat from "@material-ui/icons/Repeat";
import Check from "@material-ui/icons/Check";
// core components
import GridItem from "../../components/GridMdr/GridItem.js";
import GridContainer from "../../components/GridMdr/GridContainer.js";
import Table from "../../components/Table/Table.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardBody from "../../components/CardMdr/CardBody.js";
import CardFooter from "../../components/CardMdr/CardFooter.js";


import styles from "../../assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { StarBorderPurple500Rounded } from "@mui/icons-material";
// import { Chart } from "chart.js";

const useStyles = makeStyles(styles);



export default function DashboardPage() {

  const classes = useStyles();

  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [totalAbiertos, setTotalAbiertos] = React.useState(0);
  const [totalAbiertosxTraspaso, setTotalAbiertosxTraspaso] = React.useState(0);
  const [totalAbiertosTATC, setTotalAbiertosTATC] = React.useState(0);
  const [totalAbiertosTSTC, setTotalAbiertosTSTC] = React.useState(0);
  const [total6M, setTotal6M] = React.useState(0);  
  const [tatc6M, setTatc6M] = React.useState(0); 
  const [tstc6M, setTstc6M] = React.useState(0);   
  const [valores] = React.useState([]);
  const [etiquetas] = React.useState([]);
  const [tablaDatos] = React.useState([]);
  const [valores2] = React.useState([]);
  const [etiquetas2] = React.useState([]);
  const [tablaDatos2] = React.useState([]);
  const [valores3] = React.useState([]);
  const [etiquetas3] = React.useState([]);
  const [tablaDatos3] = React.useState([]);
  const [tablaDatos4] = React.useState([]);

  const [data,  setData] = React.useState({labels: [], datasets: [{}]}); 
  const [data2, setData2] = React.useState({labels: [], datasets: [{label: 'Contenedores x Tipo'}]});
  const [data3, setData3] = React.useState({labels: [], datasets: [{}]});  
  const [data4, setData4] = React.useState({labels: [], datasets: [{label: 'TATC'}]});

  const loaddataAbiertos = () => {
    api.getData(host + '/api/userstatstotal_abiertos').then(x => {
      if(x.status === 200){
        let datos = x.data
        setTotalAbiertos(datos.total_abiertos)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const loaddataAbiertosxTraspaso = () => {
    api.getData(host + '/api/userstatstotal_abiertosxtraspaso').then(x => {
      if(x.status === 200){
        let datos = x.data
        setTotalAbiertosxTraspaso(datos.total_abiertosxtraspaso)
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }
      
  const loaddataAbiertosTATC = () => {
        api.getData(host + '/api/userstatstotal_abiertosTATC').then(x => {
          if(x.status === 200){
            let datos = x.data
            setTotalAbiertosTATC(datos.total_abiertosTATC)
          }
        }).catch( e=>
          { 
            setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
          });


        
     
  }

  const loaddataAduana = () => {

        api.getData(host + '/api/userstatstitulos_abiertos_aduana').then(x => {
        if(x.status === 200){
          
          let datos = x.data
          var i = 0
          let aduanas = datos.aduana_data
          for (var aduana in aduanas) {
             
            etiquetas.push(aduanas[i].aduana.toString());
            valores.push(aduanas[i].cant);
            tablaDatos.push([etiquetas[i],valores[i].toString()]);                    
            i=i+1
          }
          setData( 
            {
            labels: etiquetas,
            datasets: [{
                label: 'Contenedores x Aduana Emisión',
                data: valores,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)',
                  'rgba(75, 192, 192, 0.2)',              
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(153, 102, 255, 1)',
                  'rgba(255, 159, 64, 1)',
                  'rgba(75, 192, 192, 1)',              
                ],
                borderWidth: 1,
                hoverOffset: 4
            }]
            })
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
        
     
  }
 
  const loaddataTipoC = () => {
    

        api.getData(host + '/api/userstatstitulos_abiertos_tipoc').then(x => {
          if(x.status === 200){
            
            let datos = x.data
            var j = 0
            let tipos = datos.tipoc_data
            for (var tipo in tipos) {
              etiquetas2.push(tipos[j].tipo.toString());
              valores2.push(tipos[j].cant);
              tablaDatos2.push([tipos[j].tipo.toString(),tipos[j].cant.toString()]);
              j=j+1
            }

            setData2( 
              {
              labels: etiquetas2,
              datasets: [{
                  label: 'Contenedores x Tipo',
                  data: valores2,
                  backgroundColor: 'rgba(54, 162, 235, 0.2)',
                  borderColor: 'rgba(54, 162, 235, 1)',
                  borderWidth: 1
              }]
              })            

          }
        }).catch( e=>
          { 
            setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
          });   
          
           
     
  }

  const loaddataAgente = () => {
      
          
          api.getData(host + '/api/userstatstitulos_abiertos_agente').then(x => {
            if(x.status === 200){
              
              let datos = x.data
              var k = 0
              let agentes = datos.agente_data
              

              for (var agente in agentes) {
                etiquetas3.push(agentes[k].agente.toString());
                valores3.push(agentes[k].cant);
                tablaDatos3.push([agentes[k].agente.toString(),agentes[k].cant.toString()]);
                k=k+1
              }
              setData3( 
                {
                labels: etiquetas3,
                datasets: [{
                    label: 'Contenedores x Agente',
                    data: valores3,
                    backgroundColor: [
                      'rgba(255, 99, 132, 0.2)',
                      'rgba(54, 162, 235, 0.2)',
                      'rgba(255, 206, 86, 0.2)',
                      'rgba(153, 102, 255, 0.2)',
                      'rgba(255, 159, 64, 0.2)',
                      'rgba(75, 192, 192, 0.2)',              
                    ],
                    borderColor: [
                      'rgba(255, 99, 132, 1)',
                      'rgba(54, 162, 235, 1)',
                      'rgba(255, 206, 86, 1)',
                      'rgba(153, 102, 255, 1)',
                      'rgba(255, 159, 64, 1)',
                      'rgba(75, 192, 192, 1)',              
                    ],
                    borderWidth: 1
                }]
                })              
  
            }
          }).catch( e=>
            { 
              setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
            });           
     
  }  

  const loaddata6MTATC = () => {
      
    let data_response2 = {}
    let data_to_chart2 = []
    api.getData(host + '/api/userstats6M_TATC').then(x => {
      if(x.status === 200){
        let datos = x.data
        data_response2 = datos.sem_data

      }
    }).then(() => {
        let val_tatc = []
        let val_tstc = []
        Object.keys(data_response2).forEach(mes => {
            val_tatc.push(data_response2[mes][0].tatc)
            val_tstc.push(data_response2[mes][0].tstc)
            tablaDatos4.push([mes,data_response2[mes][0].tatc.toString(), data_response2[mes][0].tstc.toString(), data_response2[mes][0].ttc.toString()]);
        });

    

        setData4( 
          {
          labels: Object.keys(data_response2),
          datasets: [{
              label: 'TATC',
              data: val_tatc,
              backgroundColor: 'rgba(255, 99, 132, 0.2)',
              borderColor: 'rgba(255, 99, 132, 1)',
              borderWidth: 1
          },
          {
            label: 'TSTC',
            data: val_tstc,
            backgroundColor: 'rgba(54, 162, 235, 0.2)',
            borderColor: 'rgba(54, 162, 235, 1)',
            borderWidth: 1
           }]
          })              
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });           

  }  

const loaddataTotal6M = () => {
  api.getData(host + '/api/userstatstotal_6M').then(x => {
    if(x.status === 200){
      let datos = x.data
      setTotal6M(datos.total_proc)
    }
  }).catch( e=>
    { 
      setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
    });
}

const loaddataTatc6M = () => {
  api.getData(host + '/api/userstatstatc_6M').then(x => {
    if(x.status === 200){
      let datos = x.data
      setTatc6M(datos.total_tatc)
    }
  }).catch( e=>
    { 
      setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
    });
}

React.useEffect(() => {
  setTstc6M(total6M-tatc6M)
 }, [tatc6M, total6M])

  React.useEffect(() => {
   setTotalAbiertosTSTC(totalAbiertos-totalAbiertosTATC)
  }, [totalAbiertosTATC, totalAbiertos])


  React.useEffect(() => {

    // Chart.register({
    //   id: "custom_canvas_background_color",
    //   beforeDraw: (chart, args, opts) => {
    //     if (!opts.color) {
    //       return;
    //     }

    //     const ctx = chart.canvas.getContext("2d");
    //     ctx.save();
    //     ctx.globalCompositeOperation = "destination-over";
    //     ctx.fillStyle = opts.color;
    //     ctx.fillRect(0, 0, chart.width, chart.height);
    //     ctx.restore();
    //   }
    // });
    loaddataTipoC();
    loaddataAbiertos();
    loaddataAbiertosxTraspaso();
    loaddataAbiertosTATC();    

    loaddataAgente();
    loaddataAduana();
    loaddata6MTATC();
    loaddataTotal6M();
    loaddataTatc6M();    

  }, [])
  
  return (
<div>

    <GridContainer>
    <GridItem xs={12} sm={12} md={4}>
      <Card chart>
        <CardHeader color="success">
        <AduanaPieChart
          data = {data}
        />
        </CardHeader>
        <CardFooter chart>
          <div className={classes.stats}>
            <ChevronRightRounded style={{color:"green", marginLeft: 15, marginTop: -2}}/> {totalAbiertosTATC} TATC
            <ChevronLeftRounded style={{color:"green", marginLeft: 15, marginTop: -2}}/> {totalAbiertosTSTC} TSTC
          </div>
        </CardFooter>
      </Card>
    </GridItem>
    <GridItem xs={12} sm={12} md={4}>
      <Card >
        <CardHeader color="info">
        <TipoCBarChart
          data = {data2}
        />
        </CardHeader>
        <CardFooter chart>
          <div className={classes.stats}>
            <Check style={{color:"blue", marginTop: -2}} /> {totalAbiertos} Contenedores en Proceso
          </div>
        </CardFooter>
      </Card>
    </GridItem>
    <GridItem xs={12} sm={12} md={4}>
      <Card chart>
        <CardHeader color="danger">
        <AgenteDonutChart
          data = {data3}
        />
        </CardHeader>
        <CardFooter chart>
          <div className={classes.stats}>
            <Repeat style={{color:"red"}} /> {totalAbiertosxTraspaso} Recepcionados por Traspaso
          </div>
        </CardFooter>
      </Card>
    </GridItem>
   
  </GridContainer>

  <GridContainer>
        <GridItem xs={12} sm={12} md={4}>
          <Card>
            <CardHeader color="success">
              <h5 className={classes.cardTitleWhite}>ADUANA DE EMISIÓN</h5>
            </CardHeader>
            <CardBody style={{marginTop: -30}}>
              <Table 
                tableHeaderColor="success"
                tableHead={[ "Aduana", " % "]}
                tableData={tablaDatos}
              />
            </CardBody>
          </Card>
        </GridItem>

        <GridItem xs={12} sm={12} md={4}>
          <Card>
            <CardHeader color="info">
              <h5 className={classes.cardTitleWhite}>TIPO DE CONTENEDOR</h5>
            </CardHeader>
            <CardBody style={{marginTop: -30}}> 
              <Table
                tableHeaderColor="info"
                tableHead={[ "Tipo Contenedor", "Cantidad"]}
                tableData={tablaDatos2}
              />
            </CardBody>
          </Card>
        </GridItem>

        <GridItem xs={12} sm={12} md={4}>
          <Card>
            <CardHeader color="danger">
              <h5 className={classes.cardTitleWhite}>AGENTE</h5>
            </CardHeader>
            <CardBody style={{marginTop: -30}}>
              <Table
                tableHeaderColor="danger"
                tableHead={[ "Agente", "Cantidad"]}
                tableData={tablaDatos3}
              />
            </CardBody>
          </Card>
        </GridItem> 
  </GridContainer>


  <GridContainer>
    <GridItem  xs={12} sm={12} md={6}>
      <Card >
        <CardHeader  color="rose" >
        <SemBarChart 
          data = {data4}
        />
        </CardHeader>
        <CardFooter chart>
          <div className={classes.stats}>
            <StarBorderPurple500Rounded style={{color:"purple", marginTop: -2}} /> {total6M} Procesados
            <ChevronRightRounded style={{color:"purple", marginLeft: 15, marginTop: -2}}/> {tatc6M} TATC
            <ChevronLeftRounded style={{color:"purple", marginLeft: 15, marginTop: -2}}/> {tstc6M} TSTC            
          </div>
        </CardFooter>
      </Card>
    </GridItem>    
    <GridItem xs={12} sm={12} md={6}>
      <Card>
        <CardHeader color="rose">
          <h5 className={classes.cardTitleWhite}>BALANCE SEMESTRAL</h5>
        </CardHeader>
        <CardBody style={{marginTop: -30}}>
          <Table
            tableHeaderColor="rose"
            tableHead={[ "Mes", "TATC", "TSTC", "Total"]}
            tableData={tablaDatos4}
          />
        </CardBody>
      </Card>
    </GridItem>    
  </GridContainer>



        

  


    </div>


  );
}
