import * as api from '../../api';
import * as React from 'react';
import { useRef } from 'react';

import { host, client } from '../../conf';
import {Paper} from "@material-ui/core"
import TextField from '@mui/material/TextField';
import MoneyRoundedIcon from '@material-ui/icons/MoneyRounded';
import Autocomplete from '@mui/material/Autocomplete';
import Notification from '../Notification'
import CustomMaterialTable from '../CustomMaterialTable'

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";

import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";
import stylesType from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useTextFieldStyles = makeStyles({	
  customTextField: {
   "& input::placeholder": {
     fontSize: "14px"
   },
   "& input": {
     fontSize: "14px"
   }
   }
 })

const useStyleCards = makeStyles(styles);
const useStylesType = makeStyles(stylesType)
export default function AduanaCorrelativo(props: any) {

  const [AduanaCorrelativoList, updateAduanaCorrelativoList] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [aduanaList,updateAduanaList]= React.useState({}) 
  const [tipoOperList,updateTipoOperList]= React.useState({})  
  const [operadorActual, updateOperadorActual] = React.useState('0') 
  const classesTypo = useStylesType(styles)
  const [aduanaLista, setAduanaLista]= React.useState([]);
  const classesCards = useStyleCards(); 
  const classesTextField = useTextFieldStyles()
  //const box_style = {background: "white"}

  
  const updateNewData = (newData) => {
    newData.operador = operadorActual
    return newData
  }
  
  const updateAllAduana = () => {
    api.getData(host + '/api/fiscalizacion_controladuana/?estado=1').then(x => {
      if(x.status === 200){
        const aduanaList={}
        x.data.map(row=>aduanaList[row.id]=row.nombre)
        updateAduanaList(aduanaList)  
        setAduanaLista(x.data);        
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }


  const updateAllTipoOper = () => {
    api.getData(host + '/api/fiscalizacion_controltipooper/').then(x => {
      if(x.status === 200){
        const tipoOperList={}
        x.data.map(row=>tipoOperList[row.id]=row.nombre)
        updateTipoOperList(tipoOperList)          
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
  }  


  React.useEffect(() => {
    //updateAllOperador(); 
    updateAllAduana();
    updateAllTipoOper(); 
    api.getData(host + '/api/fiscalizacion_controloperador/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        let datos = x.data
        if (datos.length > 0)
        {
          let opact = datos[0]
          updateOperadorActual(opact.id.toString())
        }
        else {
          setNotify({isOpen: true, message: 'Error: No está definido Operador para Cliente Actual ', errors:[], type: 'error'})             
        } 
      }        
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
   
   
  }, [])

  
  const autoC2 = useRef(null);  

  const CustomPaper = (props) => {
    return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
};

  const defaultProps2 = {
    options: aduanaLista,
    getOptionLabel: (option) => option.nombre,
  };
  
 
  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <MoneyRoundedIcon fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Correlativos</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>     */}
        <CustomMaterialTable
            baseList = {AduanaCorrelativoList}
            setBaseList= {updateAduanaCorrelativoList}
            baseColumns= {[
              { field: 'id', title: 'Id' , hidden: true, export: false},
              { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                 
              { field: 'operador', title: 'Operador',   export: false, hidden: true},     
              { field: 'aduana_nombre', title: 'Aduana' ,  hidden:true,  export: true},                                                  
              { field: 'tipooper_nombre', title: 'Tipo' ,  hidden:true,  export: true},              
              { field: 'aduana', title: 'Aduana', defaultSort: 'asc' , 
              lookup: aduanaList, export: false,
              editComponent: (editProps) =>  (
                  <Autocomplete
                    {...defaultProps2}                    
                    ref={autoC2}
                    autoSelect={true}                 
                    //freeSolo
                    onInputChange={(event, newInputValue) => {
                      if (newInputValue != "") 
                      {
                        let obj = aduanaLista.find(o => o.nombre === newInputValue);
                        if (obj) {
                          editProps.onChange(obj.id)     
                        }
                      }
                    }}                      
                    disablePortal
                    defaultValue={editProps.rowData.aduana === '' || editProps.rowData.aduana === undefined ? aduanaLista[0] : aduanaLista.find(o => o.id === editProps.rowData.aduana) }
                    openOnFocus
                    fullWidth={true}
                    noOptionsText={'No existen opciones....'}
                    PaperComponent={CustomPaper}                                       
                    renderInput={(params) => 
                      <TextField {...params}
                        required
                        autoFocus
 
                        classes={{ root: classesTextField.customTextField }} 
                        size = 'small'

                        variant="standard"/>}
                      />
              ),               
              
              validate: (rowData) =>

              rowData.aduana === undefined
                ? { isValid: false, helperText: 'Aduana Requerida' }
                : rowData.aduana === ''
                ? { isValid: false, helperText: 'Aduana Requerida' }
              
                : true            
              }, 

              { field: 'tipooper', title: 'Tipo', lookup: tipoOperList, export: false,
              validate: (rowData) =>
              rowData.tipooper === undefined
                ? { isValid: false, helperText: 'Tipo Requerido' }
                : rowData.tipooper === ''
                ? { isValid: false, helperText: 'Tipo Requerido' }
                : true            
              },                  
              { field: 'correlativo_actual', title: 'Correlativo Actual', type: 'numeric', initialEditValue: '1',
              validate: (rowData) =>
                rowData.correlativo_actual === undefined
                ? { isValid: false, helperText: 'Correlativo Actual Requerido' }
                : rowData.correlativo_actual === ''
                ? { isValid: false, helperText: 'Correlativo Actual Requerido' }
                : true
              },   
              { field: 'correlativo_maximo', title: 'Correlativo Máximo', type: 'numeric', initialEditValue: '9999999',
              validate: (rowData) =>
                rowData.correlativo_maximo === undefined
                ? { isValid: false, helperText: 'Correlativo Máximo Requerido' }
                : rowData.correlativo_maximo === ''
                ? { isValid: false, helperText: 'Correlativo Máximo Requerido' }
                : true
              },         
                                             
              { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},                                   
              { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
              { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}
            ]} 
            baseModelName= "Correlativo"
            baseModelRoute= "fiscalizacion_controladuanacorrelativo"
            filename= "Correlativos"
            setNotify = {setNotify}
            msgErrorUnique = "Aduana y Tipo deben ser Únicos"
            updateNewData = {updateNewData}
          />
         {/* </CardBody> */}
        </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
