import React from 'react';
import {Chip, Grid, InputLabel, MenuItem, Select,  FormControl, FormHelperText, Paper } from "@material-ui/core"

import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { makeStyles } from "@material-ui/core/styles";


import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

export interface Props {
  autoC2: any,
  ContenedorList: any,
  ContenedorDispList: any,
  TipoContenedorList: any,
  TipoTamanoList: any,                            
  traspasonew: any,
  numContPropio: string,

  errorContenedorActual : boolean, 
  errorTipoContenedorActual: boolean, 
  errorTipoTamanoActual : boolean, 
  errorValor : boolean,   

  setErrorContenedorActual : Function, 
  setErrorTipoContenedorActual: Function, 
  setErrorTipoTamanoActual : Function,
  setErrorValor : Function,
  
  updateTraspasoNew: Function,
  updateNumContPropio : Function,
  
  tipoBulto :string,  
  valor: string  
}


function fgValidaContenedor(varContenedor: string): boolean
{
	//if (varContenedor.toUpperCase().trim().substr(3,1) != 'U') {return (false)}
	let i = 0;
	let intdig = 0;
	let intValor = 0;
	let strDigito = 0;
	let ch = "";
	for (i = 0; i <= (varContenedor.length - 1); i++)   	/*extrae sólo los caracteres*/
	{
		ch = varContenedor.toUpperCase().trim().substr(i,1);   	/*numéricos sin dígito verificador*/
		if(ch >= "0" && ch <= "9")
		{
			intValor = Number(ch);
		}
		else
		{
			if(ch == "A")
			{
				intValor = 10;
			}
			if(ch == "B")
			{
				intValor = 12;
			}
			if(ch == "C")
			{
				intValor = 13;
			}
			if(ch == "D")
			{
				intValor = 14;
			}
			if(ch == "E")
			{
				intValor = 15;
			}
			if(ch == "F")
			{
				intValor = 16;
			}
			if(ch == "G")
			{
				intValor = 17;
			}
			if(ch == "H")
			{
				intValor = 18;
			}
			if(ch == "I")
			{
				intValor = 19;
			}
			if(ch == "J")
			{
				intValor = 20;
			}
			if(ch == "K")
			{
				intValor = 21;
			}
			if(ch == "L")
			{
				intValor = 23;
			}
			if(ch == "M")
			{
				intValor = 24;
			}
			if(ch == "N")
			{
				intValor = 25;
			}
			if(ch == "O")
			{
				intValor = 26;
			}
			if(ch == "P")
			{
				intValor = 27;
			}
			if(ch == "Q")
			{
				intValor = 28;
			}
			if(ch == "R")
			{
				intValor = 29;
			}
			if(ch == "S")
			{
				intValor = 30;
			}
			if(ch == "T")
			{
				intValor = 31;
			}
			if(ch == "U")
			{
				intValor = 32;
			}
			if(ch == "V")
			{
				intValor = 34;
			}
			if(ch == "W")
			{
				intValor = 35;
			}
			if(ch == "X")
			{
				intValor = 36;
			}
			if(ch == "Y")
			{
				intValor = 37;
			}
			if(ch == "Z")
			{
				intValor = 38;
			}
		}
		i = i + 1;
		if((i - 1) == 0)
		{
			intdig = (intdig + (intValor * 1));
		}
		if((i - 1) == 1)
		{
			intdig = (intdig + (intValor * 2));
		}
		if((i - 1) == 2)
		{
			intdig = (intdig + (intValor * 4));
		}
		if((i - 1) == 3)
		{
			intdig = (intdig + (intValor * 8));
		}
		if((i - 1) == 4)
		{
			intdig = (intdig + (intValor * 16));
		}
		if((i - 1) == 5)
		{
			intdig = (intdig + (intValor * 32));
		}
		if((i - 1) == 6)
		{
			intdig = (intdig + (intValor * 64));
		}
		if((i - 1) == 7)
		{
			intdig = (intdig + (intValor * 128));
		}
		if((i - 1) == 8)
		{
			intdig = (intdig + (intValor * 256));
		}
		if((i - 1) == 9)
		{
			intdig = (intdig + (intValor * 512));
		}
		i = i - 1;
	}

	strDigito = (intdig % 11);
	if(strDigito == 10)
	{
		strDigito = 0;
	}

	ch = varContenedor.trim().substr(i-1,1);
	return (Number(ch) == strDigito);

}
const useStylesTypo = makeStyles(styles);
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

export default function TraspasoContenedorNuevo (props: Props) {

  const defaultProps = {
    options: props.ContenedorDispList,
    getOptionLabel: (option) => option.numero,
  }; 

  const [errorMessageContenedor, setErrorMessageContenedor] = React.useState(''); 	
  const [nameClassContenedor, setNameClassContenedor] = React.useState(''); 
  const [nameClassTipoContenedor, setNameClassTipoContenedor] = React.useState('');
  const [nameClassTipoTamano, setNameClassTipoTamano] = React.useState('');  
  const [nameClassValor, setNameClassValor] = React.useState('');

  const classes = useStyles()
  const classesTypo = useStylesTypo()  
  React.useEffect(() => {
    setNameClassTipoContenedor(props.errorTipoContenedorActual == true ? classes.textFieldRed: classes.textField )
  }, [props.errorTipoContenedorActual])

  React.useEffect(() => {
    setNameClassTipoTamano(props.errorTipoTamanoActual == true ? classes.textFieldRed: classes.textField )
  }, [props.errorTipoTamanoActual])  

  React.useEffect(() => {
    setNameClassContenedor(props.errorContenedorActual == true ? classes.textFieldRed: classes.textField )
  }, [props.errorContenedorActual])

  React.useEffect(() => {
    setNameClassValor(props.errorValor == true ? classes.textFieldRed: classes.textField )
  }, [props.errorValor])

  const CustomPaper = (props) => {
    return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
    };

    return (
      <React.Fragment>    
        
		{/* <Grid 
          container spacing={2}
          style={{marginBottom: 15}}
		      item xs={12} md={12}>
         <Paper elevation={3} style={{width: '100%',  height: 'auto'}} >
		  <Grid id="cont_Aduana_Operador_Numero" item xs={12} md={12} style={{marginBottom: 10}}> */}
        <GridContainer  style={{marginTop: -20}}>
		<GridItem xs={12} sm={6} md={4}>  
		  <FormControl id="ctl_NumContenedor" 
		  	style={{width:'100%',marginTop: 10}}
            //error={props.errorContenedorActual}  
            required>
			<Autocomplete style= {{ width:'100%' }}      
				{...defaultProps}
				ref={props.autoC2}
				noOptionsText={'No existen opciones....'}
				id="numcontenedor"
				freeSolo
				PaperComponent={CustomPaper}
				onInputChange={(event, newInputValue) => {
					newInputValue = newInputValue.toUpperCase().trim()
					props.updateNumContPropio(newInputValue)
					props.setErrorContenedorActual(true)
					setErrorMessageContenedor('')  
					if (newInputValue.length != 11) {setErrorMessageContenedor('Debe contener 11 caracteres')}
					else if (newInputValue.substr(3,1) != 'U') {setErrorMessageContenedor('El cuarto caracter debe ser U')}
					else if (!fgValidaContenedor(newInputValue)) {setErrorMessageContenedor('Número incorrecto')} 
					else {
						props.setErrorContenedorActual(false)
						}                        
					}}				
				//sx={{ width: 300 }}						
				renderInput={(params) => 
					<TextField {...params}
					classes={{ root: classes.customTextField }} 
					// autoFocus 
					required
					helperText={errorMessageContenedor}
					variant="outlined" 
					size = 'small'
					label = 'Contenedor' 
					className={nameClassContenedor}
				/>}
			/>                 
          </FormControl> 
		</GridItem>

		<GridItem xs={12} sm={6} md={4}>  
			<FormControl id="ctl_TATC_TipoContenedor" 
			required
			className={nameClassTipoContenedor}
			style={{width:'100%', marginTop: 10}}
			//error={props.errorTipoContenedorActual}
			>
			{/* <InputLabel >Tipo de Contenedor</InputLabel> */}
			<TextField
				value={props.traspasonew.tipocontenedor}
				select
				label = "Tipo de Contenedor"
				defaultValue="0"
				displayEmpty={false}
				fullWidth={true}				
				size = 'small'
				variant="outlined"    
				onChange={(event) => {
					props.updateTraspasoNew({...props.traspasonew, tipocontenedor: event.target.value})
					props.setErrorTipoContenedorActual((event.target.value === "0"))
				}}                                              
			>			
			<MenuItem value="0" style= {{ fontSize: "14px" }}>              
				{<div className={classesTypo.mutedText}>Seleccione Tipo Contenedor</div>} 
			</MenuItem>
				{props.TipoContenedorList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.tipo}</div></MenuItem>})}
			</TextField>              
			</FormControl>
		</GridItem>

		<GridItem xs={12} sm={6} md={4}>  
          <FormControl id="ctl_TATC_Tamano" 
		  style={{width:'100%', marginTop: 10}}
		  required 
		  className={nameClassTipoTamano}
		  //error={props.errorTipoTamanoActual}
		  >
			{/* <InputLabel >Tamaño</InputLabel> */}
			<TextField
				select
				label = "Tamaño"
				value={props.traspasonew.tamano}
				defaultValue="0"
				displayEmpty={false}
				fullWidth={true} 
				size = 'small'
				variant="outlined" 
				onChange={(event) => {
				props.updateTraspasoNew({...props.traspasonew, tamano: event.target.value})
				props.setErrorTipoTamanoActual((event.target.value === "0"))
				}}                                             
			>			
			<MenuItem value="0" style= {{ fontSize: "14px" }}>              
			    {<div className={classesTypo.mutedText}>Seleccione Tamaño</div>} 
            </MenuItem>
				{props.TipoTamanoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.tipo}</div></MenuItem>})}
			</TextField>              
          </FormControl>
		</GridItem>

		<GridItem xs={12} sm={6} md={4}>  
		  <TextField id="ctl_TipoBulto" 
			style={{ marginTop: 10}}
			label="Tipo Bulto"
			type="text"
			variant="outlined"
			fullWidth
			size = 'small'
			inputProps={{
				style: { fontSize: 14 },                                     
			}}
			value={props.tipoBulto}
            />  
		</GridItem>
		<GridItem xs={12} sm={6} md={4}>  
		  <TextField id="ctl_Valor" 
			style={{ marginTop: 10}}
			required
			className={nameClassValor}
			label="Valor"
			type="number"
			variant="outlined"
			fullWidth
			size = 'small'
			inputProps={{
				style: { fontSize: 14 },                                     
			}}
			value={props.valor}
            />
		</GridItem>
		</GridContainer> 
		{/* 
          </Grid>                          
         </Paper>                                     
        </Grid> */}
      </React.Fragment> 
    )
}


