import React from 'react';
import {Chip, Grid, InputLabel, MenuItem, Select,  FormControl, FormHelperText, Paper } from "@material-ui/core"

import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import {DatePicker, DateTimePicker} from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import esLocale from 'date-fns/locale/es';

import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Muted from "../../components/Typography/Muted.js";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
const useStylesTypo = makeStyles(styles);


export interface Props {
  autoC2: any,
  ChoferList: any,
  EmpTpteList: any,
  titulo: any,
  rutChofer: string,
  errorFechaIngresoDeposito : boolean, 
  errorRutChofer: boolean, 
  errorPatente : boolean,   
  errorEmpTpte : boolean, 
  errorDocTpte : boolean, 
  setErrorFechaIngresoDeposito : Function, 
  setErrorRutChofer: Function, 
  setErrorPatente : Function,
  setErrorEmpTpte : Function, 
  setErrorDocTpte: Function, 
  setTitulo: Function,
  setRutChofer : Function 
}

const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

  var Fn = {
    validaRut: function(rutCompleto) {
        if (!/^[0-9]+-[0-9kK]{1}$/.test(rutCompleto))
            return false;
        var tmp = rutCompleto.split('-');
        var digv = tmp[1];
        var rut = tmp[0];
        if (digv == 'K') digv = 'k';
        return (Fn.dv(rut) == digv);
    },
    dv: function(T) {
        var M = 0,
            S = 1;
        for (; T; T = Math.floor(T / 10))
            S = (S + T % 10 * (9 - M++ % 6)) % 11;
        return S ? S - 1 : 'k';
    }
  }  

export default function TTCTpteTerrestre(props: Props) {
  
  const defaultProps = {
    options: props.ChoferList,
    getOptionLabel: (option) => option.rut+' ('+option.patente_camion+')',
  }; 

  const [nameClassFechaIngresoDeposito, setNameClassFechaIngresoDeposito] = React.useState(''); 
  const [nameClassChofer, setNameClassChofer] = React.useState('');
  const [nameClassPatente, setNameClassPatente] = React.useState(''); 
  const [nameClassEmpTpte, setNameClassEmpTpte] = React.useState(''); 
  const [nameClassDocTpte, setNameClassDocTpte] = React.useState(''); 
  const [errorMessageRut, setErrorMessageRut] = React.useState(''); 	   

  const classes = useStyles()
  const classesTypo = useStylesTypo()  

   React.useEffect(() => {
    setNameClassFechaIngresoDeposito(props.errorFechaIngresoDeposito === true ? classes.textFieldRed: classes.textField )
  }, [props.errorFechaIngresoDeposito])

  React.useEffect(() => {
    setNameClassChofer(props.errorRutChofer === true ? classes.textFieldRed: classes.textField )
  }, [props.errorRutChofer])  

  React.useEffect(() => {
    setNameClassPatente(props.errorPatente === true ? classes.textFieldRed: classes.textField )
  }, [props.errorPatente])

  React.useEffect(() => {
    setNameClassEmpTpte(props.errorEmpTpte === true ? classes.textFieldRed: classes.textField )
  }, [props.errorEmpTpte])

  React.useEffect(() => {
    setNameClassDocTpte(props.errorDocTpte === true ? classes.textFieldRed: classes.textField )
  }, [props.errorDocTpte]) 
  
  const CustomPaper = (props) => {
    return <Paper elevation={8} {...props} style={{width:'100%', fontSize: 14 }}/>;
    };

    return (
      <React.Fragment>        
        <GridContainer  style={{marginTop: -20}}>  
        <GridItem xs={12} sm={6} md={4}>
          <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
              <DatePicker 
                  disableFuture
                  label='Fecha Ingreso Depósito '
                  className={nameClassFechaIngresoDeposito}
                  value={props.titulo.fecha_ingreso_deposito_str}
                  //maxDate={props.titulo.fecha}
                  minDate={props.titulo.fecha_ingreso_pais}
                  onChange={(newValue) => {
                    props.setTitulo({...props.titulo, fecha_ingreso_deposito: newValue, fecha_ingreso_deposito_str: newValue})
                    props.setErrorFechaIngresoDeposito(!newValue || newValue.toString().length == 0 ||newValue.toString().substring(0,3) == 'Inv')                 
                  }}
                  renderInput={(params) => 
                  <TextField {...params} 
                  required 
                  //error= {props.errorFechaIngresoDeposito} 
                  className={nameClassFechaIngresoDeposito}
                  style={{marginTop: 10,width: '100%'}} 
                  //helperText = 'Requerido'
                  />}
                  InputProps={{
                    style: {
                        fontSize: 14,
                        height: 40
                    }
                  }} 
              />                             
          </LocalizationProvider> 
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>
          <FormControl id="ctl_RutChofer" style={{width:'100%',marginTop: 10}}
              //error={props.errorRutChofer}                  
              >
            <Autocomplete style= {{ width:'100%' }}                      
              {...defaultProps}
              ref={props.autoC2}
              noOptionsText={'No existen opciones....'}
              inputValue={props.rutChofer}              
              PaperComponent={CustomPaper}
              freeSolo
              onInputChange={(event, newInputValue) => {
                newInputValue = newInputValue.toUpperCase().trim()
                props.setRutChofer(newInputValue)

                setErrorMessageRut('')

                let temprut = newInputValue.substring(0,10)
                let temppatente = newInputValue.substring(12)
                temppatente=temppatente.substring(0,temppatente.length-1)
                let obj = props.ChoferList.find(o => o.rut === temprut && o.patente_camion === temppatente);
                props.setErrorRutChofer(!obj)
                if (props.errorRutChofer)
                {
                  if (newInputValue.length != 10) {setErrorMessageRut('Debe contener 10 caracteres')}
                  else if (newInputValue.substr(8,1) != '-') {setErrorMessageRut('El caracter 9 debe ser -')}
                  else if (!(Fn.validaRut(newInputValue))) {setErrorMessageRut('RUT no válido')} 
                  else {
                    props.setErrorRutChofer(false)
                  }
                }
               
              }}	
                
              renderInput={(params) => 
              <TextField {...params} 
                classes={{ root: classes.customTextField }}
                className={nameClassChofer}

																											
                {...params}								

                //autoFocus 
                variant="outlined" 
                label = 'RUT Chofer'
                size = 'small'
                helperText={errorMessageRut} 
                required
                                                        
              />}
                                          
            />
            {/* <FormHelperText>Requerido</FormHelperText>                   */}
          </FormControl> 
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>
		      <TextField id="ctl_Patente" 
                style={{ marginTop: 10}}
                label="Patente"
                type="text"
                required
                //error = {props.errorPatente}
                className={nameClassPatente}
                //helperText = "Requerido"                
                variant="outlined"
                value={props.titulo.patente_camion}
                fullWidth
                size = 'small'
                inputProps={{
                  style: { fontSize: 14 },                                     
                }}
                onChange={(event) => {
                  props.setTitulo({...props.titulo, patente_camion: event.target.value})
                  props.setErrorPatente((!event.target.value || event.target.value.toString().trim().length === 0));
                }}                
            />    		  
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>
          <FormControl id="ctl_EmpTpte" 
            style={{width:'100%', marginTop: 10}}
            className={nameClassEmpTpte}    
            required
            //</GridItem>error={props.errorEmpTpte}
            >                    
            <TextField
                required
                value={props.titulo.empresa_transportista}
                defaultValue="0"
                select
                label="Empresa Transportista"
                //displayEmpty={false}
                fullWidth
				        size = 'small'                 
                onChange={(event) => {
                  props.setTitulo({...props.titulo, empresa_transportista: event.target.value})
                  props.setErrorEmpTpte((event.target.value === "0"))
                }}                                              
            >
            <MenuItem value="0" style= {{ fontSize: "14px" }}>              
			        {<div className={classesTypo.mutedText}>Seleccione Empresa</div>} 
            </MenuItem>
            {props.EmpTpteList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}
            </TextField>
            
          </FormControl>
        </GridItem>

        <GridItem xs={12} sm={6} md={4}>                 
            <TextField id="ctl_DocTpte" 
             style={{ marginTop: 10}}
             variant="outlined"
             fullWidth
             size = 'small'
              inputProps={{
                  style: { fontSize: 14 },                                     
              }}
              label="Documento Transporte"
              type="text"
              required
              //error = {props.errorDocTpte}              
              className={nameClassDocTpte}
              value={props.titulo.documento_transporte}
              onChange={(event) => {
                props.setTitulo({...props.titulo, documento_transporte: event.target.value})
                props.setErrorDocTpte((!event.target.value || (event.target.value.toString().length === 0)));
              }}                
          /> 
        </GridItem>

      </GridContainer>
		  {/* </Grid>                                   
         </Paper>                                     
        </Grid> */}
      </React.Fragment> 
    )
}


