import * as api from '../../api';
import * as React from 'react';


import { host, client } from '../../conf';

import {TextField} from "@material-ui/core"
import TableChartRoundedIcon from '@material-ui/icons/TableChartRounded';

import Notification from '../Notification'
import CustomMaterialTable from '../CustomMaterialTable'

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

function fgValidaContenedor(varContenedor: string): boolean
{
  if (varContenedor.toUpperCase().trim().substr(3,1) !== 'U') {return (false)}
	let i = 0;
	let intdig = 0;
	let intValor = 0;
  let strDigito = 0;
  let ch = "";
	for (i = 0; i <= (varContenedor.length - 1); i++)   	/*extrae sólo los caracteres*/
	{
		ch = varContenedor.toUpperCase().trim().substr(i,1);   	/*numéricos sin dígito verificador*/
		if(ch >= "0" && ch <= "9")
		{
			intValor = Number(ch);
		}
		else
		{
			if(ch === "A")
			{
				intValor = 10;
			}
			if(ch === "B")
			{
				intValor = 12;
			}
			if(ch === "C")
			{
				intValor = 13;
			}
			if(ch === "D")
			{
				intValor = 14;
			}
			if(ch === "E")
			{
				intValor = 15;
			}
			if(ch === "F")
			{
				intValor = 16;
			}
			if(ch === "G")
			{
				intValor = 17;
			}
			if(ch === "H")
			{
				intValor = 18;
			}
			if(ch === "I")
			{
				intValor = 19;
			}
			if(ch === "J")
			{
				intValor = 20;
			}
			if(ch === "K")
			{
				intValor = 21;
			}
			if(ch === "L")
			{
				intValor = 23;
			}
			if(ch === "M")
			{
				intValor = 24;
			}
			if(ch === "N")
			{
				intValor = 25;
			}
			if(ch === "O")
			{
				intValor = 26;
			}
			if(ch === "P")
			{
				intValor = 27;
			}
			if(ch === "Q")
			{
				intValor = 28;
			}
			if(ch === "R")
			{
				intValor = 29;
			}
			if(ch === "S")
			{
				intValor = 30;
			}
			if(ch === "T")
			{
				intValor = 31;
			}
			if(ch === "U")
			{
				intValor = 32;
			}
			if(ch === "V")
			{
				intValor = 34;
			}
			if(ch === "W")
			{
				intValor = 35;
			}
			if(ch === "X")
			{
				intValor = 36;
			}
			if(ch === "Y")
			{
				intValor = 37;
			}
			if(ch === "Z")
			{
				intValor = 38;
			}
		}
		i = i + 1;
		if((i - 1) === 0)
		{
			intdig = (intdig + (intValor * 1));
		}
		if((i - 1) === 1)
		{
			intdig = (intdig + (intValor * 2));
		}
		if((i - 1) === 2)
		{
			intdig = (intdig + (intValor * 4));
		}
		if((i - 1) === 3)
		{
			intdig = (intdig + (intValor * 8));
		}
		if((i - 1) === 4)
		{
			intdig = (intdig + (intValor * 16));
		}
		if((i - 1) === 5)
		{
			intdig = (intdig + (intValor * 32));
		}
		if((i - 1) === 6)
		{
			intdig = (intdig + (intValor * 64));
		}
		if((i - 1) === 7)
		{
			intdig = (intdig + (intValor * 128));
		}
		if((i - 1) === 8)
		{
			intdig = (intdig + (intValor * 256));
		}
		if((i - 1) === 9)
		{
			intdig = (intdig + (intValor * 512));
		}
		i = i - 1;
	}

	strDigito = (intdig % 11);
	if(strDigito === 10)
	{
		strDigito = 0;
	}

	ch = varContenedor.trim().substr(i-1,1);
	return (Number(ch) === strDigito);

}

export default function Contenedor(props: any) {

  const [ContenedorList, updateContenedorList] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [tipoContenedorList,updateTipoContenedorList]= React.useState({}) 
  const [tipoTamanoList,updateTipoTamanoList]= React.useState({})  
  const classesCards = useStyleCards(); 
  
  const updateAllTipoContenedor = () => {
    api.getData(host + '/api/inventariostipocontenedor/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        const tipoContenedorList={}
        x.data.map(row=>tipoContenedorList[row.id]=row.tipo)
        updateTipoContenedorList(tipoContenedorList)          
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }

  const updateAllTipoTamano = () => {
    api.getData(host + '/api/inventariostipotamano/?estado=1&cliente__id='+client).then(x => {
      if(x.status === 200){
        const tipoTamanoList={}
        x.data.map(row=>tipoTamanoList[row.id]=row.tipo)
        updateTipoTamanoList(tipoTamanoList)          
      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
  }


  React.useEffect(() => {
      updateAllTipoContenedor();
      updateAllTipoTamano();    
  }, [])

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <TableChartRoundedIcon fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Contenedores</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>      */}
      		<CustomMaterialTable
            baseList = {ContenedorList}
            setBaseList= {updateContenedorList}
            baseColumns= {[
              { field: 'id', title: 'Id' , hidden: true, export: false},
                  
              { field: 'tiene_derivados', title: 'tiene_derivados' , hidden: true, export: false},                
              { field: 'numero', title: 'Contenedor', defaultSort: 'asc' ,
           
              render: rowData => rowData.numero.toUpperCase().trim(), 
              editComponent: (editProps) =>  (
                <TextField
                   autoFocus={true}
				   value={editProps.value}
                   onChange={(e) => editProps.onChange(e.target.value)}
				   helperText={editProps.helperText}
				   error={editProps.error}
				   variant="standard"
                />
              ),			                 
              validate: (rowData) =>
                rowData.numero === undefined
                ? { isValid: false, helperText: 'Contenedor Requerido' }
                : rowData.numero === ''
                ? { isValid: false, helperText: 'Contenedor Requerido' }
                : rowData.numero.length !== 11 
                ? 'Contenedor debe tener 11 caracteres'   
                : rowData.numero.toUpperCase().trim().substr(3,1) !== 'U'  
                ? 'Cuarto Dígito debe ser U'                    
                : !fgValidaContenedor(rowData.numero)  
                ? 'Contenedor no Válido'             
                : true
              },
              { field: 'tipocontenedor', title: 'Tipo', lookup: tipoContenedorList, export: false,
              validate: (rowData) =>
              rowData.tipocontenedor === undefined
                ? { isValid: false, helperText: 'Tipo Requerido' }
                : rowData.tipocontenedor === ''
                ? { isValid: false, helperText: 'Tipo Requerido' }
                : true            
              }, 

              { field: 'tamano', title: 'Tamaño', lookup: tipoTamanoList, export: false,
              validate: (rowData) =>
              rowData.tamano === undefined
                ? { isValid: false, helperText: 'Tamaño Requerido' }
                : rowData.tamano === ''
                ? { isValid: false, helperText: 'Tamaño Requerido' }
                : true            
              },                
              { field: 'descripcion', title: 'Descripción '},  
              { field: 'tipocontenedor_tipo', title: 'Tipo Contenedor',export: true, hidden: true},        
              { field: 'tipobulto_descripcion', title: 'Tipo Bulto',export: true, hidden: true},       
              { field: 'tipotamano_tipo', title: 'Tamaño',export: true, hidden: true},       

              { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},                                   
              { field: 'is_active', title: 'is active' ,  hidden:true, initialEditValue: 'true',  export: false},               
              { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}              

          ]} 
            baseModelName= "Contenedor"
            baseModelRoute= "inventarioscontenedor"
            filename= "Contenedores"
            setNotify = {setNotify}
          />
         {/* </CardBody> */}
        </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    </React.Fragment>
  )}
