import * as api from '../../api';
import * as React from 'react';


import { host, client } from '../../conf';
import MaterialTable from 'material-table';
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'

export interface Props {
  
  baseList: any[],
  setBaseList: Function,
  baseColumns: any[],
  baseModelName: string,
  baseModelRoute: string,
  filename: string,
  setNotify: Function,  
  msgErrorUnique?: string,
  updateNewData?: any      
}

function pick(props, object, header) { 
  const result = {}; 
  let pos = 0;
  for (const prop of props) 
  { 
    if (prop in object) {
      let key = header[pos] 
      result[key] = object[prop]; 
    } 
    pos++
  } 
  return result; 
} 

function getColName(fieldsearch, columns) { 

  const columnssearch = columns.filter(columnDef => {
    return  columnDef.field && columnDef.field === fieldsearch;
  })
 
  return columnssearch.length > 0 ? columnssearch[0].title : ''; 
} 

export default function CustomMaterialTable(props: Props) {

  const [selectedRow, setSelectedRow] = React.useState("");
  const [loadData, setLoadData] = React.useState(false);

  //const box_style = {background: "white"}

  const downloadExcel=()=>{

    const columnsexport = props.baseColumns.filter(columnDef => {
      //return !columnDef.hidden && columnDef.field && columnDef.export !== false;
      return  columnDef.field && columnDef.export !== false;
    })
   .sort((a, b) => (a.tableData.columnOrder > b.tableData.columnOrder) ? 1 : -1);

    const colExcel = []
    
    const fieldsExcel = []
    
    columnsexport.map(columnDef => 
    {
      colExcel.push(columnDef.title)
      fieldsExcel.push(columnDef.field)
    })

    const dataorig = props.baseList.filter(x => x.estado === "1").map((rowData) =>
    {
        return rowData
    })

    // columnsexport.map(columnDef => {
    //   props.baseRow[columnDef.field] = rowData[columnDef.field]
    // })


    const newData = []
    dataorig.map((obj) => {
      newData.push(pick(fieldsExcel, obj, colExcel))
    })
   
   
    const workSheet=XLSX.utils.json_to_sheet(newData)

    let header = colExcel
    const jsonKeys = header || Object.keys(newData[0])

    const objectMaxLength = []
    jsonKeys.forEach((key) => {
      objectMaxLength.push(
        pixelWidth(key, {
          size: 3,
        })
      )
    })

    newData.forEach((data, i) => {
      const value = newData[i]
      jsonKeys.forEach((key, j) => {
        const l = value[jsonKeys[j]]
          ? pixelWidth(value[jsonKeys[j]], {
              size: 3,
            })
          : 0
        objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

      })
    })



    const wscols = objectMaxLength.map((w: number) => ({width: w}))
    workSheet["!cols"] = wscols

    const workBook=XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workBook,workSheet, props.filename)
    //Buffer
    let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
    //Binary string
    XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
    //Download
    XLSX.writeFile(workBook, props.filename+".xlsx")


  }  

   const updateAllRows = () => {
    setLoadData(true) 
    api.getData(host + '/api/'+ props.baseModelRoute +'/?cliente__id='+client).then(x => {
      if(x.status === 200){
        props.setBaseList(x.data)

      }
    }).catch( e=>
      { 
        props.setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      }).finally(() => {setLoadData(false)})

  }  

  const cambiarEstado = (rowData) => {
    new Promise((resolve, reject) => {
      setTimeout(() => {
        const dataUpdate = [...props.baseList];
        const index = rowData.tableData.id;
        rowData.cliente = client
        rowData.isactive = 'true'
        rowData.estado = rowData.estado === "0" ? "1" : "0"
        api.putData(host +'/api/'+ props.baseModelRoute +'/' + rowData.id + "/",  rowData)
        .then(data => {
          let messages: any = [];
          if(data.status !== 200){
            Object.keys(data.data).map((key) => 
            {
              let mlabel =  getColName(data.data[key], props.baseColumns)
              messages.push( key + ": " + mlabel)
              return true
            });
                props.setNotify({isOpen: true, message: 'Error al cambiar Estado de ' + props.baseModelName, errors: messages, type: 'error'});
                reject();    
   
            } else {
                props.setNotify({isOpen: true, message: 'Cambio de Estado de '+ props.baseModelName + ' realizado con éxito', errors: [], type: 'success'}); 
                rowData = data.data                                      
                dataUpdate[index] = rowData;                    
                props.setBaseList([...dataUpdate]); 
                resolve(1);                             
            }
        }).catch( e=>
          { 
            props.setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
          });
      }, 1000)
 })}
  


  React.useEffect(() => {
    updateAllRows();
  }, [])  

  return (
    <React.Fragment>    
        
      <MaterialTable
          data = {props.baseList}
          columns={props.baseColumns} 
          isLoading = {loadData}         
          {...props}  
          // icons={{
          //   Edit: (rowData) => <EditOutlinedIcon  style={ { color: !!rowData.tiene_derivados ? "primary" : "disabled" }} /> ,              
          //   Delete: (rowData) => <DeleteOutlinedIcon  style={ { color: !!rowData.tiene_derivados ? "primary" : "disabled" }} /> ,
          //   Clear: () => <ClearOutlinedIcon style={{ color: "red" }} />,
          //   Check: () => <CheckOutlinedIcon style={{ color: "green" }} />,              
          //   Add: () => <AddBoxRoundedIcon style={{ color: "green" }} />              
          // }}          
          editable={{
            isEditable: rowData => rowData.tiene_derivados === false,              
            isDeletable: rowData => rowData.tiene_derivados === false,  
            onRowAdd: newData =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  newData.cliente = client
                  newData.isactive = 'true'
                  if (props.updateNewData !== undefined) {newData = props.updateNewData(newData)}
                  let messages: any = [];                  
                  api.postData(host + '/api/'+ props.baseModelRoute +'/', newData)
                  .then(data => {
                      if(data.status !== 201){
                        Object.keys(data.data).map((key) => 
                        {
                          let mlabel =  (props.msgErrorUnique !== undefined) && (props.msgErrorUnique !== "") &&  (key === "Datos Repetidos") ? props.msgErrorUnique : getColName(data.data[key], props.baseColumns)
                          messages.push( key + ": " + mlabel)
                        });
                          props.setNotify({isOpen: true, message: 'Error en la Adición de ' + props.baseModelName, errors: messages, type: 'error'});
                          reject();
                      } else {
                          props.setNotify({isOpen: true, message: 'Adición de ' +props.baseModelName +' realizada con éxito', errors: [], type: 'success'});
                          newData = data.data   
                          props.setBaseList([...props.baseList, newData]);                               
                      }
                  }).catch( e=>
                    { 
                      props.setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                    });                 
                  resolve();
                }, 1000)
              }),
            onRowUpdate: (newData, oldData) =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  const dataUpdate = [...props.baseList];
                  const index = oldData.tableData.id;
                  newData.cliente = client
                  newData.isactive = 'true'
                  api.putData(host +'/api/'+ props.baseModelRoute +'/' + oldData.id + "/",  newData)
                  .then(data => {
                    let messages: any = [];
                    if(data.status !== 200){
                      Object.keys(data.data).map((key) => 
                      {
                        let mlabel =  (props.msgErrorUnique !== undefined) && (props.msgErrorUnique !== "") && (key == "Datos Repetidos") ? props.msgErrorUnique : getColName(data.data[key], props.baseColumns)
                        messages.push( key + ": " + mlabel)
                      });
                          props.setNotify({isOpen: true, message: 'Error en la Edición de ' + props.baseModelName, errors: messages, type: 'error'});
                          reject();    
             
                      } else {
                          props.setNotify({isOpen: true, message: 'Edición de '+ props.baseModelName + ' realizada con éxito', errors: [], type: 'success'}); 
                          newData = data.data                                      
                          dataUpdate[index] = newData;                    
                          props.setBaseList([...dataUpdate]); 
                          resolve();                             
                      }
                  }).catch( e=>
                    { 
                      props.setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                    });
                }, 1000)
              }),
            onRowDelete: oldData =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  const index = oldData.tableData.id;
                  let messages: any = [];
                  api.deleteData(host + '/api/' +props.baseModelRoute +'/' + oldData.id + '/').then(data => {
                    if(data.status !== 204){
                        Object.keys(data.data).map(key => messages.push(key + ": " + data.data[key]));
                        props.setNotify({isOpen: true, message: 'Error en la Eliminación de ' + props.baseModelName, errors: messages, type: 'error'}); 
                        reject();                           
                    } else {
                      props.setNotify({isOpen: true, message: 'Eliminación de '+ props.baseModelName +' realizada con éxito', errors: [], type: 'success'}); 
                      const dataDelete = [...props.baseList];
                      dataDelete.splice(index, 1);
                      props.setBaseList([...dataDelete]);                         
                      resolve()                  
                    }        
                  }).catch( e=>
                    { 
                      props.setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                    });                 
                  

                }, 1000)
              }),
          }}          
          localization={{
            pagination: {
                labelDisplayedRows: '{from}-{to} de {count}',
                labelRowsSelect: 'filas'
            },
            toolbar: {
                nRowsSelected: '{0} filas(s) seleccionadas',
                searchTooltip: 'Buscar',
                searchPlaceholder: 'Buscar',
                exportTitle: 'Exportar',
                exportAriaLabel: 'Exportar',
                exportCSVName: 'EXCEL',
                exportPDFName: 'PDF'
            },
            header: {
                actions: 'Acciones'
            },
            grouping: {
              groupedBy: 'Agrupar por',
              placeholder: 'Arraste la columna para agrupar'
            },
            body: {
                emptyDataSourceMessage: 'No existen filas para visualizar',
                deleteTooltip : 'Eliminar', 

                editRow:{
                      saveTooltip: "Aceptar",
                      cancelTooltip: "Cancelar",                    
                      deleteText: "¿Está seguro que quiere borrar este "+ props.baseModelName +"?"
                },
                editTooltip : 'Editar', 
                addTooltip: "Adicionar",
                filterRow: {
                    filterTooltip: 'Filtrar'
                }
            }
          }}              
          onRowClick={((evt, selectedRow) => 
            {
              setSelectedRow(selectedRow.tableData.id)
            }
          )} 
          options={{
            filtering: true, 
            grouping: true, 
            
            actionsColumnIndex: -1,
            addRowPosition: "first",
            showTitle: false,  
            exportAllData: true,                    
            exportButton: true,
            exportFileName: props.filename,
            exportCsv: (columns, data) => {
              downloadExcel()
            },              
            headerStyle: {
              backgroundColor: '#8e24aa',//'#01579b',
              color: '#FFF'
            },              
            rowStyle: rowData => ({
              backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
            })          
          }}                        
          actions={[
             rowData => ({
              icon: 'check' ,
              //iconProps: { style: {  color: "green" } },                    
              tooltip: 'Cambiar Estado',
              onClick: (event, rowData) => cambiarEstado(rowData)

            }),
          ]} 
              
      />

    </React.Fragment>
  )  
 }
