import React, { useRef } from 'react';
import {Button, Box, Grid,  Input, InputLabel, MenuItem, Select, Divider, Chip, FormControl, FormHelperText } from "@material-ui/core"
import {DialogActions, DialogContent, Paper, List, ListItem} from "@material-ui/core"
import TextField from '@mui/material/TextField';
import InputAdornment from '@mui/material/InputAdornment';
import IconButton from '@mui/material/IconButton';
import { WarningAmberRounded } from '@mui/icons-material';
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";
//import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/CardMdr/CardBody.js";
import CardFooter from "../../components/CardMdr/CardFooter.js";
import CardIcon from "../../components/CardMdr/CardIcon.js";
import CustomButtons from '../../components/CustomButtons/Button.js';
import SaveRoundedIcon from '@material-ui/icons/SaveRounded';
import ClearAllRoundedIcon from '@material-ui/icons/ClearAllRounded';
import WarningRounded from '@material-ui/icons/WarningRounded';

import TraspasoDatos from './TraspasoDatos';
import TraspasoTATCExistente from './TraspasoTATCExistente';
import TraspasoTATCNuevo from './TraspasoTATCNuevo';
import TraspasoDatosExternos from './TraspasoDatosExternos';
import TraspasoContenedorExistente from './TraspasoContenedorExistente';
import TraspasoContenedorNuevo from './TraspasoContenedorNuevo';

export interface Props {
  autoC: any,
  autoC2: any,  
  TATCList: any,
  AduanaList: any,
  OperadorList: any,
  DepositoList: any,  
  ContenedorList: any,
  ContenedorDispList: any,          
  TipoContenedorList: any,
  TipoTamanoList: any,                            
 
  traspasonew: any,
  numeroPropio: string,  
  errorNumeroTATC: boolean, 
  errorAduana: boolean,
  errorTatc: boolean,
  errorOpExterno: boolean,
  errorDepositoExterno: boolean, 
  errorDepositoTatc: boolean, 
  errorContenedorActual : boolean, 
  errorTipoContenedorActual: boolean, 
  errorTipoTamanoActual : boolean, 
  errorValor : boolean, 
   
  opPropio: string,
  rutOpPropio: string,
  codOpPropio: string,
  rutOpExterno: string, 
  codOpExterno: string,   
  numContPropio: string,

  setErrorAduana: Function,
  setErrorTatc: Function,
  setErrorOpExterno: Function, 
  setErrorDepositoExterno : Function, 
  setErrorDepositoTatc: Function, 
  setErrorContenedorActual : Function, 
  setErrorTipoContenedorActual: Function, 
  setErrorTipoTamanoActual : Function,    
  setErrorValor : Function,
  updateTraspasoNew: Function,
  createTraspaso: Function, 
  resetTraspaso: Function,   
  updateNumContPropio: Function,

  errorFecha : boolean,
  errorFechaIngreso : boolean, 
  setErrorFecha :  Function, 
  setErrorFechaIngreso : Function,  

  tipoBulto :string,  
  valor: string,

  errorNumeroTATCExterno:boolean,
  setErrorNumeroTATCExterno : Function
}

export default function TraspasoBody (props: Props) {

    return (

      <React.Fragment>    
        {/* <DialogContent >
          <List
            sx={{
              width: '100%',
              //maxWidth: 360,
              bgcolor: 'background.paper',
            }}
          >
            <Divider variant="inset"  component="li"> </Divider>
            <Chip label = "Datos del Traspaso" color= "primary" style={{marginTop: -30,marginLeft: 15}}></Chip>          
             */}
             {/* <ListItem> */}
            <GridContainer style={{marginTop: -15}}>
              <GridItem md={12}>
                <Card>
                  <div>
                    <Chip label = "Datos del Traspaso" style={{marginTop: -30, marginLeft: 15}}></Chip>
                  </div>             
                  <CardBody>
                  <TraspasoDatos
                    autoC= {props.autoC}
                    TATCList= {props.TATCList}
                    AduanaList= {props.AduanaList}
                    traspasonew= {props.traspasonew}
                    errorAduana= {props.errorAduana}
                    errorTatc= {props.errorTatc}
                    setErrorAduana= {props.setErrorAduana}
                    setErrorTatc= {props.setErrorTatc}
                    updateTraspasoNew= {props.updateTraspasoNew}
                    errorFechaIngreso = {props.errorFechaIngreso}
                    setErrorFechaIngreso = {props.setErrorFechaIngreso}                    
                    errorFecha = {props.errorFecha}
                    setErrorFecha = {props.setErrorFecha}/>
                  </CardBody>  
                </Card>
              </GridItem>
                
              <GridItem md={12} style={{marginTop: -30}}>
                <Card>
                  <div>
                    <Chip label = "Origen" style={{marginTop: -30, marginLeft: 15}}></Chip>
                  </div>             
                  <CardBody>
                    {props.traspasonew.tipo==="R" &&                 
                    <TraspasoDatosExternos
                        OperadorList={props.OperadorList}
                        DepositoList= {props.DepositoList}
                        errorOpExterno={props.errorOpExterno}
                        errorDepositoExterno= {props.errorDepositoExterno} 
                        errorNumeroTATCExterno= {props.errorNumeroTATCExterno}
                        setErrorNumeroTATCExterno = {props.setErrorNumeroTATCExterno}
                        rutOpExterno= {props.rutOpExterno}  
                        codOpExterno= {props.codOpExterno}                          
                        traspasonew={props.traspasonew}
                        setErrorOpExterno={props.setErrorOpExterno}
                        setErrorDepositoExterno={props.setErrorDepositoExterno}
                        updateTraspasoNew= {props.updateTraspasoNew}/> 
                      }
                    {props.traspasonew.tipo==="E" && 
                    <TraspasoTATCExistente
                      OperadorList={props.OperadorList}
                      DepositoList={props.DepositoList}  
                      opPropio= {props.opPropio}
                      rutOpPropio={props.rutOpPropio}
                      codOpPropio={props.codOpPropio}                      
                      traspasonew= {props.traspasonew}/>
                    }                  
                  </CardBody>  
                </Card>
              </GridItem>                  
                               
              
              <GridItem md={12} style={{marginTop: -30}}>
                <Card>
                  <div>
                    <Chip label = "Destino" style={{marginTop: -30, marginLeft: 15}}></Chip>
                  </div>             
                  <CardBody>
                    {props.traspasonew.tipo==="R" &&                 
                    <TraspasoTATCNuevo
                      OperadorList={props.OperadorList}
                      DepositoList= {props.DepositoList} 
                      opPropio={props.opPropio}
                      rutOpPropio={props.rutOpPropio}
                      codOpPropio={props.codOpPropio}
                      traspasonew={props.traspasonew}
                      errorDepositoTatc={props.errorDepositoTatc}
                      numeroPropio= {props.numeroPropio}
                      errorNumeroTATC = {props.errorNumeroTATC}
                      updateTraspasoNew= {props.updateTraspasoNew}
                      setErrorDepositoTatc={props.setErrorDepositoTatc}/>                   
                    }
                    {props.traspasonew.tipo==="E" && 
                    <TraspasoDatosExternos
                      OperadorList={props.OperadorList}
                      DepositoList= {props.DepositoList}
                      errorOpExterno={props.errorOpExterno}
                      errorDepositoExterno= {props.errorDepositoExterno} 
                      errorNumeroTATCExterno= {props.errorNumeroTATCExterno}
                      setErrorNumeroTATCExterno = {props.setErrorNumeroTATCExterno}                      
                      rutOpExterno= {props.rutOpExterno}
                      codOpExterno= {props.codOpExterno}
                      traspasonew={props.traspasonew}
                      setErrorOpExterno={props.setErrorOpExterno}
                      setErrorDepositoExterno={props.setErrorDepositoExterno}    
                      updateTraspasoNew= {props.updateTraspasoNew}/>
                    }                  
                  </CardBody>  
                </Card>
              </GridItem>


              <GridItem md={12} style={{marginTop: -30}}>
                <Card>
                  <div>
                    <Chip label = "Contenedor" style={{marginTop: -30, marginLeft: 15}}></Chip>
                  </div>             
                  <CardBody>
                    {props.traspasonew.tipo==="R" &&                 
                      <TraspasoContenedorNuevo
                      autoC2= {props.autoC2}
                      ContenedorList={props.ContenedorList}
                      ContenedorDispList={props.ContenedorDispList}
                      TipoContenedorList={props.TipoContenedorList}
                      TipoTamanoList= {props.TipoTamanoList}                        
                      traspasonew={props.traspasonew}
                      errorContenedorActual = {props.errorContenedorActual}
                      errorTipoContenedorActual = {props.errorTipoContenedorActual}
                      errorTipoTamanoActual =  {props.errorTipoTamanoActual}
                      errorValor =  {props.errorValor}
                      numContPropio = {props.numContPropio}
                      setErrorContenedorActual = {props.setErrorContenedorActual}
                      setErrorTipoContenedorActual= {props.setErrorTipoContenedorActual}
                      setErrorTipoTamanoActual = {props.setErrorTipoTamanoActual}
                      setErrorValor = {props.setErrorValor}
                      updateTraspasoNew= {props.updateTraspasoNew}     
                      updateNumContPropio = {props.updateNumContPropio}
                      tipoBulto =  {props.tipoBulto}  
                      valor =  {props.valor}/>
                    }
                    {props.traspasonew.tipo==="E" && 
                      <TraspasoContenedorExistente
                      ContenedorList = {props.ContenedorList}
                      TipoContenedorList = {props.TipoContenedorList}
                      TipoTamanoList = {props.TipoTamanoList}                            
                      traspasonew=  {props.traspasonew}
                      tipoBulto =  {props.tipoBulto}  
                      valor =  {props.valor}/>
                    }                  
                  </CardBody>  
                </Card>
              </GridItem> 

              <GridItem md={12} style={{marginTop: -20}}>              
              <TextField  
                value = '* Requerido' 
                variant="standard"
                InputProps={{ 
                  disableUnderline: true, 
                  style: {fontSize: 12},
                  startAdornment: 
                  <InputAdornment 
                
                  position="start">
                    
                  <WarningAmberRounded   />

                  </InputAdornment>, 
              
                }} 
                //style={{marginLeft: 85}} 
              />                         
            </GridItem>
            <CardFooter>
              <GridContainer>
              <GridItem  xs={3} sm={3} md={3}>
              <CustomButtons variant="contained" round
                    //fullWidth
                    color="primary"
                    style={{ marginLeft: 0}}  
                    disabled = {(props.errorAduana || props.errorOpExterno || props.errorDepositoExterno) 
                        || (props.traspasonew.tipo==="E" ? props.errorTatc : false) 
                        ||(props.traspasonew.tipo==="R" ? (props.errorDepositoTatc  || props.errorContenedorActual || props.errorNumeroTATCExterno  
                        || props.errorTipoContenedorActual || props.errorTipoTamanoActual || props.errorValor) : false )
                    }  
                    startIcon={<SaveRoundedIcon />}                   
                    onClick={props.createTraspaso}>Guardar
                </CustomButtons>    
                </GridItem>
                <GridItem  xs={3} sm={3} md={3}>
                <CustomButtons variant="contained" round
                  //fullWidth
                  color="primary" 
                  startIcon={<ClearAllRoundedIcon />} 
                  style={{ marginLeft: 50}} 
                  onClick={props.resetTraspaso}>Inicializar
                </CustomButtons> 
              </GridItem>
              </GridContainer>
            </CardFooter> 
            
                   


      </GridContainer> 
      </React.Fragment>

    )
}


