import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
// creates a beautiful scrollbar
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";
import "../../assets/css/material-dashboard-react.css?v=1.10.0";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Navbar from "../Navbars/Navbar.js";

import Sidebar from "../Sidebar/Sidebar.js";

import dashboardRoutes from "../../routes.js";
import dashboardRoutesOper from "../../routesoper.js";
import dashboardRoutesSuper from "../../routessuper.js";
import dashboardRoutesAduana from "../../routesaduana.js";
import styles from "../../assets/jss/material-dashboard-react/layouts/adminStyle.js";

import bgImage from "../../assets/img/sidebar-2.jpg";
import logo from "../../assets/img/logo512.png";

import agente from "../../components/Agente";
import almacenista from "../../components/Almacenista";
import depositos from "../../components/Depositos";
import puertos from "../../components/Puertos";
import nave from "../../components/Nave";
import tipobulto from "../../components/TipoBulto";
import genericocontenedor from "../../components/GenericoContenedor";
import tipocontenedor from "../../components/TipoContenedor";
import genericotamano from "../../components/GenericoTamano";
import tipotamano from "../../components/TipoTamano";
import contenedor from "../../components/Contenedor";
import contenedorvalores from "../../components/ContenedorValores";
import aduana from "../../components/Aduana";
import operador from "../../components/Operador";
import aduanacorrelativo from "../../components/AduanaCorrelativo";
import emptpte from "../../components/EmpTpte";
import chofer from "../../components/Chofer";
import motivocierre from "../../components/MotivoCierre";
import detalleanulacion from "../../components/DetalleAnulacion";
import documentacion from "../../components/Documentacion";
import documento from "../../components/Documento";
import extension from "../../components/Extension";
import usuarios from "../../components/Usuarios";
import rol from "../../components/Rol";





let ps;



const useStyles = makeStyles(styles);

export default function Principal({rolCurrent,...rest},props) {

 
  
  // styles
  const classes = useStyles();

  const mainPanel = React.createRef();

  const [image] = React.useState(bgImage);
  const [color] = React.useState("purple");
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [routes] = React.useState(dashboardRoutesOper);
  const [routesAdmin] = React.useState(dashboardRoutes);
  const [routesSuper] = React.useState(dashboardRoutesSuper);
  const [routesAduana] = React.useState(dashboardRoutesAduana);

  const switchRoutes = (
    <Switch>
      {rolCurrent === 'OPERADOR' && routes.map((prop, key) => {
        if (prop.layout === "/principal") {
          return (
            <Route exact
              path={prop.layout + prop.path}            
              component={prop.component}
              key={key}
            />
          );
        } 

        return null;
      })}
      {rolCurrent === 'ADMINISTRADOR' && routesAdmin.map((prop, key) => {
        if (prop.layout === "/principal") {
          return (
            <Route exact
              path={process.env.PUBLIC_URL  + prop.layout + prop.path}            
              component={prop.component}
              key={key}
            />
          );
        }

        return null;
      })}   
      {rolCurrent === 'SUPERVISOR' && routesSuper.map((prop, key) => {
        if (prop.layout === "/principal") {
          return (
            <Route exact
              path={prop.layout + prop.path}            
              component={prop.component}
              key={key}
            />
          );
        }
        
        return null;
      })}
      {rolCurrent === 'ADUANA' && routesAduana.map((prop, key) => {
        if (prop.layout === "/principal") {
          return (
            <Route exact
              path={prop.layout + prop.path}            
              component={prop.component}
              key={key}
            />
          );
        }               
        
        return null;
      })}                   
      <Route exact path='/principal/agente' component={agente} />
      <Route exact path='/principal/almacenista' component={almacenista} />
      <Route exact path='/principal/depositos' component={depositos} /> 
      <Route exact path={process.env.PUBLIC_URL +  '/principal/puertos'} component={puertos} />        
      <Route exact path='/principal/nave' component={nave} /> 
      <Route exact path='/principal/tipobulto' component={tipobulto} />
      <Route exact path='/principal/genericocontenedor' component={genericocontenedor} />
      <Route exact path='/principal/tipocontenedor' component={tipocontenedor} />            
      <Route exact path='/principal/genericotamano' component={genericotamano} /> 
      <Route exact path='/principal/tipotamano' component={tipotamano} /> 
      <Route exact path='/principal/contenedor' component={contenedor} /> 
      <Route exact path='/principal/contenedorvalores' component={contenedorvalores} />
      <Route exact path='/principal/aduana' component={aduana} />    
      <Route exact path='/principal/operador' component={operador} />
      <Route exact path='/principal/aduanacorrelativo' component={aduanacorrelativo} />
      <Route exact path='/principal/emptpte' component={emptpte} /> 
      <Route exact path='/principal/chofer' component={chofer} /> 
      <Route exact path='/principal/motivocierre' component={motivocierre} />
      <Route exact path='/principal/detalleanulacion' component={detalleanulacion} /> 
      <Route exact path='/principal/documento' component={documento} /> 
      <Route exact path='/principal/extension' component={extension} />
      <Route exact path='/principal/documentacion' component={documentacion} />           
      <Route exact path='/principal/usuarios' component={usuarios} /> 
      <Route exact path='/principal/rol' component={rol} />     
      {rolCurrent === 'ADUANA' &&(
        <Redirect from="/" to="/principal/FiscalizacionAduana" />  
      )}
      {rolCurrent === 'OPERADOR' &&(
        <Redirect from="/" to="/principal/tatc" />  
      )}
      <Redirect from="/" to="/principal/dashboard" />    
    </Switch>
  );
  
  // const handleImageClick = (image) => {
  //   setImage(image);
  // };
  // const handleColorClick = (color) => {
  //   setColor(color);
  // };
  // const handleFixedClick = () => {
  //   if (fixedClasses === "dropdown") {
  //     setFixedClasses("dropdown show");
  //   } else {
  //     setFixedClasses("dropdown");
  //   }
  // };
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const getRoute = () => {
    return window.location.pathname !== "/maps";
  };
  const resizeFunction = () => {
    if (window.innerWidth >= 960) {
      setMobileOpen(false);
    }
  };

  // initialize and destroy the PerfectScrollbar plugin
  React.useEffect(() => {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(mainPanel.current, {
        suppressScrollX: true,
        suppressScrollY: false,
      });
      document.body.style.overflow = "hidden";
    }
    window.addEventListener("resize", resizeFunction);
    // Specify how to clean up after this effect:
    return function cleanup() {
      if (navigator.platform.indexOf("Win") > -1) {
        ps.destroy();
      }
      window.removeEventListener("resize", resizeFunction);
    };
  }, [mainPanel]);

//  React.useEffect(() => {
//     if (props.login) {       
//        // eslint-disable-next-line no-lone-blocks
//        {getRoute("///") ? (
//           <div className={classes.content}>
//             <div className={classes.container}>{switchRoutes}</div>
//           </div>
//         ) : (
//           <div className={classes.map}>{switchRoutes}</div>
//         )}      
//      }    
//   }, [props.login,classes.content,classes.container,classes.map,switchRoutes]);  

  return (
    <div>
    <div>
    {rolCurrent === 'ADMINISTRADOR' &&
    <div className={classes.wrapper}>           
      <Sidebar
        routes= {routesAdmin}
        logoText={"Get It Done"}
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
        {...rest}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routesAdmin}
          handleDrawerToggle={handleDrawerToggle}
          {...rest}
          props ={props}
        />
        {/* On the /maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}
        {getRoute() ? (
          <div className={classes.content}>
            <div className={classes.container}>{switchRoutes}</div>
          </div>
        ) : (
          <div className={classes.map}>{switchRoutes}</div>
        )}
       
      </div>
    </div>
    }
    </div>
    <div>
    {rolCurrent === 'OPERADOR' &&
    <div className={classes.wrapper}>           
      <Sidebar
        routes= {routes}
        logoText={"Get It Done"}        
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
        {...rest}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routes}
          handleDrawerToggle={handleDrawerToggle}
          {...rest}          
          props ={props}
        />
        {/* On the /maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}
        {getRoute() ? (
          <div className={classes.content}>
            <div className={classes.container}>{switchRoutes}</div>
          </div>
        ) : (
          <div className={classes.map}>{switchRoutes}</div>
        )}
       
      </div>
    </div>
    }
    </div> 
    <div>
    {rolCurrent === 'ADUANA' &&
    <div className={classes.wrapper}>           
      <Sidebar
        routes= {routesAduana}
        logoText={"Get It Done"}        
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
        {...rest}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routesAduana}
          handleDrawerToggle={handleDrawerToggle}
          {...rest}          
          props ={props}
        />
        {/* On the /maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}
        {getRoute() ? (
          <div className={classes.content}>
            <div className={classes.container}>{switchRoutes}</div>
          </div>
        ) : (
          <div className={classes.map}>{switchRoutes}</div>
        )}
       
      </div>
    </div>
    }
    </div> 
    <div>
    {rolCurrent === 'SUPERVISOR' &&
    <div className={classes.wrapper}>           
      <Sidebar
        routes= {routesSuper}
        logoText={"Get It Done"}        
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
        {...rest}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routesSuper}
          handleDrawerToggle={handleDrawerToggle}
          {...rest}          
          props ={props}
        />
        {/* On the /maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}
        {getRoute() ? (
          <div className={classes.content}>
            <div className={classes.container}>{switchRoutes}</div>
          </div>
        ) : (
          <div className={classes.map}>{switchRoutes}</div>
        )}
       
      </div>
    </div>
    }
    </div>
    </div>
  );
}
