import * as api from '../../api';
import * as React from 'react';

import { host, client } from '../../conf';


import { Button} from "@material-ui/core"
import LibraryBooksRoundedIcon from '@material-ui/icons/LibraryBooksRounded';

import Notification from '../Notification'
import MaterialTable from 'material-table';
import DownloadIcon from '@mui/icons-material/Download';
import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";

import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

function doesFileExist(urlToFile) {
  var xhr = new XMLHttpRequest();
  xhr.open('HEAD', urlToFile, false);
  xhr.send();
   
  return xhr.status !== 404;
}

export default function Documentacion(props: any) {

  const [DocumentoList, updateDocumentoList] = React.useState([]);
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const [selectedRow, setSelectedRow] = React.useState("");
  const classesCards = useStyleCards();   
  const updateAllDocumentos = () => {
    api.getData(host + '/api/documentosdocumento/?cliente__id='+client+'&estado=1').then(x => {
      if(x.status === 200){
        let ctemp = x.data
        ctemp = ctemp.filter(x => doesFileExist(host +'/docs/'+x.url))
        updateDocumentoList(ctemp)


      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
}

// React.useEffect(() => {
//   setHostDoc("../../../../"+urldoc+"/")
// }, [DocumentoList])

React.useEffect(() => {
  updateAllDocumentos();

}, [])


  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>
        <CardHeader color="primary">
            <CardIcon color="primary">
                <LibraryBooksRoundedIcon fontSize="large" />
            </CardIcon>
            <h4 className={classesCards.cardTitleWhite}>Documentación</h4>
            <p className={classesCards.cardCategoryWhite}>Repositorio de Documentos de Soporte del Sistema</p>
          </CardHeader>      
          {/* <CardBody>            */}
          <MaterialTable
           data = {DocumentoList}
           {...props}            
           localization={{
             pagination: {
                 labelDisplayedRows: '{from}-{to} de {count}',
                 labelRowsSelect: 'filas'
             },
             header: {
                 actions: 'Acciones'
             },
             body: {
                 emptyDataSourceMessage: 'No existen filas para visualizar',
                 filterRow: {
                     filterTooltip: 'Filtrar'
                 }
             }
           }}              
           columns={[
            { field: 'id', title: 'Id' , hidden: true, export: false},
            //{ field: 'codigo', title: 'Código', defaultSort: 'asc'},

            { field: 'url', title: 'Documento',
            render: (rowData: any) => ( 
              <div>
              <a href={host +'/docs/'+rowData.url}  target="_blank" rel="noopener noreferrer" download>
             
              <Button color= 'primary' style={{borderRadius:15}} 
              startIcon={<DownloadIcon />}>
                  {/* <i className="fas fa-download"/> */}
              </Button>
              </a>    
              {/* <a href={process.env.PUBLIC_URL +rowData.url} target="_blank" rel="noopener noreferrer" > */}
              <a href={host+'/docs/'+rowData.url} target="_blank" rel="noopener noreferrer" >              
              {rowData.namefile}
            </a>               
              </div>               
            )
          },              
          ]} 
           onRowClick={((evt, selectedRow) => 
             {
               setSelectedRow(selectedRow.tableData.id)
             }
           )} 
           options={{
             filtering: true, 
             addRowPosition: "first",
             showTitle: false, 
             headerStyle: {
              backgroundColor: '#8e24aa',//'#01579b',
               color: '#FFF'
             },              
             rowStyle: rowData => ({
               backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
             })          
           }}   
          />
        {/* </CardBody>  */}
        </Card>  
     
      <Notification
          notify = {notify}
          setNotify = {setNotify}
        > 
      </Notification>
      </GridItem>           
    </GridContainer>
  </React.Fragment>
  )}
