import * as api from '../../api';
import * as React from 'react';


import { host, client } from '../../conf';

import {Button,  Grid, TextField } from "@material-ui/core"
import {DialogActions,  DialogContent} from "@material-ui/core"
import SaveRoundedIcon from '@mui/icons-material/SaveRounded';
import Person from "@mui/icons-material/Person";

import DialogScroll from '../DialogScroll';
import MaterialTable from 'material-table';
import XLSX from 'xlsx'
import pixelWidth from 'string-pixel-width'

import Notification from '../Notification'
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteOutlinedIcon from '@mui/icons-material/DeleteOutlined';
import AddBoxRoundedIcon from '@mui/icons-material/AddBoxRounded';
import CheckOutlinedIcon from '@mui/icons-material/CheckOutlined';
import ClearOutlinedIcon from '@mui/icons-material/ClearOutlined';

import GridContainer from "../../components/Grid/GridContainer.js";

import GridItem from "../../components/Grid/GridItem.js";
import Card from "../../components/CardMdr/Card.js";
import CardHeader from "../../components/CardMdr/CardHeader.js";

import CardIcon from "../../components/CardMdr/CardIcon.js";
import { makeStyles } from "@material-ui/core/styles";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};
const useStyleCards = makeStyles(styles);

export default function Usuarios(props: any) {
  const classesCards = useStyleCards(); 
  const [UsuariosList, updateUsuariosList] = React.useState([]);
  const [RolList, updateRolList] = React.useState([]);
  const [selectedRow, setSelectedRow] = React.useState("");
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});

  const [user, updateUserDetail] = React.useState({id: "" ,username: '', email: "", is_active: true, is_superuser: false, old_password: "", new_password: "", password1: "", password2: "", cliente: client, user: "", rol: ""})

  const [userToEdit, updateuserToEdit] = React.useState(0);
  const [showModal, updateshowModal] = React.useState(false);
  const closeModal = () => {updateshowModal(false); };
  const [showModalAdd, updateshowModalAdd] = React.useState(false);
  const closeModalAdd = () => {updateshowModalAdd(false); };
  //const [errorOldPass, setErrorOldPass] = React.useState(true);
  const [errorNewPass, setErrorNewPass] = React.useState(true);
  const [pass2, setPass2] = React.useState('');
  const [errorNewPass2, setErrorNewPass2] = React.useState(true);  
  const [loadData, setLoadData] = React.useState(false);

  const [maxWidth] = React.useState('xs'); 

  //const box_style = {background: "white"}

  const updateAllRol = () => {
    setLoadData(true) 
    api.getData(host + '/api/usuariosrol/?cliente__id='+client).then(x => {
      if(x.status === 200){
        const RolList={}
        x.data.map(row=>RolList[row.id]=row.nombre)
        updateRolList(RolList)          

      }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      }).finally(() => {setLoadData(false)})
  }

  const downloadExcel=(filename)=>{


    const newData= UsuariosList.map((row)=>{

      return {
        Usuario: row.username,
        Nombre: row.first_name,
        Apellidos: row.last_name,        
        Email: row.email,         
        Activo: row.is_active, 
        Administrador: row.is_superuser,                 
      }
    })


    const workSheet=XLSX.utils.json_to_sheet(newData)

    let header = ['Usuario', 'Nombre', 'Apellidos', 'Email', 'VERDADERO', 'Administrador']
    const jsonKeys = header || Object.keys(newData[0])

    const objectMaxLength = []
    jsonKeys.forEach((key) => {
      objectMaxLength.push(
        pixelWidth(key, {
          size: 3,
        })
      )
    })

    newData.forEach((data, i) => {
      const value = newData[i]
      jsonKeys.forEach((key, j) => {
        const l = value[jsonKeys[j]]
          ? pixelWidth(value[jsonKeys[j]], {
              size: 3,
            })
          : 0
        objectMaxLength[j] = objectMaxLength[j] >= l ? objectMaxLength[j] : l

      })
    })



    const wscols = objectMaxLength.map((w: number) => ({width: w}))
    workSheet["!cols"] = wscols

    const workBook=XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workBook,workSheet,filename)
    //Buffer
    //let buf=XLSX.write(workBook,{bookType:"xlsx",type:"buffer"})
    //Binary string
    XLSX.write(workBook,{bookType:"xlsx",type:"binary"})
    //Download
    XLSX.writeFile(workBook, filename+".xlsx")


  }  

const updateUser = () => {
    if (pass2 !== user.new_password)
    {
      setNotify({isOpen: true, message: 'Error: No coincide la nueva contraseña confirmada ', errors: [], type: 'error'});
    }
    else
    {
    api.putData(host +'/api/usuariosuserprofile/' +  userToEdit.toString() + "/",  user)
    .then(data => {
      let messages: any = [];
      if(data.status === 400){
        Object.keys(data.data).map((key) => 
        {
          let mlabel = data.data[key]
          if (key === "old_password")
          {
                mlabel = 'Usuario' 
          } 
          messages.push( (key === 'old_password' ? '': 'Nueva Contraseña: ') +  mlabel)
          return true
        });
            setNotify({isOpen: true, message: 'Error en la Edición del Usuario: ', errors: messages, type: 'error'});
        } else {
            setNotify({isOpen: true, message: 'Edición de Usuario realizada con éxito', errors: [], type: 'success'})
            updateUserDetail({...user, old_password:'', new_password: ''})
            setErrorNewPass(true)
            setPass2('')
            setErrorNewPass2(true) 
            closeModal()          
        }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });
    }

}


const addUser = () => {
  if (user.password1 !== user.password2)
  {
    setNotify({isOpen: true, message: 'Error: No coincide la contraseña confirmada ', errors: [], type: 'error'});
  }
  else
  {
    let messages: any = [];   
              
    api.postData(host + '/rest-auth/registration/', user)
   
    .then(data => {
        if(data.status !== 201){
            Object.keys(data.data).map((key) => 
            {
              let mlabel = data.data[key]
              if (key === "Datos Repetidos")
              {
                    mlabel = 'Nombre ' 
              } 
              messages.push( key + ": " + mlabel)
              return true
            });
            setNotify({isOpen: true, message: 'Error en la Adición del Usuario:', errors: messages, type: 'error'});
        } else {
  
            api.postData(host + '/api/usuariosuserprofile/', user)
            .then(data => {
                if(data.status !== 201){
                  Object.keys(data.data).map((key) => 
                  {
                    let mlabel = data.data[key]
                    messages.push( key + ": " + mlabel)
                    return true
                  });
                  setNotify({isOpen: true, message: 'Error en la Adición del Usuario:', errors: messages, type: 'error'});
                } 
                else {          
                      setNotify({isOpen: true, message: 'Adición de Usuario realizada con éxito', errors: [], type: 'success'});
                      updateUsuariosList([...UsuariosList, user]);
                      closeModalAdd()                                
                }
              }).catch( e=>
              { 
                setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
              });              
        }
    }).catch( e=>
      { 
        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
      });     
  }

}

  const editModalBody = (user: any,  updateUserDetail: any) => { 
  
     return (           
      <div >    


          <DialogContent>

                      {/* <Grid  container spacing={2}> */}
                            <GridItem xs={12} sm={12} md={12} >                   
                            <TextField 
                              value={user.old_password} 
                              id="standard-basic" 
                              label="Contraseña Actual" 
                              //error = {errorOldPass}
                              type = "password"
                              required 
                              style={{width: '100%'}} 
                              onChange={(event) => {
                                updateUserDetail({...user, old_password: event.target.value})
                                //setErrorOldPass(event.target.value === '')
                                }} />
                            </GridItem>
                            <br/><br/>
                            <GridItem xs={12} sm={12} md={12} >
                            <TextField 
                                value={user.new_password} 
                                type = "password"
                                label="Contraseña Nueva" 
                                required 
                                //error = {errorNewPass}
                                style={{width: '100%'}}                                
                                onChange={(event) => {
                                  updateUserDetail({...user, new_password  : event.target.value
                                })
                                  setErrorNewPass(event.target.value === '')
                                }} />
                            </GridItem>
                            <br/><br/>
                            <GridItem xs={12} sm={12} md={12} >
                            <TextField 
                                value={pass2} 
                                type = "password"
                                label="Confirme Contraseña Nueva" 
                                required 
                                error = {errorNewPass2}
                                
                                style={{width: '100%'}}                                  
                                onChange={(event) => {
                                  setPass2(event.target.value)
                                  setErrorNewPass2(event.target.value === '' )

                                }} />
                            </GridItem>
                            <br/><br/>
                          {/* </Grid> */}
                      {/* </Grid> */}

          </DialogContent>

          <DialogActions>
          <GridItem xs={12} sm={12} md={12} >
            <Button   color="primary" style={{backgroundColor: '#8e24aa', color: '#ffffff',marginTop: 10}} 
            disabled = {errorNewPass || errorNewPass2 }
            onClick={updateUser} round fullWidth>Cambiar Contraseña</Button>
            </GridItem>           
          </DialogActions>
  
      </div>
  )};

  const addModalBody = () => { 

 
    return (           
     <div >    


         <DialogContent>

                     <Grid  container spacing={2}>
                         <Grid id="cont_Pass" item xs={6} md={6}>                      
                           <TextField 
                               value={user.password1} 
                               type = "password"
                               label="Contraseña Nueva" 
                               required 
                               //error = {errorNewPass}
                               style={{minWidth:300,marginTop: 5,marginLeft: 15}}                                  
                               onChange={(event) => {
                                 updateUserDetail({...user, password1  : event.target.value
                               })
                                 setErrorNewPass(event.target.value === '')
                               }} />
                           <br/><br/>'
                           <TextField 
                               value={user.password2}  
                               type = "password"
                               label="Confirme Contraseña Nueva" 
                               required 
                               error = {errorNewPass2}
                               
                               style={{minWidth:300,marginTop: 5,marginLeft: 15}}                                  
                               onChange={(event) => {
                                updateUserDetail({...user, password2  : event.target.value
                                })
                                 setErrorNewPass2(event.target.value === '' )

                               }} />
                           <br/><br/>                            '
                         </Grid>
                     </Grid>

         </DialogContent>

         <DialogActions>
           <Button  startIcon={<SaveRoundedIcon />} color="secondary" style={{backgroundColor: '#8e24aa', color: '#ffffff'}} 
           disabled = {errorNewPass || errorNewPass2 }
           onClick={addUser}>Crear Usuario</Button>           
         </DialogActions>
 
     </div>
 )};

  const changePassword = (rowData) => {
      updateuserToEdit(rowData.id);
      updateshowModal(true);
      updateUserDetail({id: rowData.id, username: rowData.username, email: rowData.email, is_active: rowData.is_active, is_superuser: rowData.is_superuser, old_password: "", new_password: "",  password1: "", password2: "", cliente: client, rol: "", user: rowData.id});
   };

   const changePasswordAdd = (newData) => {
    updateshowModalAdd(true);
    updateUserDetail({id: '', username: newData.username, email: newData.email, is_active: true, is_superuser: newData.is_superuser, old_password: "", new_password: "", password1: "", password2: "", cliente: client, rol: newData.rol, user: ""})    

 };   

  const updateAllUsuarios = () => {
      api.getData(host + '/api/usuariosuserprofile/?cliente='+client).then(x => {
        if(x.status === 200){
          updateUsuariosList(x.data)
        }
      }).catch( e=>
        { 
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
        });
  }

  React.useEffect(() => {
    updateAllRol()    
    updateAllUsuarios()
  }, [])

  return (
    <React.Fragment>
      <GridContainer >
      <GridItem md={12}>
      <Card>         
        <CardHeader color="primary">
          <CardIcon color="primary">
              <Person fontSize="large" />
          </CardIcon>
          <h4 className={classesCards.cardTitleWhite}>Usuarios</h4>
          <p className={classesCards.cardCategoryWhite}>Gestión de datos del Mantenedor</p>
        </CardHeader> 
        {/* <CardBody>*/}      
          <MaterialTable
            data = {UsuariosList}
            {...props}
            isLoading ={loadData}            
            icons={{
              Edit: () => <EditOutlinedIcon style={{ color: "green" }} />,
              Delete: () => <DeleteOutlinedIcon style={{ color: "red" }} />,
              Clear: () => <ClearOutlinedIcon style={{ color: "red" }} />,
              Check: () => <CheckOutlinedIcon style={{ color: "green" }} />,              
              Add: () => <AddBoxRoundedIcon style={{ color: "green" }} />              
            }}        
            editable={{
              isDeleteHidden: rowData => true, 
              onRowAdd: newData =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    changePasswordAdd(newData)
                     resolve();
                  }, 1000)
                }),
              onRowUpdate: (newData, oldData) =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    const dataUpdate = [...UsuariosList];
                    const index = oldData.tableData.id;
                    newData.cliente = client
                    api.putData(host +'/api/usuariosuserprofile/' + oldData.id + "/",  newData)
                    .then(data => {
                      let messages: any = [];
                      if(data.status !== 200){
                        Object.keys(data.data).map((key) => 
                        {
                          let mlabel = data.data[key]
                          if (key === "Datos Repetidos")
                          {
                                mlabel = 'Nombre' 
                          } 
                          messages.push( key + ": " + mlabel)
                          return true
                        });
                            setNotify({isOpen: true, message: 'Error en la Edición del Usuario: ', errors: messages, type: 'error'});
                            reject();    
               
                        } else {
                            setNotify({isOpen: true, message: 'Usuario actualizado con éxito', errors: [], type: 'success'});           
                            dataUpdate[index] = newData;                    
                            updateUsuariosList([...dataUpdate]); 
                            resolve();                             
                        }
                    }).catch( e=>
                      { 
                        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                      });
                  }, 1000)
                }),
              onRowDelete: oldData =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    const index = oldData.tableData.id;
                    let messages: any = [];
                    api.deleteData(host + '/api/usuariosuser/' + oldData.id + '/').then(data => {
                      if(data.status !== 204){
                          Object.keys(data.data).map(key => messages.push(key + ": " + data.data[key]));
                          setNotify({isOpen: true, message: 'Error en la Eliminación del Usuario: ', errors: messages, type: 'error'}); 
                          reject();                           
                      } else {
                        setNotify({isOpen: true, message: 'Usuario eliminado con éxito', errors: [], type: 'success'}); 
                        const dataDelete = [...UsuariosList];
                        dataDelete.splice(index, 1);
                        updateUsuariosList([...dataDelete]);                         
                        resolve()                  
                      }        
                    }).catch( e=>
                      { 
                        setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos...', errors: [], type: 'error'});
                      });                 
                    

                  }, 1000)
                }),
            }}          
            localization={{
              pagination: {
                  labelDisplayedRows: '{from}-{to} de {count}',
                  labelRowsSelect: 'filas'
              },
              toolbar: {
                  nRowsSelected: '{0} filas(s) seleccionadas',
                  searchTooltip: 'Buscar',
                  searchPlaceholder: 'Buscar',
                  exportTitle: 'Exportar',
                  exportAriaLabel: 'Exportar',
                  exportCSVName: 'EXCEL',
                  exportPDFName: 'PDF'
              },
              header: {
                  actions: 'Acciones'
              },
              grouping: {
                groupedBy: 'Agrupar por',
                placeholder: 'Arraste la columna para agrupar'
              },
              body: {
                  emptyDataSourceMessage: 'No existen filas para visualizar',
                  deleteTooltip : 'Eliminar', 

                  editRow:{
                        saveTooltip: "Aceptar",
                        cancelTooltip: "Cancelar",                    
                        deleteText: "¿Está seguro que quiere borrar este Usuario?"
                  },
                  editTooltip : 'Editar', 
                  addTooltip: "Adicionar",
                  filterRow: {
                      filterTooltip: 'Filtrar'
                  }
              }
            }}              
            columns={[
                { field: 'id', title: 'Id' , hidden: true, export: false},
                { field: 'username', title: 'Usuario', defaultSort: 'asc', 
                editComponent: (editProps) =>  (
                  <TextField
                     autoFocus={true}
                      value={editProps.value}
                              onChange={(e) => editProps.onChange(e.target.value)}
                      helperText={editProps.helperText}
                      error={editProps.error}
                      variant="standard"
                  />              
                ),                     
                validate: (rowData) =>
                  rowData.username === undefined
                  ? { isValid: false, helperText: 'Usuario Requerido' }
                  : rowData.username === ''
                  ? { isValid: false, helperText: 'Usuario Requerido' }
                  : true
                },
                { field: 'email', title: 'Email', defaultSort: 'asc'},   
                { field: 'rol', title: 'Rol', lookup: RolList, export: false,
                validate: (rowData) =>
                rowData.rol === undefined
                  ? { isValid: false, helperText: 'Rol Requerido' }
                  : rowData.rol === ''
                  ? { isValid: false, helperText: 'Rol Requerido' }
                  : true            
                }, 
                { field: 'rol_nombre', title: 'Rol',export: true, hidden: true} ,                                             
                // { field: 'estado', title: 'Estado',export: false,lookup: { '1': 'ACTIVO', '0': 'NO ACTIVO' },   initialEditValue: '1'},   
                { field: 'is_active', title: 'Activo' , type: 'boolean',  initialEditValue: 'true'},               
                { field: 'is_superuser', title: 'Administrador' , type: 'boolean', initialEditValue: 'true'}, 
                { field: 'cliente', title: 'client' , hidden:true,  initialEditValue: client,  export: false}  
            ]} 
            onRowClick={((evt, selectedRow) => 
              {
                setSelectedRow(selectedRow.tableData.id)
              }
            )} 
            options={{
              filtering: true, 
              grouping: true, 
              actionsColumnIndex: -1,
              addRowPosition: "first",
              showTitle: false,    
              exportAllData: true,                  
              exportButton: true,
              exportFileName: 'Usuarios',
              exportCsv: (columns, data) => {
                downloadExcel("Usuarios")
              },               
              headerStyle: {
                backgroundColor: '#8e24aa',//'#01579b',
                color: '#FFF'
              },              
              rowStyle: rowData => ({
                backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
              })          
            }}   
            actions={[
              rowData => ({
                icon: 'password' ,
                iconProps: { style: {  color: "black" } },                    
                tooltip: 'Cambiar Contraseña',
                onClick: (event, rowData) => changePassword(rowData)
              })
            ]}                         
          />
        {/* </CardBody> */}
       </Card>  
        <Notification
            notify = {notify}
            setNotify = {setNotify}
          > 
        </Notification>
      </GridItem>           
    </GridContainer>
    
      <DialogScroll
                    showDialog={showModal}
                    closeFunction={closeModal}                    
                    titleBar="Cambiar Contraseña"
                    maxWidth={maxWidth}
                    showBotonPDF={false}  
                >
              {editModalBody(user, updateUserDetail)}
              
      </DialogScroll> 
      <DialogScroll
                    showDialog={showModalAdd}
                    closeFunction={closeModalAdd}                    
                    titleBar="Cambiar Contraseña"
                    maxWidth={maxWidth}
                    showBotonPDF={false}  
                >
              {addModalBody()}
              
      </DialogScroll>               
    </React.Fragment>
  )}
