import React from 'react';
import { Box, Grid, Paper,InputLabel,  MenuItem, Select,  FormControl, FormHelperText} from "@material-ui/core"
import TextField from '@mui/material/TextField';

import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Muted from "../../components/Typography/Muted.js";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
const useStylesTypo = makeStyles(styles);


export interface Props {
  NaveList: any,
  PuertoList: any,

  titulo: any,
  errorBL: boolean,
  errorViaje: boolean,
  errorNave: boolean,
  errorPuerto: boolean,

  setErrorBL: Function,
  setErrorViaje: Function,
  setErrorNave: Function,
  setErrorPuerto: Function,

  setTitulo: Function           
}

const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 }
  })

export default function TTCRecalada(props: Props) {

  const [nameClassBL, setNameClassBL] = React.useState(''); 
  const [nameClassNave, setNameClassNave] = React.useState('');
  const [nameClassViaje, setNameClassViaje] = React.useState('');  
  const [nameClassPuerto, setNameClassPuerto] = React.useState(''); 

  const classes = useStyles()
  const classesTypo = useStylesTypo() 

  React.useEffect(() => {
    setNameClassBL(props.errorBL == true ? classes.textFieldRed: classes.textField )
  }, [props.errorBL])

  React.useEffect(() => {
    setNameClassNave(props.errorNave == true ? classes.textFieldRed: classes.textField )
  }, [props.errorNave])  

  React.useEffect(() => {
    setNameClassViaje(props.errorViaje == true ? classes.textFieldRed: classes.textField )
  }, [props.errorViaje])  

  React.useEffect(() => {
    setNameClassPuerto(props.errorPuerto == true ? classes.textFieldRed: classes.textField )
  }, [props.errorPuerto])

    return (
      <React.Fragment>    
        <GridContainer  style={{marginTop: -20}}>
          <GridItem xs={12} sm={6} md={3}>
          <TextField id="ctl_BL" 
                style={{ marginTop: 10}}
                label="Número BL"
                className={nameClassBL}
                required
                //error = {props.errorBL}
                type="text"
                // multiline
                // maxRows = {3}
                //helperText = "Requerido"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.titulo.bl}
                onChange={(event) => {
                  props.setTitulo({...props.titulo, bl: event.target.value})
                  props.setErrorBL((!event.target.value || event.target.value.toString().length === 0));
                }}                
            /> 
          </GridItem>
          
          <GridItem xs={12} sm={6} md={3}>
          <FormControl id="ctl_Nave" style={{width:'100%',marginTop: 10}}               
              required 
              className={nameClassNave}
              //</GridItem>error={props.errorNave}
              >              
              <TextField
                  required 
                  value={props.titulo.nave}
                  select
                  label="Nave"
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, nave: event.target.value})
                    props.setErrorNave((event.target.value === "0"));
                  }}
                  defaultValue="0"
                  size = 'small'
                  fullWidth={true}                  
              >
                  <MenuItem value="0" style= {{ fontSize: "14px" }}>
                  {<div className={classesTypo.mutedText}>Seleccione Nave</div>} 
                  </MenuItem>
                  {props.NaveList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
            </FormControl> 
          </GridItem>

          <GridItem xs={12} sm={6} md={3}>
            <TextField id="ctl_Viaje" 
                style={{ marginTop: 10}}
                label="Viaje"
                type="text"
                className={nameClassViaje}
                required
                //error = {props.errorViaje}               
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.titulo.viaje}
                onChange={(event) => {
                  props.setTitulo({...props.titulo, viaje: event.target.value})
                  props.setErrorViaje((!event.target.value || event.target.value.toString().length === 0));
                }}                
            />              
          </GridItem>
          <GridItem xs={12} sm={6} md={3}>
            <FormControl id="ctl_Puerto" style={{width:'100%', marginTop: 10}}               
              required 
              className={nameClassPuerto}
              //error={props.errorPuerto}
              >
              {/* <InputLabel >Puerto</InputLabel> */}
              <TextField
                  required 
                  value={props.titulo.puerto}
                  select
                  label="Puerto"
                  size = 'small'  
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, puerto: event.target.value})
                    props.setErrorPuerto((event.target.value === "0"));
                  }}
                  defaultValue="0"
                  //displayEmpty={false}
                  fullWidth={true}                  
              >
                  <MenuItem value="0" style= {{ fontSize: "14px" }}>              
			              {<div className={classesTypo.mutedText}>Seleccione Puerto</div>} 
                  </MenuItem>
                  {props.PuertoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                
              </TextField>
              {/* <FormHelperText>Requerido</FormHelperText> */}
            </FormControl>        
          </GridItem>
          {/* </Grid>   */}
          {/*</Paper>       
        </Grid> */}
      </GridContainer>
     </React.Fragment> 
    )
}


