import React from 'react';
import { host, client } from '../../conf';
import {Chip, Grid, Paper, InputLabel, MenuItem, Select,  FormControl, FormHelperText } from "@material-ui/core"
import TextField from '@mui/material/TextField';
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";

export interface Props {

  OperadorList: any,
  DepositoList: any,  

  errorOpExterno: boolean,
  errorDepositoExterno: boolean, 
  errorNumeroTATCExterno: boolean, 
  rutOpExterno: string, 
  codOpExterno: string,  
    
  traspasonew: any,

  setErrorOpExterno: Function,
  setErrorDepositoExterno: Function,
  setErrorNumeroTATCExterno: Function
 
  updateTraspasoNew: Function,   
}
const useStylesType = makeStyles(styles)
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 },
	 customTextField: {
		"& input::placeholder": {
		  fontSize: "14px"
		},
		"& input": {
		  fontSize: "14px"
		}
	  }
  })

  function isDigitCode(n) {
    //return(n >= charCodeZero && n <= charCodeNine);
    return(n >= "0" && n <= "9")
 }  

export default function TraspasoDatosExternos (props: Props) {

  const [nameClassOpExterno, setNameClassOpExterno] = React.useState(''); 
  const [nameClassDepositoExterno, setNameClassDepositoExterno] = React.useState('');
  const [nameClassNumeroTATCExterno, setNameClassNumeroTATCExterno] = React.useState('');  

  const classes = useStyles()  
  const classesTypo = useStylesType()

  React.useEffect(() => {
    setNameClassOpExterno(props.errorOpExterno == true ? classes.textFieldRed: classes.textField )
  }, [props.errorOpExterno])

  React.useEffect(() => {
    setNameClassDepositoExterno(props.errorDepositoExterno == true ? classes.textFieldRed: classes.textField )
  }, [props.errorDepositoExterno])  

  React.useEffect(() => {
    setNameClassNumeroTATCExterno(props.errorNumeroTATCExterno == true ? classes.textFieldRed: classes.textField )
  }, [props.errorNumeroTATCExterno])



  return (
    <React.Fragment>    
     {/* <Grid id="cont_DatosExternos" 
               container spacing={2}
               style={{marginBottom: 15}}
               item xs={12} md={12}>

           

      <Paper elevation={3} style={{width: '100%',  height: 'auto'}} >
        <Grid id="cont_Aduana_Operador_Numero" item xs={12} md={12} style={{marginBottom: 10}}> */}
      <GridContainer  style={{marginTop: -20}}>
        <GridItem xs={12} sm={6} md={4} >
        <FormControl id="ctl_OpExterno" style={{width:'100%'}}
          required 
          className={nameClassOpExterno}
          //error={props.errorOpExterno} 
          >
            {/* <InputLabel id="demo-simple-select-required-label">Operador</InputLabel> */}
            <TextField
                label="Operador"
                value={props.traspasonew.operador_externo}
                defaultValue="0"
                displayEmpty={false}
                fullWidth={true} 
                select 
                onChange={(event) => {
                  props.updateTraspasoNew({...props.traspasonew, operador_externo: event.target.value})
                  props.setErrorOpExterno((event.target.value === "0"))
                }}               
                variant="outlined"                             
                style={{marginTop: 10}} 
                size = 'small'
                required 
                //error={props.errorAduana} 
                inputProps={{
                  style: { fontSize: 14 },                                     
                }}                                
                  
            >
                <MenuItem value="0" style= {{ fontSize: "14px" }}>                  
                    {<div className={classesTypo.mutedText}>Seleccione Operador</div>} 
                </MenuItem>
                {props.OperadorList.filter(x => x.cliente != client).map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}          
            </TextField>
        </FormControl>
        </GridItem>   

        <GridItem xs={12} sm={6} md={4} >          
          <TextField id="ctl_CodOpExterno" style={{ marginTop: 10}}
                label="Código Operador"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.codOpExterno}
          />       
        </GridItem>        
        
        <GridItem xs={12} sm={6} md={4} >          
          <TextField id="ctl_RutOpExterno" style={{ marginTop: 10}}
                label="RUT"
                type="text"
                variant="outlined"
                fullWidth
                size = 'small'
                inputProps={{
                    style: { fontSize: 14 },                                     
                }}
                value={props.rutOpExterno}
          />       
        </GridItem>

        <GridItem xs={12} sm={6} md={4} > 
          <TextField id="ctl_NumeroExterno" style={{ marginTop: 10}}
              required
              //autoFocus 
              label="TATC Externo"
              type="text"
              className={nameClassNumeroTATCExterno}
              //error={props.errorNumeroTATCExterno}
              value={props.traspasonew.tatc_externo}
              variant="outlined"
              fullWidth
              onKeyPress={(event) => {
                if (!isDigitCode(event?.key)) {
                  event.preventDefault();
                }
              }}                 
              size = 'small'
              onChange={(event) => {
                props.updateTraspasoNew({...props.traspasonew, tatc_externo: event.target.value})
                props.setErrorNumeroTATCExterno((event.target.value === ""))
              }}
              inputProps={{
                style: { fontSize: 14 },                                     
              }}
          />     
        </GridItem> 

        <GridItem xs={12} sm={6} md={4} > 
          <FormControl id="ctl_DepositoExterno"  style={{width:'100%'}}
             
            className={nameClassDepositoExterno}
            //error={props.errorDepositoExterno}  
            >
              <TextField
                value={props.traspasonew.deposito_externo}                
                defaultValue="0"
                label= {props.traspasonew.tipo === 'E'? "Depósito Externo": "Depósito"}
                //displayEmpty={false}
                required 
                fullWidth={true}               
                select  
                onChange={(event) => {
                  props.updateTraspasoNew({...props.traspasonew, deposito_externo: event.target.value})
                  props.setErrorDepositoExterno((event.target.value === "0"))
                }}
                variant="outlined"                             
                style={{marginTop: 10}} 
                size = 'small'                            
              >
                <MenuItem value="0" style= {{ fontSize: "14px" }}>                  
                    {<div className={classesTypo.mutedText}>Seleccione Depósito</div>} 
                </MenuItem>              
              {props.DepositoList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}          
              </TextField>
          </FormControl>
        </GridItem>
      </GridContainer> 
       
    </React.Fragment> 
  )
}


