import React from 'react';
import { Box, Grid, Paper,InputLabel, MenuItem, Select,  FormControl, FormHelperText} from "@material-ui/core"
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import {DatePicker, DateTimePicker} from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import esLocale from 'date-fns/locale/es';
//import Container from "@material-ui/core/Container";
import GridContainer from "../../components/Grid/GridContainer.js";
import GridItem from "../../components/Grid/GridItem.js";
import Muted from "../../components/Typography/Muted.js";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../assets/jss/material-dashboard-react/components/typographyStyle.js";

export interface Props {
  OperadorList: any,
  AduanaList: any,

  titulo: any,
  rutOpPropio: string,
  codOpPropio: string,
  nombreOpPropio: string,  
  numeroPropio: string,
  codAduana: string,

  errorNumeroTATC: boolean,
  errorAduana: boolean,
  errorFecha: boolean,
  errorFechaIngreso: boolean,
  setErrorNumeroTATC: Function,
  setErrorAduana: Function,
  setErrorFecha: Function,
  setErrorFechaIngreso: Function,

  setTitulo: Function           
}
const useStylesType = makeStyles(styles)
const useStyles = makeStyles({
	textField: {
  
		"& label span": {
		  color: "green"
		}
	  },
	textFieldRed: {
  
		"& label span": {
		  color: "red"
		}
	 }
  })

export default function TTC(props: Props) {
    const [nameClassAduana, setNameClassAduana] = React.useState(''); 
    const [nameClassNumeroTATC, setNameClassNumeroTATC] = React.useState('');
    const [nameClassFecha, setNameClassFecha] = React.useState('');  
    const [nameClassFechaIngreso, setNameClassFechaIngreso] = React.useState(''); 

    const classes = useStyles()  
    const classesTypo = useStylesType()
    React.useEffect(() => {
      setNameClassAduana(props.errorAduana === true ? classes.textFieldRed: classes.textField )
    }, [props.errorAduana])

    React.useEffect(() => {
      setNameClassNumeroTATC(props.errorNumeroTATC === true ? classes.textFieldRed: classes.textField )
    }, [props.errorNumeroTATC])  

    React.useEffect(() => {
      setNameClassFecha(props.errorFecha === true ? classes.textFieldRed: classes.textField )
    }, [props.errorFecha])       

    React.useEffect(() => {
      setNameClassFechaIngreso(props.errorFechaIngreso === true ? classes.textFieldRed: classes.textField )
    }, [props.errorFechaIngreso])    
    return (
      <React.Fragment>    
        <GridContainer style={{marginTop: -20}}>
            <GridItem xs={12} sm={6} md={6}>              
              <TextField id="ctl_OpPropio" 
                  style={{ marginTop: 10 }}
                  label= {<div className={classesTypo.mutedText}>Operador</div>}
                  type="text"                  
                  variant="outlined"
                  value= {props.nombreOpPropio}
                  fullWidth
                  size = 'small'                 
                  inputProps={{
                    style: { fontSize: 14 },                                     
                  }}
              />              
            </GridItem>

            <GridItem xs={12} sm={6} md={3} >
              <TextField id="ctl_RutOpPropio"                  
                  style={{ marginTop: 10}}
                  label="RUT"
                  type="text"
                  variant="outlined"
                  value={props.rutOpPropio}
                  fullWidth
                  size = 'small'
                  inputProps={{
                    style: { fontSize: 14 },                                     
                  }}
              /> 
            </GridItem>
            
            <GridItem xs={12} sm={6} md={3} >
            <FormControl id="ctl_Aduana" style={{width:'100%'}}
              required 
              //error={props.errorAduana}>
              className={nameClassAduana}>
              <TextField                  
                  labelId="demo-simple-select-label"
                  id="aduana-select"
                  select
                  value={props.titulo.aduana}
                  label="Aduana"
                  color="primary"                  
                  onChange={(event) => {
                    props.setTitulo({...props.titulo, aduana: event.target.value})
                    props.setErrorAduana((event.target.value === "0"));
                  }}
                  defaultValue="0"
                  displayEmpty={false}
                  //fullWidth={true}
                  variant="outlined"                             
                  style={{marginTop: 10}} 
                  size = 'small'
                  required 
                  //error={props.errorAduana} 
                  inputProps={{
                    style: { fontSize: 14 },                                     
                  }}                 
              >
                  <MenuItem value="0" style= {{ fontSize: "14px" }}>
                  {<div className={classesTypo.mutedText}>Seleccione Aduana</div>} 
                  </MenuItem>
                  {props.AduanaList.map((c:any, key) => {return <MenuItem key={key} value={c.id}><div style= {{ fontSize: "14px"}}>{c.nombre}</div></MenuItem>})}                
              </TextField>
              </FormControl>
            </GridItem>
                      
            <GridItem xs={12} sm={6} md={3}>
              <TextField id="ctl_CodAduana" 
                  style={{ marginTop: 10}}
                  label="Código Aduana"
                  type="text"
                  variant="outlined"
                  value={props.codAduana}
                  fullWidth
                  size = 'small'
                  inputProps={{
                    style: { fontSize: 14 },                                     
                  }}
              /> 
            </GridItem>

            <GridItem xs={12} sm={6} md={3}>
              <TextField id="ctl_Numero" 
                  style={{ marginTop: 10}}
                  label="Número"
                  //error = {props.errorNumeroTATC}                  
                  className={nameClassNumeroTATC}
                  type="text"
                  variant="outlined"
                  value={props.numeroPropio}
                  fullWidth
                  size = 'small'
                  inputProps={{
                    style: { fontSize: 14 },                                     
                  }}
              /> 
            </GridItem>

            <LocalizationProvider dateAdapter={AdapterDateFns} locale={esLocale}>
              <GridItem xs={12} sm={6} md={3}>
                <DatePicker 
                    disableFuture
                    label="Fecha Emisión"
                    minDate={props.titulo.fecha_ingreso_pais}
                    value={props.titulo.fecha_str}
                    onChange={(newValue) => {
                      props.setTitulo({...props.titulo, fecha: newValue, fecha_str: newValue})
                      props.setErrorFecha(!newValue || newValue.toString().length === 0 ||newValue.toString().substring(0,3) === 'Inv')                 
                    }}
                    renderInput={(params) => 
                    <TextField {...params} 
                    required 
                    //error= {props.errorFecha}
                    className={nameClassFecha}
                    size = 'small' 
                    style={{marginTop: 10,width: '100%'}} />} 
                    InputProps={{
                      style: {
                          fontSize: 14,
                          //height: 38
                      }
                    }}                                  
                />
               </GridItem>
              
              <GridItem xs={12} sm={6} md={3}> 
                <DateTimePicker  
                  disableFuture
                  label="Ingreso al País"
                  //maxDate={props.titulo.fecha}
                  value={props.titulo.fecha_ingreso_pais}
                  onChange={(newValue) => {
                    props.setTitulo({...props.titulo, fecha_ingreso_pais: newValue, fecha_ingreso_pais_str_dif: newValue})
                    props.setErrorFechaIngreso(!newValue || newValue.toString().length === 0 ||newValue.toString().substring(0,3) === 'Inv')                 
                  }}
                  renderInput={(params) => <TextField {...params} required 
                  //error= {props.errorFechaIngreso} 
                  className={nameClassFechaIngreso}
                  size = 'small'
                  style={{marginTop: 10,width: '100%'}} />}
                  InputProps={{
                    style: {
                        fontSize: 14,
                        //height: 38
                    }
                  }}                
                />
              </GridItem>
            </LocalizationProvider>            
     </GridContainer> 
     </React.Fragment> 
    )
}


